module.exports = {
	getVisibility: function getVisibility(finWindow) {
		return new Promise(function (resolve, reject) {
			finWindow.isShowing(isShowing => resolve(isShowing), err => reject(err));
		});
	},
	spawnComponent: function spawnComponent(componentType) {
		return new Promise(function (resolve, reject) {
			function spawnComponent() {
				return new Promise(function (res, rej) {
					FSBL.Clients.RouterClient.query('TestRunner.AppLauncherMenu', {
						test: 'spawnComponent',
						name: componentType
					}, function (err, message) {
						res();
					});
				});
			}
			spawnComponent()
				.then(function () {
					setTimeout(resolve, 1500);
				});
		});
	},
	getWindow: function getWindow(name) {
		name = name.replace(/\s/g, '').toLowerCase();
		return new Promise(function (resolve, reject) {
			FSBL.Clients.LauncherClient.getActiveDescriptors(function (err, descriptors) {
				for (var key in descriptors) {
					var descriptor = descriptors[key];
					let windowName = descriptor.name.replace(/\s/g, '').toLowerCase();
					if (windowName.includes(name)) {
						return resolve(fin.desktop.Window.wrap(descriptor.uuid, descriptor.name));
					}
				}
				reject(new Error('Window not found'));
			});
		});
	},
	getWindows: function getWindows(name) {
		name = name.replace(/\s/g, '').toLowerCase();
		let windows = [];
		return new Promise(function (resolve, reject) {
			FSBL.Clients.LauncherClient.getActiveDescriptors(function (err, descriptors) {
				for (var key in descriptors) {
					var descriptor = descriptors[key];
					let windowName = descriptor.name.replace(/\s/g, '').toLowerCase();
					if (windowName.includes(name)) {
						windows.push(fin.desktop.Window.wrap(descriptor.uuid, descriptor.name));
					}
				}
				if (windows.length) {
					resolve(windows);
				} else {
					reject(new Error('Windows not found'));
				}
			});
		});
	},
	shouldBeHidden: function shouldBeHidden(visibility) {
		return new Promise(function (resolve, reject) {
			expect(visibility).to.be.false;
			resolve();
		});
	},
	shouldbeVisible: function shouldBeVisible(visibility) {
		return new Promise(function (resolve, reject) {
			expect(visibility).to.be.true;
			resolve();
		});
	},
	closeAllWindows: function closeAllWindows() {
		return new Promise(function (resolve, reject) {
			var doNotClose = [
				'App Launcher',
				'File Menu',
				'Workspace Management Menu',
				'Toolbar'];
			var myName = fin.desktop.Window.getCurrent().name;
			FSBL.Clients.LauncherClient.getActiveDescriptors((err, descriptors) => {
				for (let key in descriptors) {
					let descriptor = descriptors[key];
					if (doNotClose.includes(descriptor.componentType) || descriptor.name === myName) {
						continue;
					}
					fin.desktop.Window.wrap(descriptor.uuid, descriptor.name).close(true);
				}
				resolve();
			});
		});

	}
}


