var chai = require('chai');
var assert = chai.assert;
var should = chai.should;
var expect = chai.expect;
var WindowClient = FSBL.Clients.WindowClient;
var finWindow = fin.desktop.Window.getCurrent();

describe('WindowClient', function () {
	var testTitle = 'New Window Title';
	context('Window Title', function () {
		it('should set the window\'s title', function () {
			WindowClient.setWindowTitle(testTitle);
			//expect(document.title).to.equal(testTitle);
		});

		it('should change the window\'s title', function () {
			testTitle += ' Modified';
			WindowClient.setWindowTitle(testTitle);
			//expect(document.title).to.equal(testTitle);
		});

		it('should retrieve the window\'s title', function () {
			var theTitle = WindowClient.getWindowTitle();
			expect(theTitle).to.equal(testTitle);
		});
	});

	context('Window Controls', function () {
		it('should minimize the window', function (done) {
			WindowClient.minimize(cb);
			function cb() {
				finWindow.getState((windowState) => {
					expect(windowState).to.equal('minimized');
					done();
				});
			}
		});
		it('should restore the window from a minimized state', function (done) {
			WindowClient.restore(cb);
			function cb() {
				finWindow.getState((windowState) => {
					expect(windowState).to.equal('normal');
					done();
				});
			}
		});
		it('should maximize the window', function (done) {
			WindowClient.maximize(maximizeCallback);
			function maximizeCallback() {

				console.log("IN STATE CB", WindowClient.windowState);
				expect(WindowClient.windowState).to.equal('maximized');
				done();

			}
		});

		it('should restore the window from a maximized state', function (done) {
			WindowClient.restore(cb);
			function cb() {
				finWindow.getState((windowState) => {
					expect(windowState).to.equal('normal');
					done();
				});
			}
		});
	});

	context('Component State', function () {
		var field = 'myTestField';
		var field2 = 'myTestField2';
		var value2 = 'wooo2!';
		var value = 'wooo!';
		it('should set componentState', function (done) {
			WindowClient.setComponentState({
				field: field,
				value: value
			}, function (err, response) {
				expect(err).to.be.null;
				expect(response).to.exist;
				done();
			});
		});
		it('should set componentState multiple fields', function (done) {
			WindowClient.setComponentState({
				fields: [{
					field: field2,
					value: value2
				}]
			}, function (err, response) {
				expect(err).to.be.null;
				expect(response).to.exist;
				done();
			});
		});


		it('should get componentState from storage', function (done) {
			WindowClient.getComponentState({
				field: field
			}, function (err, response) {
				expect(err).to.be.null;
				expect(response).to.exist;
				expect(WindowClient.componentState[field]).to.equal(response);
				done();
			});
		});
		it('should get componentState from storage using array', function (done) {
			WindowClient.getComponentState({
				fields: [field2]
			}, function (err, response) {
				expect(err).to.be.null;
				expect(response).to.exist;
				expect(WindowClient.componentState[field2]).to.equal(response[field2]);
				done();
			});
		});
		it('should get full componentState from storage', function (done) {
			WindowClient.getComponentState(null, function (err, response) {
				var state = JSON.stringify(WindowClient.componentState);
				expect(err).to.be.null;
				expect(response).to.exist;
				expect(state).to.equal(JSON.stringify(response));
				done();
			});
		});
	});

	context('Window Bounds', function () {
		it('should check `setinitialWindowBounds`', function () {
			var opts = WindowClient.options;
			expect(opts).to.exist;
			expect(opts.customData).to.exist;
			expect(opts.customData).to.contain.all.keys(['component', 'foreign', 'monitorDimensions']);
			//opts.url doesn\'t have the query string. so i can\'t do an equality assesment for the testRunner;
			var windowLocationIncludesOptionsURL = window.location.toString().includes(opts.url);
			expect(windowLocationIncludesOptionsURL).to.be.true;
		});

		it('Should test saveWindowBounds', function (done) {
			//reminder to write this test once we figure out storage and testing.
			fin.desktop.Window.getCurrent().getBounds((bounds) => {
				WindowClient.saveWindowBounds(bounds);
				expect(WindowClient.options.defaultLeft).to.equal(bounds.left);
				expect(WindowClient.options.defaultTop).to.equal(bounds.top);
				expect(WindowClient.options.defaultWidth).to.equal(bounds.width);
				expect(WindowClient.options.defaultHeight).to.equal(bounds.height);
				done();
			});
		});
	});
});



