var React = require("react");
var TestRunnerActions = require("../stores/TestRunnerStore").Actions;
var TestRunnerStore = require("../stores/TestRunnerStore").Store;

import Code from './Code';
import TestError from './TestError';

class TestItem extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            showCode: false,
            test: props.test
        }
        TestRunnerStore.addListener(`change:${props.test.title}`, () => {
            this.setState({
                test: TestRunnerStore.getTest(props.test.title)
            });
        });
    }
    toggleCode() {
        this.setState({
            showCode: !this.state.showCode
        })
    }
    runOnlyThisTest() {
        let URL = TestRunnerActions.makeUrl(this.state.test.title);
        window.location.assign(URL);
    }
    getTestDurationClasses(test) {
        let classes = 'TestDuration ';
        let isSlow = test.duration > test.slow;
        let isVerySlow = test.duration > 5000;
        let isSuperFast = test.duration < test.slow;
        if (typeof(test.duration) !== 'undefined') {
            if (isVerySlow) {
                classes += 'VerySlowTest';
            } else if (isSlow) {
                classes += 'SlowTest';
            } else if (isSuperFast) {
                classes += 'SuperFast'
            }
        }
        return classes;
    }
    render() {
        var test = this.state.test;
        let cleanQS = decodeURIComponent(window.location.search).replace('?grep=', '');
        let title = test.title;
        // console.log(constructParentTree(test));
        console.log('ITEM FILTER', this.props.filter);
        let shouldRender = constructParentTree(test).includes(cleanQS);


        let codeClassNames = "js TestCode";
        function constructParentTree(test) {
            let str = [test.title];
            if (test.parent && !test.parent.root) {
                str.unshift(constructParentTree(test.parent));
            }
            return str.join(' > ');
        }

        if (shouldRender) {
            if (Object.keys(this.props.filter).length) {
                if (this.props.filter.property === 'title') {
                    let regex = new RegExp(this.props.filter.value, 'g');
                    let segments = title.split(regex);
                    let matches = title.match(segments);
                    let match = <span className="highLight">{this.props.filter.value}</span>;
                    title = [];
                    segments.forEach((segment, i) => {
                        title.push(segment);
                        if (i === segments.length - 1) return;
                        title.push(match);
                    })
                } else if (this.props.filter.property === 'state') {
                    if (this.props.filter.value === 'pending') {
                        shouldRender = test.pending;
                    } else {
                        shouldRender = test.state === this.props.filter.value;
                    }
                }
                if (!shouldRender) {
                    return null;
                }
            }
            let testIconMap = {
                passed: 'test-passed ff-check-circle',
                failed: 'test-failed ff-delete-circle',
                pending: 'test-pending ff-dots-horiz'
            }
            let className = testIconMap[test.state];
            let testTitleClasses = 'TestTitle';
            if (test.pending) {
                className = testIconMap['pending'];
                testTitleClasses += ' TestPending'
            }
            let testDurationClasses = this.getTestDurationClasses(test);
            let breadcrumbs = constructParentTree(test).replace(' > ' + test.title, '');
            let numParents = breadcrumbs.split(' > ').length;
            console.log('DURATION CLASSES', testDurationClasses);
            let breadcrumbsStyle = {
                marginLeft: numParents + 'em'
            }
            return <div className="TestWrapper" style={breadcrumbsStyle}>
                <div className="TestRow breadcrumbs" >
                    {breadcrumbs}
                </div>
                <div className="TestRow">
                    <div className="TestLeft" onClick={this.runOnlyThisTest.bind(this)}>
                        <div className={testTitleClasses}> <i className={className}></i>{test.pending ? '(pending) ' : null}{title}</div>
                        {
                            typeof(test.duration) !== 'undefined' &&
                            <div className={testDurationClasses} >{test.duration}ms</div>
                        }
                    </div>
                    <div className="TestRight">
                        {!test.pending &&
                            <div className="TestCodeToggle" onClick={this.toggleCode.bind(this)}>Code</div>
                        }
                    </div>
                </div>
                <div className="TestRow">
                    {test.fn &&
                        <Code classNames={codeClassNames}
                            show={this.state.showCode}
                            code={test.fn.toString()} />}
                    {test.state === 'failed' &&
                        <TestError classNames={codeClassNames} test={test} />}
                </div>
            </div>;
        }
        return null;
    }
};

export default TestItem;


