it('should resize the group from the top left', function () {
	resetGrid();

	var windowList = group1.getWindows();
	var cachedBounds = getCachedBoundsForGrid();

	moveRequest = createMoveRequest({
		newLeft: A.left - 20,
		newTop: A.top + 20,
		newBottom: A.bottom,
		newRight: A.right,
		changeType: 1,
		win: A
	});
	A.requestBoundsChange(moveRequest);

	cachedBounds.forEach((cacheObj) => {
		var win = DockingMath.getWindow(cacheObj.name);
		//check anchors
		if (['I'].includes(cacheObj.name)) {
			expect(cacheObj.bounds.right).to.equal(win.right);
		}
		if (['G', 'H', 'I'].includes(cacheObj.name)) {
			expect(cacheObj.bounds.bottom).to.equal(win.bottom);
		}
	});


	// //We started with equally an sized grid. All resulting windows should be teh same width and height.
	// ['B', 'C', 'D', 'E', 'F', 'G', 'H', 'I'].forEach((windowName, i) => {
	// 	expect(DockingMath.getWindow(windowName).height).to.equal(A.height);
	// 	expect(DockingMath.getWindow(windowName).width).to.equal(A.width);
	// });



	//Checks that no gaps exist between windows that were snapped.

	//Row 1 bottom == row2 top
	[B.bottom, C.bottom, D.top, E.top, F.top].forEach((number, i) => {
		expect(number).to.equal(A.bottom);
	});

	//row 2 bottom == row 3 top
	[E.bottom, F.bottom, G.top, H.top, I.top].forEach((number, i) => {
		expect(number).to.equal(D.bottom);
	});
	//Column 1 right == Column 2 left
	[B.left, E.left, H.left, D.right, G.right].forEach((number) => {
		expect(number).to.equal(A.right);
	});

	//Column 1 right == Column 2 left
	[C.left, F.left, I.left, E.right, H.right].forEach((number) => {
		expect(number).to.equal(B.right);
	});

	//Check group bounds.
	[G.bottom, H.bottom, I.bottom].forEach((number) => {
		expect(number).to.equal(group1.bottom);
	});

	[C.right, F.right, I.right].forEach((number) => {
		expect(number).to.equal(group1.right);
	});

	[A.left, D.left, G.left].forEach((number) => {
		expect(number).to.equal(group1.left);
	});

	[A.top, B.top, C.top].forEach((number) => {
		expect(number).to.equal(group1.top);
	});
});
