it('should resize the group from the top right', function () {
	resetGrid();

	var windowList = group1.getWindows();

	var cachedBounds = getCachedBoundsForGrid();

	moveRequest = createMoveRequest({
		newLeft: C.left,
		newTop: C.top + 20,
		newBottom: C.bottom,
		newRight: C.right - 20,
		changeType: 1,
		win: C
	});

	C.requestBoundsChange(moveRequest);

	cachedBounds.forEach((cacheObj) => {
		var win = DockingMath.getWindow(cacheObj.name);
		//check anchors
		if (['G'].includes(cacheObj.name)) {
			expect(cacheObj.bounds.left).to.equal(win.left);
		}
		if (['G', 'H', 'I'].includes(cacheObj.name)) {
			expect(cacheObj.bounds.bottom).to.equal(win.bottom);
		}
	});



	//Checks that no gaps exist between windows that were snapped.

	//Row 1 bottom == row2 top
	//B.bottom, e.TOP, f.top, C.bottom,
	[D.top].forEach((number, i) => {
		expect(number).to.equal(A.bottom);
	});

	[B.bottom, F.top, C.bottom].forEach((number, i) => {
		expect(number).to.equal(E.top);
	});


	//row 2 bottom == row 3 top
	var bottoms = [E.bottom, F.bottom, G.top, H.top, I.top];
	bottoms.forEach((number) => {
		expect(number).to.equal(D.bottom);
	});
	//Column 1 right == Column 2 left
	[B.left, E.left, H.left, D.right, G.right].forEach((number, i) => {
		expect(number).to.equal(A.right);
	});

	//Column 1 right == Column 2 left
	[I.left].forEach((number) => {
		expect(number).to.equal(H.right);
	});

	//Check group bounds.
	[G.bottom, H.bottom, I.bottom].forEach((number) => {
		expect(number).to.equal(group1.bottom);
	});
	[C.right, F.right, I.right].forEach((number) => {
		expect(number).to.equal(group1.right);
	});

	[A.left, D.left, G.left].forEach((number) => {
		expect(number).to.equal(group1.left);
	});

	[A.top, B.top, C.top].forEach((number) => {
		expect(number).to.equal(group1.top);
	});
});
