var chai = require('chai');
var assert = chai.assert;
var should = chai.should;
var expect = chai.expect;

var DataTransferClient = FSBL.Clients.DataTransferClient;

describe('DataTransferClient', function () {

	var methods = getAllMethods(DataTransferClient);
	var baseMethods = getAllMethods(new FSBL.Clients.BaseClient());

	var testMethods = methods.filter(function (item) {
		return !baseMethods.includes(item)
	})

	var untestedMethods = {};
	for (var i = 0; i < testMethods.length; i++) {
		untestedMethods[testMethods[i]] = true;
	}

	function testThat(method, test, fn) {
		it(test, fn)
		if (untestedMethods[method]) delete untestedMethods[method];
	}

	context('Emitters and Receivers', function () {
		var testObject = {
			test: "someData"
		}
		function getTestObject() {
			return testObject
		}

		var shareResults = {
			gotTestObjectCalled: false,
			gotAWordCalled: false,
			testObjectData: null,
			wordData: null
		}

		function gotTestObject(err, response) {
			shareResults.testObjectData = response.data['testObject']
			shareResults.gotTestObjectCalled = true;
			
		}

		function gotAWord(err, response) {
			shareResults.wordData = response.data['word.symbol']
			shareResults.gotAWordCalled = true;
		}

		// fake js event simulator
		var event = {
			dataTransfer: {
				data: {},
				getData: function (key) {
					return this.data[key];
				},
				setData: function (key, value) {
					this.data[key] = value;
				}
			},
			preventDefault: function () {

			},
			stopPropagation: function () {

			}
		}

		testThat('setEmitters', 'should set emitters', function (done) {
			DataTransferClient.setEmitters({
				emitters: [{
					type: "word.symbol",
					data: "IBM"
				}, {
					type: "testObject",
					data: getTestObject
				}]
			})
			expect(Object.keys(DataTransferClient.emitters).length).to.equal(2);
			done();
		})

		testThat('addReceivers', 'should add receivers', function (done) {
			DataTransferClient.addReceivers({
				receivers:
				[{
					type: /word.*/,
					handler: gotAWord
				}, {
					type: "testObject",
					handler: gotTestObject
				}]
			})
			expect(Object.keys(DataTransferClient.receivers).length).to.equal(2);
			done();
		})

		testThat('canReceiveData', 'should return proper values', function (done) {
			expect(DataTransferClient.canReceiveData('word.something')).to.equal(true);
			expect(DataTransferClient.canReceiveData('woard.something')).to.equal(false);
			expect(DataTransferClient.canReceiveData('testObject')).to.equal(true);
			expect(DataTransferClient.canReceiveData('testObjects')).to.equal(false);
			done();
		})

		testThat('dragStart', 'should set the data properly', function (done) {
			DataTransferClient.dragStart(event);
			var eventData = event.dataTransfer.getData("text/plain");
			expect(eventData).to.include("word.symbol");
			expect(eventData).to.include("testObject");
			done();
		})

		testThat('getComponentsThatCanReceiveType', 'should return proper list of components', function (done) {
			DataTransferClient.getComponentsThatCanReceiveType({
				type: "chartiq.chart"
			}, function (err, response) {
				expect(response).to.include('Advanced Chart');
				done();
			})
		})

		testThat('drop', 'should get the data properly', function (done) {
			DataTransferClient.drop(event);
			function checkIfDone() {
				if (shareResults.gotAWordCalled && shareResults.gotTestObjectCalled) {
					expect(shareResults.testObjectData).to.deep.equal(testObject);
					expect(shareResults.wordData).to.equal('IBM');
					done();
				} else {
					setTimeout(checkIfDone, 25);
				}

			}
			checkIfDone();
		})

		testThat('dragStartWithData', 'should contain the data', function (done) {
			let data = {
				'word.symbol': "AAPL"
			}
			// TODO: How to make sure that this is a real test?
			DataTransferClient.dragStartWithData(event, data);
			DataTransferClient.drop(event);
			expect(shareResults.wordData).to.equal('AAPL');
			done();
		})



	})

	context.skip('Completion', function () {
		//This needs to be considered in the context of an overarching code-coverage tool. This causes failures that aren't _really_ failures. We need to look at this eventually, but this isn't the solution. -- Brad
		it('should check all functions', function (done) {
			expect(untestedMethods).to.be.empty;
		});
	});

});



