var chai = require('chai');
var assert = chai.assert;
var should = chai.should;
var expect = chai.expect;
var RouterClient = FSBL.Clients.RouterClient;
var Helpers = require('./helpers');
const BASE_DELAY = 2000;
context('Menu Opening/Closing', function () {

	it('should click the workspace button, opening the menu.', function (done) {
		RouterClient.query('TestRunner.Toolbar', {
			test: 'openWorkspaceMenu'
		}, function (err, message) {
			let data = message.data;
			if (err) {
				console.error(err.message);
				return done(err.message);
			}
			console.log('need to get window');
			Helpers.getWindow('Workspace Management Menu')
				.then(Helpers.getVisibility)
				.then(Helpers.shouldBeVisible)
				//somehow done was being invoked with 'true', though i pass nothing from `shouldBeVisible`. This is a bandaid until I have time to actually look at it.
				.then(done.bind(null, null));
		});
	});

	it('should hide the workspace Menu', function (done) {
		this.timeout(5000);
		RouterClient.query('TestRunner.Toolbar', {
			test: 'closeWorkspaceMenu'
		}, function (err, message) {
			if (err) {
				console.error(err.message);
				return done(err.message);
			}
			Helpers.getWindow('Workspace Management Menu')
				.then(Helpers.getVisibility)
				.then(Helpers.shouldBeHidden)
				.then(done.bind(null, null));
		});
	});

	it('should click the file menu button, opening the menu.', function (done) {
		this.timeout(5000);
		RouterClient.query('TestRunner.Toolbar', {
			test: 'openFileMenu'
		}, function (err, message) {
			let data = message.data;
			if (err) {
				console.error(err.message);
				return done(err.message);
			}
			console.log('need to get window');
			Helpers.getWindow('File Menu')
				.then(Helpers.getVisibility)
				.then(Helpers.shouldBeVisible)
				.then(done.bind(null, null));
		});
	});

	it('should hide the file Menu', function (done) {
		this.timeout(5000);

		RouterClient.query('TestRunner.Toolbar', {
			test: 'closeFileMenu'
		}, function (err, message) {
			if (err) {
				console.error(err.message);
				return done(err.message);
			}
			Helpers.getWindow('File Menu')
				.then(Helpers.getVisibility)
				.then(Helpers.shouldBeHidden)
				.then(done.bind(null, null));
		});
	});

	it('should click the apps menu button, opening the menu.', function (done) {
		RouterClient.query('TestRunner.Toolbar', {
			test: 'openAppsMenu'
		}, function (err, message) {
			let data = message.data;
			if (err) {
				console.error(err.message);
				return done(err.message);
			}
			console.log('need to get window');
			Helpers.getWindow('App Launcher')
				.then(Helpers.getVisibility)
				.then(Helpers.shouldBeVisible)
				.then(done.bind(null, null));
		});
	});

	it('should hide the apps Menu', function (done) {
		this.timeout(5000);
		RouterClient.query('TestRunner.Toolbar', {
			test: 'closeAppsMenu'
		}, function (err, message) {
			if (err) {
				console.error(err.message);
				return done(err.message);
			}
			Helpers.getWindow('App Launcher')
				.then(Helpers.getVisibility)
				.then(Helpers.shouldBeHidden)
				.then(done.bind(null, null));
		});
	});

})
let WindowsForAutoArrange = [],
	initialAutoArrangeWindowDimensions = {};
//@todo, make this spawn multiple; limitation is that getWindow will not return multiple of the same component. For now this will be sufficient.
it('should spawn an advanced chart', function (done) {
	this.timeout(20000);
	FSBL.Clients.RouterClient.query('TestRunner.Toolbar', {
		test: 'openAppsMenu'
	}, function (err, message) {
		//hide it.
		fin.desktop.Window.getCurrent().focus();
		//makes sure that the windows are spawned on the proper monitor.
		Helpers.closeAllWindows()
			.then(Helpers.spawnComponent.bind(null, 'Advanced Chart'))
			.then(Helpers.getWindows.bind(null, 'Advanced Chart'))
			.then((windows) => {
				WindowsForAutoArrange = windows;
				setTimeout(done, BASE_DELAY);
			});
	});
});

it('should get the chart\'s initial dimensions', function (done) {
	for (let i = 0; i < WindowsForAutoArrange.length; i++) {
		let finWin = WindowsForAutoArrange[i];
		finWin.getBounds((bounds) => {
			initialAutoArrangeWindowDimensions[finWin.name] = Object.assign({}, bounds);
			if (i === WindowsForAutoArrange.length - 1) {
				done();
			}
		});
	}
});
it('should click the auto-arrange button', function (done) {
	this.timeout(5000);
	RouterClient.query('TestRunner.Toolbar', {
		test: 'triggerAutoArrange',
	}, function (err, message) {
		setTimeout(done, 2000);
	});
});

it('should verify that the window has changed size', function (done) {
	this.timeout(5000);
	for (let i = 0; i < WindowsForAutoArrange.length; i++) {
		let finWin = WindowsForAutoArrange[i];
		finWin.getBounds((bounds) => {
			let originalBounds = initialAutoArrangeWindowDimensions[finWin.name];
			console.log('comparing bounds', finWin.name, originalBounds.height, bounds.height)
			expect(originalBounds.height).to.not.equal(bounds.height);
			expect(originalBounds.width).to.not.equal(bounds.width);
			if (i === WindowsForAutoArrange.length - 1) {
				done();
			}
		});
	}
});

it('should click the auto-arrange button a second time', function (done) {
	RouterClient.query('TestRunner.Toolbar', {
		test: 'triggerAutoArrange',
	}, function (err, message) {
		done();
	});
});

it('should verify that the window goes back to its original size.', function (done) {
	WindowsForAutoArrange.forEach((finWin, i) => {
		finWin.getBounds((bounds) => {
			let originalBounds = initialAutoArrangeWindowDimensions[finWin.name];
			expect(originalBounds.height).to.equal(bounds.height);
			expect(originalBounds.width).to.equal(bounds.width);
			if (i === WindowsForAutoArrange.length - 1) {
				done();
			}
		});
	});
});


