
var React = require("react");
var TestRunnerActions = require("../stores/TestRunnerStore").Actions;
import Suite from './Suite';
class SuiteList extends React.Component {
    constructor() {
        super();
        this.state = {
            qs: false
        };
    }
    render() {
        var self = this;
        function renderSuiteList(suites) {
            var suiteList = suites.map((item, i) => {
                return <Suite qs={self.props.qs} filter={self.props.filter} suite={item} key={i} />;
            });
            return suiteList;
        }
        var suiteList = this.props.suites ? renderSuiteList(this.props.suites) : null;
        return <ul>
            {suiteList}
        </ul>
    }
};

export default SuiteList;


