var chai = require('chai');
var assert = chai.assert;
var should = chai.should;
var expect = chai.expect;

var WorkspaceClient = FSBL.Clients.WorkspaceClient;

describe('WorkspaceClient', function () {
	context('Basic Operations', function () {
		var originalWorkspaceName, newWorkspace, startingWorkspaceCount, workspaceNamem, currentWorkspaceName, clonedName, spawnedWindowInfo, spawnedWindowWrap;

		it('should get starting workspace', function () {
			originalWorkspaceName = WorkspaceClient.activeWorkspace.name;
			expect(originalWorkspaceName).to.not.be.null;
		});

		it('should save state of current workspace', function (done) {
			WorkspaceClient.save(function (err, res) {
				expect(err).to.be.null;
				var i = 1;
				done();
			});
		});

		it('should query list of workspaces', function (done) {
			WorkspaceClient.getWorkspaces(function (err, workspaceList) {
				expect(err).to.be.null;
				expect(workspaceList).to.deep.equal(WorkspaceClient.workspaces);
				startingWorkspaceCount = workspaceList.length;
				for (var i = 0; i < workspaceList.length; i++) {
					var workspace = workspaceList[i];
					expect(workspace).to.be.a('object');
					//expect(workspace).to.have.all.keys(['components', 'initializedWindows', 'isDirty', 'name', 'windows']);
				}
				done();
			});
		});

		it('should create a workspace', function (done) {
			var oldName = WorkspaceClient.activeWorkspace.name;
			console.log("WORKSPACE TEST, OLD NAME", oldName);
			WorkspaceClient.createNewWorkspace(function (err, res) {
				expect(err).to.be.null;
				expect(res).to.be.a('object');
				expect(res).to.deep.equal(WorkspaceClient.activeWorkspace);
				expect(oldName).to.not.equal(WorkspaceClient.activeWorkspace.name);
				window.console.log("WORKSPACE TEST, NEW NAME NAME", WorkspaceClient.activeWorkspace.name);

				newWorkspace = res;
				done();
			});
		});

		it('should be active workspace', function (done) {
			WorkspaceClient.getActiveWorkspace(function (err, res) {
				expect(err).to.be.null;
				expect(res).to.be.a('object');
				expect(res).to.deep.equal(newWorkspace);
				done();
			});
		});

		it('should have one more workspace in query', function (done) {
			WorkspaceClient.getWorkspaces(function (err, workspaceList) {
				expect(err).to.be.null;
				expect(startingWorkspaceCount + 1).to.equal(workspaceList.length);
				done();
			});
		});

		it('should be a clean workspace', function (done) {
			WorkspaceClient.isWorkspaceDirty(function (err, res) {
				expect(err).to.be.null;
				expect(res).to.be.false;
				expect(res).to.equal(WorkspaceClient.activeWorkspace.isDirty);
				done();
			});
		});

		it('should not delete active workspace by object', function (done) {
			var params =  newWorkspace;
			WorkspaceClient.remove(params, function (err, res) {
				expect(err).to.not.be.null;
				done();
			});
		});

		it('should not delete active workspace by name', function (done) {
			var params = {
				name: newWorkspace.name
			};
			WorkspaceClient.remove(params, function (err, res) {
				expect(err).to.not.be.null;
				done();
			});
		});

		it('should still have one more workspace in query', function (done) {
			WorkspaceClient.getWorkspaces(function (err, workspaceList) {
				expect(err).to.be.null;
				expect(startingWorkspaceCount + 1).to.equal(workspaceList.length);
				done();
			});
		});

		it('should spawn an advanced chart', function (done) {
			this.timeout(5000); // increase Mocha timeout; spawn waits for component to come online before invoking its callback.
			FSBL.Clients.LauncherClient.spawn('Advanced Chart', {
				addToWorkspace: true
			}, function (err, response) {
				expect(err).to.be.null;
				expect(response).to.exist;
				spawnedWindowInfo = response.windowIdentifier;
				spawnedWindowWrap = response.finWindow;
				setTimeout(done, 500); // have to wait for the spawn to result in dirty bit being set
			});
		});

		it('should be check to see if the activeWorkspace is dirty', function (done) {
			WorkspaceClient.isWorkspaceDirty(function (err, res) {
				expect(err).to.be.null;
				expect(res).to.equal(WorkspaceClient.activeWorkspace.isDirty);
				expect(res).to.be.true;
				done();
			});
		});

		it('should close the advanced window.', function () {
			fin.desktop.Window.wrap(spawnedWindowInfo.uuid, spawnedWindowInfo.name).close(true);
		});

		it('should create another workspace', function (done) {
			WorkspaceClient.createNewWorkspace(function (err, res) {
				expect(err).to.be.null;
				expect(res).to.be.a('object');
				workspaceName = res.name;
				done();
			});
		});

		it('should delete first created workspace by object', function (done) {
			var params = {
				workspace: newWorkspace
			};
			WorkspaceClient.remove(params, function (err, res) {
				expect(err).to.be.null;
				expect(res).to.equal("success");
				done();
			});
		});

		it('should switch back to original workspace', function (done) {
			var params = {
				name: originalWorkspaceName
			};
			WorkspaceClient.switchTo(params, function (err, res) {
				expect(err).to.be.null;
				expect(res).to.be.a('object');
				expect(res).to.equal(WorkspaceClient.activeWorkspace);
				done();
			});
		});

		it('should delete recently created workspace by name', function (done) {
			var params = {
				name: workspaceName
			};
			WorkspaceClient.remove(params, function (err, res) {
				expect(err).to.be.null;
				expect(res).to.equal("success");
				done();
			});
		});

		it('should have original number of workspaces', function (done) {
			WorkspaceClient.getWorkspaces(function (err, workspaceList) {
				expect(err).to.be.null;
				expect(startingWorkspaceCount).to.equal(workspaceList.length);
				done();
			});

		});

		it('should clone workspace', function (done) {
			currentWorkspaceName = WorkspaceClient.activeWorkspace.name;
			var params = {
				name: currentWorkspaceName
			};
			WorkspaceClient.clone(params, function (err, name) {
				clonedName = name;
				expect(clonedName).to.be.not.null;
				expect(clonedName).to.not.equal(currentWorkspaceName);
				done();
			});
		});

		it('should delete cloned workspace by name', function (done) {
			var params = {
				name: clonedName
			};
			WorkspaceClient.remove(params, function (err, res) {
				expect(err).to.be.null;
				expect(res).to.equal("success");
				done();
			});
		});

		it('should be same active workspace', function (done) {
			expect(currentWorkspaceName).to.equal(WorkspaceClient.activeWorkspace.name);
			done();
		});

		it('should end with original number of workspaces', function (done) {
			WorkspaceClient.getWorkspaces(function (err, workspaceList) {
				expect(err).to.be.null;
				expect(startingWorkspaceCount).to.equal(workspaceList.length);
				done();
			});

		});
		it('should save workspace');
		it('should save workspace under specied name');
		it('should rename workspace');
		it('should remove window in workspace');
		/*
		it('should return error deleting non-existance workspace by name', function (done) {
			var params = {
				name: "dummyname"
			};
			WorkspaceClient.remove(params, function (err, res) {
				expect(err).to.not.be.null;
				done();
			});
		});
		*/

	});

});



