

var React = require("react");
var TestRunnerActions = require("../stores/TestRunnerStore").Actions;
import Code from './Code';
class TestError extends React.Component {
	constructor() {
		super();
	}
	createStackString() {
		let test = this.props.test;
		let stackString = '';
		if (test.err.stack) {
			var indexOfMessage = test.err.stack.indexOf(test.err.message);
			if (indexOfMessage === -1) {
				stackString = test.err.stack;
			} else {
				stackString = test.err.stack.substr(test.err.message.length + indexOfMessage);
			}
		} else if (test.err.sourceURL && test.err.line !== undefined) {
			// Safari doesn't give you a stack. Let's at least provide a source line.
			stackString = '\n(' + test.err.sourceURL + ':' + test.err.line + ')';
		}

		return stackString || '';
	}
	render() {
		let classNames = this.props.classNames + ' code-error';
		let stackString = this.createStackString();
		let test = this.props.test;
		let code = test.err.message;
		let htmlMessage = test.err.htmlMessage;
		if (stackString) {
			code += stackString;
		}
		if (htmlMessage) {
			code += htmlMessage;
		}
		return <div className="Code TestErrorWrapper">
			<Code classNames={classNames} show={true} code={code} />
		</div>
	}
};

export default TestError;


