var React = require("react");
var TestRunnerStore = require("../stores/TestRunnerStore").Store;
var TestRunnerActions = require("../stores/TestRunnerStore").Actions;



class TestStats extends React.Component {
    constructor() {
		super();
		let stats = TestRunnerStore.getStats();
		this.state = this.updateStats(stats);
		//Statitics, that when clicked, will filter the view.
		this.possibleFilters = ['passes', 'failures', 'pending'];
		//Statistic => test state map.
		this.filterMap = {
			passes: 'passed',
			failures: 'failed',
			pending: 'pending'
		}
	}
	updateStats(stats) {
		console.log('stats', stats);
		if (!stats || !Object.keys(stats).length) {
			return { stats: {} };
		}
		let remaining = stats.total - stats.passes - stats.failures - stats.pending;
		if (remaining <= 0) {
			remaining = 'Finished';
		}
		return {
			stats: {
				total: stats.total,
				remaining: remaining,
				passes: stats.passes,
				failures: stats.failures,
				pending: stats.pending,
				duration: stats.duration
			}
		}
	}
	componentDidMount() {
		TestRunnerStore.addListener('change:stats', () => {
			let stats = TestRunnerStore.getStats();
			this.setState(this.updateStats(stats));
		})
	}
	filterState(stat) {
		TestRunnerActions.filterTests({
			property: 'state',
			value: this.filterMap[stat]
		});
	}
	render() {
		let stats = [];
		for (var stat in this.state.stats) {
			let classNames = this.possibleFilters.includes(stat) ? 'TestStat StatisticalFilter' : 'TestStat';
			stats.push(<div key={stat} className={classNames} onClick={this.filterState.bind(this, stat)}>
				<div className="TestStatLabel">{stat}</div>
				<div className="TestStatValue">{this.state.stats[stat]}</div>
			</div>);
		}
		return <div className="TestStatsWrapper">
			{stats}
		</div >
	}
};
export default TestStats;


