var defaults = {};

defaults.stationaryWindowBounds = {
	left: 100,
	top: 100,
	width: 500,
	height: 500
};

defaults.movingWindowBounds = {
	left: 700,
	top: 700,
	width: 400,
	height: 400
};

module.exports = defaults;


