context('Docking Helpers', function () {
	it('should say that the group is a box', function () {
		resetGrid();
		expect(group1.isARectangle()).to.be.true;
	});
	it('should add a snappedWindow to A', function () {
		A.snappedWindows = null;
		A.addSnappedWindow({
			name: 'B',
			edges: {
				right: true
			}
		});
		expect(A.snappedWindows.length).to.equal(1);
		expect(A.snappedWindows[0].name).to.equal('B');
	});
	it('should unsnap B from A', function () {
		A.removeSnappedWindow('B');
		expect(A.snappedWindows.length).to.equal(0);
		resetGrid();
	});
	it('should say that the group is not a box', function () {
		resetGrid();
		groupMode.enabled = false;
		DockingMath.setGroupMode(groupMode);
		DockingMath.setMovingWindow(A);
		A.setOpacity(1);
		var moveRequest = createMoveRequest({
			newLeft: A.left,
			newTop: A.top + 60,
			newBottom: A.bottom,
			newRight: A.right,
			changeType: 1,
			win: A
		});
		A.requestBoundsChange(moveRequest);

		groupMode.enabled = true;
		DockingMath.setGroupMode(groupMode);
		expect(group1.isARectangle()).to.be.false;
	});
	it('should say that the move does not affect the group', function () {
		resetGrid();
		//No windows have been moved, and I'm not actually making any movements. I need to make my code better - these workarounds shouldn't be necessary.
		DockingMath.setMovingWindow(A);
		A.setOpacity(1);
		var moveRequest = createMoveRequest({
			newLeft: A.left,
			newTop: A.top,
			newBottom: A.bottom + 1,
			newRight: A.right + 1,
			changeType: 1,
			win: A
		});
		expect(DockingMath.moveAffectsGroup(moveRequest)).to.be.false;
		moveRequest = createMoveRequest({
			newLeft: A.left,
			newTop: A.top,
			newBottom: A.bottom,
			newRight: A.right + 1,
			changeType: 1,
			win: A
		});

		expect(DockingMath.moveAffectsGroup(moveRequest)).to.be.false;
	});
	it('should say that the move does affect the group', function () {
		DockingMath.setMovingWindow(A);
		createMovableGroupFromGrid();

		A.setOpacity(1);
		A.resizeHandle = null;

		var moveRequest = createMoveRequest({
			newLeft: A.left + 5,
			newTop: A.top,
			newBottom: A.bottom,
			newRight: A.right,
			changeType: 1,
			win: A
		});

		expect(DockingMath.moveAffectsGroup(moveRequest)).to.be.true;

		A.resizeHandle = null;

		moveRequest = createMoveRequest({
			newLeft: A.left,
			newTop: A.top,
			newBottom: A.bottom,
			newRight: A.right + 1,
			changeType: 1,
			win: A
		});
		expect(DockingMath.moveAffectsGroup(moveRequest)).to.be.false;
	});

	it('should produce a positive bounds Delta', function () {
		function resetBounds() {
			b1 = {
				top: 100,
				left: 100,
				right: 100,
				bottom: 100,
				height: 100,
				width: 100
			};
			b2 = {
				top: 100,
				left: 200,
				right: 200,
				bottom: 100,
				height: 100,
				width: 100
			};
		}
		var b1 = {
			top: 100,
			left: 100,
			right: 100,
			bottom: 100,
			height: 100,
			width: 100
		};
		var b2 = {
			top: 100,
			left: 200,
			right: 200,
			bottom: 100,
			height: 100,
			width: 100
		};
		var boundsDelta = DockingMath.getBoundsDelta(b2, b1);

		expect(boundsDelta.height).to.equal(0);
		expect(boundsDelta.width).to.equal(0);
		expect(boundsDelta.top).to.equal(0);
		expect(boundsDelta.left).to.equal(100);

		b1.top = -30;
		b2.top = 30;
		boundsDelta = DockingMath.getBoundsDelta(b2, b1);
		expect(boundsDelta.height).to.equal(0);
		expect(boundsDelta.width).to.equal(0);
		expect(boundsDelta.top).to.equal(60);
		expect(boundsDelta.left).to.equal(100);

		b1.top = -30;
		b2.top = -60;
		boundsDelta = DockingMath.getBoundsDelta(b2, b1);

		expect(boundsDelta.height).to.equal(0);
		expect(boundsDelta.width).to.equal(0);
		expect(boundsDelta.top).to.equal(-30);
		expect(boundsDelta.left).to.equal(100);

		resetBounds();
		b2.height = 50;
		boundsDelta = DockingMath.getBoundsDelta(b2, b1);
		expect(boundsDelta.height).to.equal(-50);

		resetBounds();
		b2.left = 0;
		b2.width = b2.width + 100;
		boundsDelta = DockingMath.getBoundsDelta(b2, b1);
		expect(boundsDelta.width).to.equal(100);

	});
	it('should test windowsOnEdge', function () {
		resetGrid();
		var woe = DockingMath.getWindowsOnEdge(A, 'right');
		expect(woe[0].name).to.equal('B');

		var moveRequest = createMoveRequest({
			newLeft: F.left - 36,
			newTop: F.top,
			newBottom: F.bottom,
			newRight: F.right,
			changeType: 1,
			win: F
		});
		F.requestBoundsChange(moveRequest);

		var woe1 = DockingMath.getWindowsOnEdge(B, 'bottom');
		var expectation = [
			{
				name: "E",
				edge: "top"
			},
			{
				name: "F",
				edge: "top"
			}
		];
		expect(woe1).to.deep.equal(expectation);
	});

});


