it('should test shortCircuit resizing interior windows', function () {
	resetGrid();
	groupMode.enabled = true;
	DockingMath.setGroupMode(groupMode);
	var handle = 'right';
	newPosition = {
		right: B.left + 10
	};
	var cachedWidth = B.width;
	var type = 'resize';
	moveWindow({
		handle: handle,
		win: B,
		newPosition: newPosition,
		type: type
	});
	expect(B.width).to.equal(cachedWidth);
	//Resize C to the point where B is too small. expect it to stop.
	var shortCircuitedRight = B.right;
	handle = 'left';

	newPosition = {
		left: B.left + MINIMUM_WIDTH
	};
	var cachedLeft = C.left;
	moveWindow({
		handle: handle,
		win: C,
		newPosition: newPosition,
		type: type
	});

	expect(C.left).to.equal(cachedLeft);
	expect(B.width).to.equal(cachedWidth);
});
it('should test shortCircuit resizing a group', function () {
	var alphabet = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ';
	//creates a 3x3 grid
	var row = 0;
	var column = 0;
	group1 = DockingMath.getMovingGroup(A);
	for (var i = 0; i < 9; i++) {
		var windowName = alphabet[i];

		win = global[windowName];

		if (i > 1 && i % 3 === 0) {
			row++;
			column = 0;
		}
		win.setBounds(
			{
				left: 70 * column,
				top: 70 * row,
				width: 70,
				height: 70
			}
		);
		group1.updateWindow(global[windowName]);
		column++;
	}
	group1.updateBounds();

	groupMode.enabled = true;
	DockingMath.setGroupMode(groupMode);
	var handle = 'right';
	newPosition = {
		left: (MINIMUM_WIDTH * 3) - MINIMUM_WIDTH - 6,
		right: (MINIMUM_WIDTH * 3) - 1
	};

	var type = 'resize';
	moveWindow({
		handle: handle,
		win: C,
		newPosition: newPosition,
		type: type
	});
	expect(B.width).to.equal(MINIMUM_WIDTH);

});


