

var React = require("react");
var ReactDOM = require("react-dom");
var TestRunnerActions = require("../stores/TestRunnerStore").Actions;
import TestList from './TestList';
class Suite extends React.Component {
    constructor() {
        super();
        this.rendered = 0;
    }
    runOnlyThisSuite() {
        //`this` is bound to the `suite` object being rendered below.
        let URL = TestRunnerActions.makeUrl(this.title);
        window.location.assign(URL);
    }
    renderSuite(suite, filter) {
        var self = this;
        var grandSuiteList;
        this.rendered++;
        if (suite.title === 'Managing Groups') {
            // debugger;
        }
        if (TestRunnerActions.testFilter(suite, false, filter)) {
            //don't filter if the suite passes.
            //filter = {};
        }
        if (suite.suites) {
            grandSuiteList = suite.suites.map((grandSuite, i) => {
                // console.log('GS:', grandSuite.title);
                return self.renderSuite(grandSuite, filter);
            });
        }



        let shouldRender = false;
        let tests = suite.tests;
        if (tests.length === 0 && suite.suites[0]) {
            tests = suite.suites[0].tests;
        }
        for (var i = 0; i < tests.length; i++) {
            var test = tests[i];
            if (typeof test.state !== 'undefined') {
                shouldRender = true;
                break;
            }
        }


        let testList = <TestList qs={self.props.qs} filter={filter} tests={suite.tests} />;
        let style = {};
        if ((suite.suites.length > 1 && suite.tests.length === 0) || suite.tests.length > 1) {
            style = { borderLeft: '1px solid #039bff' };
        }
        return <div key={this.rendered}>
            <div onClick={this.runOnlyThisSuite.bind(suite)} style={{ display: shouldRender ? 'inline-block' : 'none' }} className="suiteTitle">{suite.title}</div>
            <ul style={style}>
                {testList}
                {grandSuiteList}
            </ul>
        </div>
    }
    render() {
        let suite = this.props.suite;
        return this.renderSuite(suite, this.props.filter);
    }
};

export default Suite;


