it('should return valid bounding boxes', function () {
	var bounds = {
		top: 100,
		left: 100,
		right: 200,
		bottom: 200,
		width: 100,
		height: 100
	};

	bounds = DockingMath.setBoundingBoxes(bounds);
	var keys = ['snappingRegions', 'windowBoundingBox', 'buffer', 'innerBuffer'];
	keys.forEach((key) => {
		if (key === 'snappingRegions') {
			for (var region in bounds[key]) {
				if (bounds[key][region].min || bounds[key.max]) {
					expect(bounds[key][region].min.x).to.not.be.NaN;
					expect(bounds[key][region].min.y).to.not.be.NaN;
					expect(bounds[key][region].max.x).to.not.be.NaN;
					expect(bounds[key][region].max.y).to.not.be.NaN;
				}
			}
		}

		if (bounds[key].min || bounds[key.max]) {
			expect(bounds[key].min.x).to.not.be.NaN;
			expect(bounds[key].min.y).to.not.be.NaN;
			expect(bounds[key].max.x).to.not.be.NaN;
			expect(bounds[key].max.y).to.not.be.NaN;
		}

	});
});
it('should check to see if two windows are snapped S=>L, M=>R', function () {
	var newLeft = stationaryWindow.left - movingWindow.width;
	var newTop = stationaryWindow.top;
	var moveRequest = createMoveRequest({
		newLeft: newLeft,
		newTop: newTop,
		newRight: newLeft + movingWindow.width,
		newBottom: newTop + movingWindow.height,
		changeType: 0,
		win: movingWindow
	});
	movingWindow.requestBoundsChange(moveRequest);

	var sharedEdges = movingWindow.getSharedEdges(stationaryWindow);
	expect(sharedEdges.right).to.be.true;
});

it('should check to see if two windows are snapped S=>R, M =>L', function () {
	movingWindow.moveTo(stationaryWindow.right, stationaryWindow.top);

	var sharedEdges = movingWindow.getSharedEdges(stationaryWindow);
	expect(sharedEdges.left).to.be.true;
});

it('should check to see if two windows are snapped S=>B, M =>T', function () {
	movingWindow.moveTo(stationaryWindow.right - 30, stationaryWindow.bottom);
	var sharedEdges = movingWindow.getSharedEdges(stationaryWindow);
	expect(sharedEdges.top).to.be.true;
});

it('should check to see if two windows are snapped S=>T, M =>B', function () {
	movingWindow.moveTo(stationaryWindow.left + 30, stationaryWindow.top -
		movingWindow.height);
	var sharedEdges = movingWindow.getSharedEdges(stationaryWindow);
	expect(sharedEdges.bottom).to.be.true;
});
it('should check my proportion math', function () {
	var num = 10;
	var pct = .20;
	var expected = 12;
	var actual = BoxMath.scaleProportionately(num, pct);
	expect(actual).to.equal(expected);

	pct = -0.20;
	expected = 8;
	actual = BoxMath.scaleProportionately(num, pct);
	expect(actual).to.equal(expected);

	num = -10;
	pct = .20;
	expected = -12;
	actual = BoxMath.scaleProportionately(num, pct);
	expect(actual).to.equal(expected);

	pct = -.20;
	expected = -8;
	actual = BoxMath.scaleProportionately(num, pct);
	expect(actual).to.equal(expected);

	pct = 0;
	actual = BoxMath.scaleProportionately(num, pct);
	expect(actual).to.equal(num);

	var num1 = 10;
	var num2 = 15;
	expected = 0.5;
	actual = BoxMath.getPercentChange(num1, num2);
	expect(actual).to.equal(expected);
});
context('movingRegion', function () {
	it('should say that the right edge is moving', function () {
		var request = {
			changeType: 1,
			left: movingWindow.left,
			top: movingWindow.top,
			width: movingWindow.width - 10,
			height: movingWindow.height
		};
		request.right = request.left + request.width;
		request.bottom = request.top + request.height;
		movingWindow.resizeHandle = null;
		var movingEdge = movingWindow.getResizeHandle(request);
		expect(movingEdge).to.equal('right');
	});
	it('should say that the topRight corner is moving', function () {
		movingWindow.resizeHandle = null;

		var moveRequest = createMoveRequest({
			newLeft: movingWindow.left,
			newTop: movingWindow.top + 2,
			newBottom: movingWindow.bottom,
			newRight: movingWindow.right + 5,
			changeType: 1,
			win: movingWindow
		});
		var movingEdge = movingWindow.getResizeHandle(moveRequest);

		expect(movingEdge).to.equal('topRight');
	});
	it('should say that the bottomRight corner is moving', function () {
		movingWindow.resizeHandle = null;

		var moveRequest = createMoveRequest({
			newLeft: movingWindow.left,
			newTop: movingWindow.top,
			newBottom: movingWindow.bottom - 10,
			newRight: movingWindow.right + 5,
			changeType: 1,
			win: movingWindow
		});

		var movingEdge = movingWindow.getResizeHandle(moveRequest);

		expect(movingEdge).to.equal('bottomRight');
	});
	it('should say that the bottom edge is moving', function () {
		movingWindow.resizeHandle = null;

		var request = {
			changeType: 1,
			left: movingWindow.left,
			top: movingWindow.top,
			width: movingWindow.width,
			height: movingWindow.height - 10
		};
		request.right = request.left + request.width;
		request.bottom = request.top + request.height;

		var movingEdge = movingWindow.getResizeHandle(request);
		expect(movingEdge).to.equal('bottom');
	});

	it('should say that the bottomLeft corner is moving', function () {
		movingWindow.resizeHandle = null;

		var moveRequest = createMoveRequest({
			newLeft: movingWindow.left + 4,
			newTop: movingWindow.top,
			newBottom: movingWindow.bottom + 1,
			newRight: movingWindow.right,
			changeType: 1,
			win: movingWindow
		});

		var movingEdge = movingWindow.getResizeHandle(moveRequest);

		expect(movingEdge).to.equal('bottomLeft');
	});

	it('should say that the left edge is moving', function () {
		movingWindow.resizeHandle = null;
		var request = {
			changeType: 1,
			left: movingWindow.left + 10,
			top: movingWindow.top,
			width: movingWindow.width - 10,
			height: movingWindow.height
		};
		request.right = request.left + request.width;
		request.bottom = request.top + request.height;
		;
		var movingEdge = movingWindow.getResizeHandle(request);

		expect(movingEdge).to.equal('left');
	});
	it('should say that the topLeft corner is moving', function () {
		movingWindow.resizeHandle = null;

		var moveRequest = createMoveRequest({
			newLeft: movingWindow.left + 6,
			newTop: movingWindow.top + 2,
			newBottom: movingWindow.bottom,
			newRight: movingWindow.right,
			changeType: 1,
			win: movingWindow
		});

		var movingEdge = movingWindow.getResizeHandle(moveRequest);

		expect(movingEdge).to.equal('topLeft');
	});
	after(function () {
		movingWindow.changeType = 0;
	});
});




