context('Moving window sitting on top of two windows placed edge to edge', function () {
	it('Resizing via right edge, should snap to the top right edge of each window below it.', function () {
		var newLeft = sw2.left + 40;
		var newTop = sw2.top - movingWindow.height;
		var moveRequest = createMoveRequest({
			newLeft: newLeft,
			newTop: newTop,
			newBottom: newTop + movingWindow.height,
			newRight: newLeft + movingWindow.width,
			changeType: 0,
			win: movingWindow
		});
		movingWindow.requestBoundsChange(moveRequest);
		//start with the left edge offset from the left stationary window.			
		//move inside of the left-most stationary window's right buffer.	
		moveRequest = createMoveRequest({
			newLeft: movingWindow.left,
			newRight: sw2.right - 12,
			newTop: movingWindow.top,
			newBottom: movingWindow.bottom,
			win: movingWindow,
			changeType: 1
		});

		movingWindow.requestBoundsChange(moveRequest);

		var oldTop = movingWindow.top;

		expect(movingWindow.bottom).to.equal(sw2.top);
		expect(movingWindow.right).to.equal(sw2.right);


		//move inside of the rightmost stationary window's right buffer		
		moveRequest = createMoveRequest({
			newLeft: movingWindow.left,
			newRight: stationaryWindow.right - 7,
			newTop: movingWindow.top,
			newBottom: movingWindow.bottom,
			win: movingWindow,
			changeType: 1
		});

		movingWindow.requestBoundsChange(moveRequest);
		expect(movingWindow.top).to.equal(oldTop);
		expect(movingWindow.bottom).to.equal(stationaryWindow.top);
		expect(movingWindow.right).to.equal(stationaryWindow.right);

		moveRequest = createMoveRequest({
			newLeft: movingWindow.left,
			newRight: sw2.right + 47,
			newTop: movingWindow.top,
			newBottom: movingWindow.bottom,
			win: movingWindow,
			changeType: 1
		});
		movingWindow.requestBoundsChange(moveRequest);

		expect(movingWindow.right).to.equal(sw2.right + 47);
		expect(movingWindow.top).to.equal(oldTop);
	});

	it('Resizing via left edge, should snap to the top left edge of each window below it.', function () {
		var newLeft = stationaryWindow.right - 40 - movingWindow.width;
		var newTop = stationaryWindow.top - movingWindow.height;
		var moveRequest = createMoveRequest({
			newLeft: newLeft,
			newTop: newTop,
			newBottom: stationaryWindow.top - 10,
			newRight: newLeft + movingWindow.width,
			changeType: 0,
			win: movingWindow
		});
		movingWindow.requestBoundsChange(moveRequest);
		//start with the left edge offset from the left stationary window.

		//move inside of the left-most stationary window's right buffer.
		moveRequest = createMoveRequest({
			newLeft: stationaryWindow.left + 10,
			newRight: movingWindow.right,
			newTop: movingWindow.top,
			newBottom: movingWindow.bottom,
			win: movingWindow,
			changeType: 1
		});
		movingWindow.requestBoundsChange(moveRequest);

		var oldTop = movingWindow.top;

		expect(movingWindow.bottom).to.equal(stationaryWindow.top);
		expect(movingWindow.left).to.equal(stationaryWindow.left);


		//move inside of the rightmost stationary window's right buffer		
		moveRequest = createMoveRequest({
			newLeft: sw2.left + 7,
			newRight: movingWindow.right,
			newTop: movingWindow.top,
			newBottom: movingWindow.bottom,
			win: movingWindow,
			changeType: 1
		});

		movingWindow.requestBoundsChange(moveRequest);

		expect(movingWindow.top).to.equal(oldTop);
		expect(movingWindow.bottom).to.equal(sw2.top);
		expect(movingWindow.left).to.equal(sw2.left);
	});
});

context('Moving window sitting below two windows placed edge to edge', function () {
	it('Resizing via right edge, should snap to the right edge of each window below it.', function () {
		var newLeft = sw2.left + 40;

		var newTop = sw2.bottom;
		var moveRequest = createMoveRequest({
			newLeft: newLeft,
			newTop: newTop,
			newBottom: newTop + 60,
			newRight: newLeft + movingWindow.width,
			changeType: 0,
			win: movingWindow
		});
		movingWindow.requestBoundsChange(moveRequest);
		//start with the left edge offset from the left stationary window.

		//move inside of the left-most stationary window's right buffer.	
		moveRequest = createMoveRequest({
			newLeft: movingWindow.left,
			newRight: sw2.right - 12,
			newTop: movingWindow.top,
			newBottom: movingWindow.bottom,
			win: movingWindow,
			changeType: 1
		});
		var oldTop = movingWindow.top;
		movingWindow.requestBoundsChange(moveRequest);

		expect(movingWindow.top).to.equal(sw2.bottom);
		expect(movingWindow.right).to.equal(sw2.right);


		//move inside of the rightmost stationary window's right buffer		
		moveRequest = createMoveRequest({
			newLeft: movingWindow.left,
			newRight: stationaryWindow.right + 7,
			newTop: movingWindow.top,
			newBottom: movingWindow.bottom,
			win: movingWindow,
			changeType: 1
		});
		movingWindow.requestBoundsChange(moveRequest);

		expect(movingWindow.top).to.equal(oldTop);
		expect(movingWindow.top).to.equal(stationaryWindow.bottom);
		expect(movingWindow.right).to.equal(stationaryWindow.right);

		moveRequest = createMoveRequest({
			newLeft: movingWindow.left,
			newRight: sw2.right + 47,
			newTop: movingWindow.top,
			newBottom: movingWindow.bottom,
			win: movingWindow,
			changeType: 1
		});
		movingWindow.requestBoundsChange(moveRequest);

		expect(movingWindow.right).to.equal(sw2.right + 47);
		expect(movingWindow.top).to.equal(oldTop);
	});

	it('Resizing via left edge, should snap to the top left edge of each window below it.', function () {
		var newLeft = stationaryWindow.right - 40 - movingWindow.width;
		var newTop = stationaryWindow.top - movingWindow.height;
		var moveRequest = createMoveRequest({
			newLeft: newLeft,
			newTop: newTop,
			newBottom: newTop + 60,
			newRight: newLeft + movingWindow.width,
			changeType: 0,
			win: movingWindow
		});
		movingWindow.requestBoundsChange(moveRequest);

		//move inside of the left-most stationary window's right buffer.	
		var moveRequest = createMoveRequest({
			newLeft: stationaryWindow.left + 10,
			newRight: movingWindow.right,
			newTop: movingWindow.top,
			newBottom: movingWindow.bottom,
			win: movingWindow,
			changeType: 1
		});

		movingWindow.requestBoundsChange(moveRequest);

		var oldTop = movingWindow.top;

		expect(movingWindow.bottom).to.equal(stationaryWindow.top);
		expect(movingWindow.left).to.equal(stationaryWindow.left);


		//move inside of the rightmost stationary window's right buffer		
		moveRequest = createMoveRequest({
			newLeft: sw2.left + 7,
			newRight: movingWindow.right,
			newTop: movingWindow.top,
			newBottom: movingWindow.bottom,
			win: movingWindow,
			changeType: 1
		});

		movingWindow.requestBoundsChange(moveRequest);

		expect(movingWindow.top).to.equal(oldTop);
		expect(movingWindow.bottom).to.equal(sw2.top);
		expect(movingWindow.left).to.equal(sw2.left);
	});
});



