before(function () {
	//if i just want to run these tests.
	if (!A.name) {
		var alphabet = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ';
		DockingMath.removeWindow(sw2.name);
		DockingMath.removeWindow(stationaryWindow.name);
		function addWindowsToGrid(arr) {
			return new Promise(function (resolve, reject) {
				windowsAdded = 0;
				arr.forEach((name) => {
					addWindowToGrid(name).then(function () {
						windowsAdded++;
						if (windowsAdded === arr.length) {
							resolve();
						}
					});
				});

			});
		}
		function createGrid() {
			//creates a 3x3 grid
			var row = 0;
			var column = 0;
			for (var i = 0; i < 9; i++) {
				var windowName = alphabet[i];
				var mockWin = new mockWindow(windowName, 'ciq-test');
				if (i > 1 && i % 3 === 0) {
					row++;
					column = 0;
				}
				mockWin.setBounds(
					300 * column,
					300 * row,
					300,
					300
				);
				mockGrid[mockWin.name] = mockWin;
				column++;

			}
		}
		createGrid();
		addWindowsToGrid(['A', 'B', 'C', 'D', 'E', 'F', 'G', 'H', 'I']).then(function () {
			A = DockingMath.getWindow('A');
			B = DockingMath.getWindow('B');
			C = DockingMath.getWindow('C');
			D = DockingMath.getWindow('D');
			E = DockingMath.getWindow('E');
			F = DockingMath.getWindow('F');
			G = DockingMath.getWindow('G');
			H = DockingMath.getWindow('H');
			I = DockingMath.getWindow('I');
			group1 = DockingMath.getGroup('Group0');

		});
	}

});
it('should resize a vertically shared edge - non straddle', function () {
	resetGrid();
	//Resize left edge 22 px left.
	var cachedFBounds = F.getBounds();
	var cachedEBounds = E.getBounds();
	var moveRequest = createMoveRequest({
		newLeft: F.left - 32,
		newTop: F.top,
		newBottom: F.bottom,
		newRight: F.right,
		changeType: 1,
		win: F
	});	
	F.requestBoundsChange(moveRequest);

	expect(E.left).to.equal(cachedEBounds.left);
	expect(F.right).to.equal(cachedFBounds.right);

	expect(F.left).to.equal(E.right);

	//Resize left edge 32 px right.
	cachedFBounds = F.getBounds();
	cachedEBounds = E.getBounds();
	moveRequest = createMoveRequest({
		newLeft: F.left + 32,
		newTop: F.top,
		newBottom: F.bottom,
		newRight: F.right,
		changeType: 1,
		win: F
	});
	F.requestBoundsChange(moveRequest);

	expect(E.left).to.equal(cachedEBounds.left);
	expect(F.right).to.equal(cachedFBounds.right);
	expect(F.left).to.equal(E.right);

	//Resize right edge of 33 px right.
	cachedEBounds = E.getBounds();
	cachedFBounds = F.getBounds();
	moveRequest = createMoveRequest({
		newLeft: E.left,
		newTop: E.top,
		newBottom: E.bottom,
		newRight: E.right + 33,
		changeType: 1,
		win: E
	});

	E.requestBoundsChange(moveRequest);

	expect(E.left).to.equal(cachedEBounds.left);
	expect(F.right).to.equal(cachedFBounds.right);

	expect(F.left).to.equal(E.right);

	//resize right edge of E 33 px left				
	cachedEBounds = E.getBounds();
	cachedFBounds = F.getBounds();
	moveRequest = createMoveRequest({
		newLeft: E.left,
		newTop: E.top,
		newBottom: E.bottom,
		newRight: E.right - 33,
		changeType: 1,
		win: E
	});

	E.requestBoundsChange(moveRequest);

	expect(E.left).to.equal(cachedEBounds.left);
	expect(F.right).to.equal(cachedFBounds.right);

	expect(F.left).to.equal(E.right);
});

it('should resize a vertically shared edge - single straddle case', function () {
	resetGrid();
	expect(B.bottom).to.equal(E.top);
	expect(B.bottom).to.equal(300);
	expect(F.top).to.equal(300);

	var cachedFBounds = F.getBounds();
	var cachedEBounds = E.getBounds();
	var moveRequest = createMoveRequest({
		newLeft: F.left - 36,
		newTop: F.top,
		newBottom: F.bottom,
		newRight: F.right,
		changeType: 1,
		win: F
	});
	F.requestBoundsChange(moveRequest);
	expect(F.top).to.equal(300);

	expect(E.left).to.equal(cachedEBounds.left);
	expect(F.right).to.equal(cachedFBounds.right);
	expect(F.left).to.equal(E.right);
	cachedFBounds = F.getBounds();
	cachedBBounds = B.getBounds();
	moveRequest = createMoveRequest({
		newLeft: F.left,
		newTop: F.top - 20,
		newBottom: F.bottom,
		newRight: F.right,
		changeType: 1,
		win: F
	});
	F.resizeHandle = null;
	F.requestBoundsChange(moveRequest);

	expect(E.left).to.equal(cachedEBounds.left);
	expect(F.top).to.equal(B.bottom);
	expect(E.top).to.equal(F.top);


	expect(F.right).to.equal(cachedFBounds.right);
	expect(F.left).to.equal(E.right);
	expect(F.left).to.be.lessThan(B.right);

	expect(C.bottom).to.equal(F.top);
	expect(B.bottom).to.equal(F.top);


	cachedFBounds = F.getBounds();
	cachedBBounds = B.getBounds();
	moveRequest = createMoveRequest({
		newLeft: B.left,
		newTop: B.top,
		newBottom: B.bottom - 10,
		newRight: B.right,
		changeType: 1,
		win: B
	});
	B.requestBoundsChange(moveRequest);

	expect(B.bottom).to.equal(F.top);
	expect(B.bottom).to.equal(E.top);
	expect(B.bottom).to.equal(C.bottom);


});
it('should resize a vertically shared edge - double straddle case', function () {
	resetGrid();
	var cachedFBounds = F.getBounds();
	var cachedEBounds = E.getBounds();
	var moveRequest = createMoveRequest({
		newLeft: F.left,
		newTop: F.top,
		newBottom: F.bottom - 70,
		newRight: F.right,
		changeType: 1,
		win: F
	});

	F.requestBoundsChange(moveRequest);
	expect(I.top).to.be.lessThan(E.bottom);

	moveRequest = createMoveRequest({
		newLeft: F.left,
		newTop: F.top + 22,
		newBottom: F.bottom,
		newRight: F.right,
		changeType: 1,
		win: F
	});
	F.requestBoundsChange(moveRequest);
	
	expect(E.bottom).to.be.greaterThan(F.bottom);
	expect(I.bottom).to.be.greaterThan(E.top);
	moveRequest = createMoveRequest({
		newLeft: B.left,
		newTop: B.top,
		newBottom: B.bottom,
		newRight: B.right - 70,
		changeType: 1,
		win: B
	});

	B.requestBoundsChange(moveRequest);	
	//Test the left edge of each, compare them to the right edge of B, which just moved over.
	[F.left, I.left, C.left, E.right, H.right].forEach((number, i) => {
		expect(number).to.equal(B.right);
	});
	moveRequest = createMoveRequest({
		newLeft: B.left,
		newTop: B.top,
		newBottom: B.bottom,
		newRight: B.right + 90,
		changeType: 1,
		win: B
	});
	B.requestBoundsChange(moveRequest);
	//Test the left edge of each, compare them to the right edge of E, which just moved over.
	[F.left, I.left, C.left, E.right, H.right].forEach((number, i) => {
		expect(number).to.equal(B.right);
	});
});

it('should resize a horizontally shared edge - non straddle', function () {
	var cachedBBounds = B.getBounds();
	var moveRequest = createMoveRequest({
		newLeft: B.left,
		newTop: B.top,
		newBottom: B.bottom - 24,
		newRight: B.right,
		changeType: 1,
		win: B
	});
	B.requestBoundsChange(moveRequest);

	expect(B.left).to.equal(cachedBBounds.left);
	expect(B.top).to.equal(cachedBBounds.top);
	expect(B.bottom).to.equal(E.top);

	var cachedEBounds = E.getBounds();
	moveRequest = createMoveRequest({
		newLeft: E.left,
		newTop: E.top - 12,
		newBottom: E.bottom,
		newRight: E.right,
		changeType: 1,
		win: E
	});
	E.requestBoundsChange(moveRequest);

	expect(B.left).to.equal(cachedBBounds.left);
	expect(B.top).to.equal(cachedBBounds.top);
	expect(E.top).to.equal(B.bottom);
});
it('should resize a horizontally shared edge - single straddle case', function () {
	resetGrid();

	var cachedFBounds = F.getBounds();
	var cachedEBounds = E.getBounds();
	var moveRequest = createMoveRequest({
		newLeft: F.left - 31,
		newTop: F.top,
		newBottom: F.bottom,
		newRight: F.right,
		changeType: 1,
		win: F
	});

	F.requestBoundsChange(moveRequest);
	expect(F.left).to.equal(E.right);

	moveRequest = createMoveRequest({
		newLeft: H.left,
		newTop: H.top - 31,
		newBottom: H.bottom,
		newRight: H.right,
		changeType: 1,
		win: H
	});
	//H's top edge shares a bottom edge with E and F.
	H.requestBoundsChange(moveRequest);
	[E.bottom, F.bottom].forEach((number) => {
		expect(number).to.equal(H.top);
	});

});
it('should resize a horizontally shared edge - double straddle case', function () {
	resetGrid();
	var moveRequest = createMoveRequest({
		newLeft: E.left,
		newTop: E.top,
		newBottom: E.bottom,
		newRight: E.right + 31,
		changeType: 1,
		win: E
	});

	E.requestBoundsChange(moveRequest);
	moveRequest = createMoveRequest({
		newLeft: H.left,
		newTop: H.top + 31,
		newBottom: H.bottom,
		newRight: H.right,
		changeType: 1,
		win: H
	});
	//H's top edge shares a bottom edge with E and F.
	H.requestBoundsChange(moveRequest);
	[E.bottom, F.bottom].forEach((number) => {
		expect(number).to.equal(H.top);
	});
});


