var chai = require('chai');
var assert = chai.assert;
var should = chai.should;
var expect = chai.expect;
var RouterClient = FSBL.Clients.RouterClient;

/********************************************
 * 			Transmit/Query setup.			*
 ********************************************/
var clientName = 'WigglesWorthMcGee',
	//Dataset to send back and forth in transmit and query/responder.
	testDataset = {
		first: "wow",
		second: true,
		third: null,
		//uncomment to break test.
		//fourth: new Date(),
		fifth: {
			"1st": "weee",
			"2nd": true,
			"3rd": null
		}
	},
	//List of tests. The properties are modified as callbacks are invoked; they're later validated. The Keys are the channels
	tests = {
		channels: {
			"NoSpecialCharacters": {
				transmitDataReceived: null,
				queryDataReceived: null,
				hasReceivedTransmitCallback: false,
				hasReceivedQueryCallback: false
			},
			"This has spaces": {
				transmitDataReceived: null,
				queryDataReceived: null,
				hasReceivedTransmitCallback: false,
				hasReceivedQueryCallback: false
			},
			"Thi$ H@$ 5p3c'al C4&rz": {
				transmitDataReceived: null,
				queryDataReceived: null,
				hasReceivedTransmitCallback: false,
				hasReceivedQueryCallback: false
			}
		}
	},
	//an array to quickly iterate through the channels.
	channelNames = Object.keys(tests.channels);

function transmitCallback(err, response) {
	expect(err).to.be.null;
	var channel = response.header.channel;
	var test = tests.channels[channel];
	test.hasReceivedTransmitCallback = true;
	test.transmitDataReceived = response.data;
}
function responderCallback(err, message) {
	message.sendQueryResponse(err, testDataset);
}

/********************************************
 *  			Pub/Sub Setup				*
 ********************************************/
//Will keep our subscription ID and topic once we've subscribed. Used in the unsubscribe.
var subscriptions = [],
	//Topics and data to send across tests.
	topics = [
		{
			topic: 'Football Scores',
			initial: {
				score: {
					Redskins: 0,
					Cowboys: 0
				}
			},
			subsequentUpdates: [
				{
					score: {
						Redskins: 7,
						Cowboys: 0
					}
				},
				{
					score: {
						Redskins: 10,
						Cowboys: 0
					}
				}, {
					score: {
						Redskins: 17,
						Cowboys: 0
					}
				},
				{
					score: {
						Redskins: 24,
						Cowboys: 3
					}
				}
			],
			dataReceived: []
		},
		{
			topic: 'Weight Loss!',
			initial: {
				weight: 205,
				reason: 'life'
			},
			subsequentUpdates: [
				{
					weight: 210,
					reason: 'beer'
				},
				{
					weight: 215,
					reason: 'pork'
				},
				{
					weight: 230,
					reason: 'vacation'
				}
			],
			finalUpdate: {
				weight: 205,
				reason: 'clothes got too tight'
			},
			dataReceived: [],
			customUnsubscribeInvoked: false
		}
	],
	football = topics[0],
	weight = topics[1];


function customSubscribeCallback(err, subscribe) {
	subscribe.data.weight *= 2;
	subscribe.sendNotifyToSubscriber(null, subscribe.data);
}
function customPublishCallback(err, publish) {
	publish.data.weight *= 2;
	publish.sendNotifyToAllSubscribers(null, publish.data);
}

function customUnsubscribeCallback(error, unsubscribe) {
	weight.customUnsubscribeInvoked = true;
	if (!error) {
		unsubscribe.removeSubscriber();
	}
}

describe('RouterClient', function () {

	context('Listen/Transmit', function () {
		it('should add several listeners', function (done) {
			channelNames.forEach((channel, index) => {
				var test = tests.channels[channel];
				RouterClient.addListener(channel, transmitCallback);
			});
			done();
		});

		it('should transmit several messages', function (done) {
			channelNames.forEach((channel, index) => {
				var test = tests.channels[channel];
				RouterClient.transmit(channel, testDataset);
			});
			setTimeout(function () {
				done();
			}, 400);
		});

		it('should verify that data was received as it was sent', function () {
			channelNames.forEach((channel, index) => {
				var test = tests.channels[channel];
				expect(test.hasReceivedTransmitCallback).to.be.true;
				expect(testDataset).to.deep.equal(test.transmitDataReceived);
			});
		});
	});

	context('Query/Responder', function () {
		it('should add several responders', function (done) {
			channelNames.forEach((channel, index) => {
				RouterClient.addResponder(channel, responderCallback);
			});
			RouterClient.addResponder("errorChannel", function (err,message) {
				message.sendQueryResponse("errorMessage", null);
			});
			RouterClient.addResponder("timeoutChannel", function (err,message) {
				setTimeout(function () {
					message.sendQueryResponse("errorMessage", null);
				}, 2000);
			});
			done();
		});

		it('should query the responders', function (done) {
			var testsComplete = 0;
			channelNames.forEach((channel, index) => {
				var test = tests.channels[channel];
				RouterClient.query(channel, null, function (error, response) {
					var test = tests.channels[channel];
					test.queryDataReceived = response.data;
					test.hasReceivedQueryCallback = true;
					testsComplete++;
					if (testsComplete === Object.keys(tests.channels).length) {
						done();
					}
				});
			});
		});

		it('should verify that data was received as it was sent', function () {
			channelNames.forEach((channel, index) => {
				var test = tests.channels[channel];
				expect(test.hasReceivedTransmitCallback).to.be.true;
				expect(testDataset).to.deep.equal(test.queryDataReceived);
			});
		});

		it('should receive error message back on queryResponse error', function (done) {
			RouterClient.query("errorChannel", null, function (error, response) {
				expect(error).to.equal("errorMessage");
				if (error) {
					done();
				}
			});
		});

		it('should receive error message back on query to underdefined channel', function (done) {
			RouterClient.query("undefinedChannel", null, function (error, response) {
				if (error) {
					done();
				}
			});
		});

		it('should receive error message back on creating duplicate local responder', function (done) {
			RouterClient.addResponder("errorChannel", function (error, message) {
				if (error) {
					done();
				}
			});
		});

		it('should write to console timeout warning', function (done) {
			console.log("SHOULD SEE TIMEOUT WARNING SOMEWHERE BELOW");
			RouterClient.query("timeoutChannel", null, { timeout: 1000 }, function (error, response) {
				done();
			});
		});

		it('should receive error message back on creating duplicate remote responder', function (done) {
			RouterClient.addResponder('Launcher.getWindowTitleBar', function (error, message) {
				if (error) {
					done();
				}
			});
		});

	});

	context('Pub/Sub', function () {

		it('should create a pub/sub responder for a static topic', function () {
			RouterClient.addPubSubResponder(weight.topic, weight.initial);
		});

		it('should create a pub/sub responder for a regex topic', function () {
			RouterClient.addPubSubResponder(/Football.*/, football.initial);
		});

		it('should subscribe to both topics', function () {
			var weightSubscription = RouterClient.subscribe(weight.topic, (err, notification) => {
				expect(err).to.be.null;
				weight.dataReceived.push(notification.data);
			});

			subscriptions.push(weightSubscription);

			var footballSubscription = RouterClient.subscribe(football.topic, (err, notification) => {
				expect(err).to.be.null;
				football.dataReceived.push(notification.data);
			});

			subscriptions.push(footballSubscription);
		});

		it('should publish a messages on each topic', function (done) {
			weight.subsequentUpdates.forEach((update, index) => {
				RouterClient.publish(weight.topic, update);
			});
			RouterClient.publish(weight.topic, weight.finalUpdate);
			football.subsequentUpdates.forEach((update, index) => {
				RouterClient.publish(football.topic, update);
			});
			setTimeout(function () {
				done();
			}, 400);
		});

		var weightData = [weight.initial]
			.concat(weight.subsequentUpdates)
			.concat(weight.finalUpdate),
			footballData = [football.initial].concat(football.subsequentUpdates);

		it('should validate the data that the subscribers received', function () {
			expect(weight.dataReceived).to.deep.equal(weightData);
			expect(football.dataReceived).to.deep.equal(footballData);
		});

		it('should subscribe after many messages have been published and receive last transmitted state', function (done) {
			RouterClient.subscribe(weight.topic, (err, response) => {
				expect(err).to.be.null;
				expect(response.data).to.deep.equal(weight.finalUpdate);
				done();
			});
		});

		it('should add two new pubsub responders that modify default behavior', function (done) {
			var params = {
				publishCallback: customPublishCallback,
				subscribeCallback: customSubscribeCallback,
				unsubscribeCallback: customUnsubscribeCallback
			};
			RouterClient.addPubSubResponder(
				"This is not default behavior",
				weight.initial,
				params);
			setTimeout(function () {
				done();
			}, 400);
		});

		it('should subscribe to the non-default pubsub responder', function (done) {
			weight.dataReceived = [];
			var subscription = RouterClient.subscribe("This is not default behavior", (err, response) => {
				if (!weight.dataReceived.length) {
					weight.initialDataReceived = response.data;
				}
				weight.dataReceived.push(response.data);
			});
			subscriptions.push(subscription);
			setTimeout(function () {
				done();
			}, 400);
		});

		it('should verify that the new subscriber received a modified initial state', function () {
			expect(weight.initialDataReceived.weight).to.equal(weight.initial.weight * 2);
		});

		it('should publish on topics that our new responders are listening to', function (done) {
			weight.subsequentUpdates.forEach((update, index) => {
				RouterClient.publish("This is not default behavior", update);
			});
			setTimeout(function () {
				done();
			}, 400);
		});

		it('should validate that the subscribers received modified weight data.', function () {
			//Data received should be the weights * 2;
			weight.dataReceived.forEach((weight, index) => {
				var dataSent = weightData.filter((sentWeight, index) => {
					return sentWeight.reason === weight.reason;
				})[0];
				expect(weight.weight).to.equal(dataSent.weight * 2);
			});
		});


	});

	context('Error Paths', function () {
		it('should add a second PubSub responder to an existing topic and receive an error', function (done) {
			var callbacksReceived = 0;
			RouterClient.addPubSubResponder(weight.topic, {}, {}, (err, reponse) => {
				expect(err).to.exist;
				callbacksReceived++;
				if (callbacksReceived === 2) {
					done();
				}
			});
			RouterClient.addPubSubResponder(/Football.*/, {}, {}, (err, reponse) => {
				expect(err).to.exist;
				callbacksReceived++;
				if (callbacksReceived === 2) {
					done();
				}
			});
		});


		it('should add a second queryResponder on an existing channel and receive an error', function (done) {
			RouterClient.addResponder(Object.keys(tests.channels)[0], (err, response) => {
				expect(err).to.exist;
				done();
			});
		});

	});

	context('Removal', function () {
		it('should remove all listeners', function () {
			channelNames.forEach((channel, index) => {
				RouterClient.removeListener(channel, transmitCallback);
			});

		});

		it('should remove all responders', function () {
			channelNames.forEach((channel, index) => {
				RouterClient.removeResponder(channel, transmitCallback);
			});
		});

		it('should unsubscribe from all topics', function () {
			subscriptions.forEach((subscription, index) => {
				RouterClient.unsubscribe(subscription);
			});
		});
		it('should remove pubSubResponders', function () {
			RouterClient.removePubSubResponder(weight.topic);
			RouterClient.removePubSubResponder(/Football.*/);
		});
	});


	context('Removal Testing', function () {

		before(function () {
			weight.dataReceived = [];
			Object.keys(tests.channels).forEach((channel, index) => {
				var test = tests.channels[channel];
				test.hasReceivedQueryCallback = false;
				test.hasReceivedTransmitCallback = false;
			});
		});

		it('should transmit, query, and publish on removed channels', function (done) {
			Object.keys(tests.channels).forEach((channel, index) => {
				RouterClient.transmit(channel, {
					dummy: 'data'
				});
				RouterClient.query(channel, responderCallback);
			});

			setTimeout(function () {
				done();
			}, 400);
		});

		//these next two are split out so that if there's a failure, it's not difficult to figure out where the failure was.
		it('should verify that no data was received from transmissions', function () {
			Object.keys(tests.channels).forEach((channel, index) => {
				var test = tests.channels[channel];
				expect(test.hasReceivedTransmitCallback).to.be.false;
			});
		});

		it('should verify that no data was received from responders', function () {
			Object.keys(tests.channels).forEach((channel, index) => {
				var test = tests.channels[channel];
				expect(test.hasReceivedTransmitCallback).to.be.false;
			});
		});

		it('should publish on topics with no subscribers', function (done) {
			RouterClient.publish(weight.topic, {
				weight: 2222,
				reason: 'never left the bed.'
			});
			setTimeout(function () {
				done();
			}, 400);
		});

		it('should confirm that the custom unsubscribe method was invoked', function () {
			expect(weight.customUnsubscribeInvoked).to.be.true;
		});

		it('should verify that no data was recieved for pubsub with no subscribers', function () {
			expect(weight.dataReceived.length).to.equal(0);
		});

	});

	context('Burst-Transmit Performance Testing -- See Console.Info for Times (logLevel=3)', function () {

		var RouterClientConstructor = require('./../../../../../../src/clients/routerClientConstructor');
		var counter = 2500;

		it('SharedWorker transport', function (done) {
			var RouterClient = new RouterClientConstructor({ clientName: "timeTest1Client", transportName: "SharedWorker"});
			var start, end;
			var anArray = [];
			var timeTestAListener;

			this.timeout(5000); // increase Mocha timeout

			for (var i = 0; i < 10; i++) { // can change to test passing large messages
				anArray.push(i);
			}

			RouterClient.onReady(function () {
				RouterClient.addListener("TimeTestA", timeTestAListener = function(error, response) {
					if (response.data.count === counter) {
						end = new Date();
						var elapsedTime = end.getTime() - start.getTime();
						var speedtestStatus = "SharedWorker Burst SpeedTest " + Math.round(counter / elapsedTime * 1000) + " msgs/sec";
						console.info(speedtestStatus);
						RouterClient.removeListener("TimeTestA", timeTestAListener);
						done();
					}
				});

				start = new Date();
				for (var i = 0; i < counter; i++) {
					RouterClient.transmit("TimeTestA", { "count": i + 1 , dataArray: anArray});
				}
			});

		});

		it('OpenFinBus transport', function (done) {
			var RouterClient = new RouterClientConstructor({ clientName: "timeTest1Client", transportName: "OpenFinBus"});
			var start, end;
			var anArray = [];
			var timeTestAListener;

			this.timeout(5000); // increase Mocha timeout

			for (var i = 0; i < 10; i++) { // can change to test passing large messages
				anArray.push(i);
			}

			RouterClient.onReady(function () {
				RouterClient.addListener("TimeTestA", timeTestAListener = function(error, response) {
					if (response.data.count === counter) {
						end = new Date();
						var elapsedTime = end.getTime() - start.getTime();
						var speedtestStatus = "OpenFin Burst SpeedTest " + Math.round(counter / elapsedTime * 1000) + " msgs/sec";
						console.info(speedtestStatus);
						RouterClient.removeListener("TimeTestA", timeTestAListener);
						done();
					}
				});

				start = new Date();
				for (var i = 0; i < counter; i++) {
					RouterClient.transmit("TimeTestA", { "count": i + 1 , dataArray: anArray});
				}
			});

		});

	});

	context('Round-Trip-Query Performance Testing -- See Console.Info for Times (logLevel=3)', function () {

		var RouterClientConstructor = require('./../../../../../../src/clients/routerClientConstructor');

		it('SharedWorker transport', function (done) {
			var RouterClient = new RouterClientConstructor({ clientName: "timeTest1Client", transportName: "SharedWorker"});
			counter = 2000;

			this.timeout(5000); // increase Mocha timeout

			RouterClient.onReady(function () {
				function serverCallBack(error, queryMessage) {
					if (error) {
						console.log('Error in Server CallBack ' + JSON.stringify(error));
					} else {
						queryMessage.sendQueryResponse(null, queryMessage.data);
					}
				}

				RouterClient.addResponder("TimeTestB", serverCallBack);

				function queryResponseCallBack(error, response) {
					if (error) {
						console.log('Error in queryResponse ' + JSON.stringify(error));
					} else if (response.data.count === counter) {
						end = new Date();
						var elapsedTime = end.getTime() - start.getTime();
						var speedStatus = "SharedWorker RoundTrip SpeedTest " + Math.round(counter / elapsedTime * 1000) + " round trips/sec";
						console.info(speedStatus);
						RouterClient.removeResponder("TimeTestB");
						done();
					}
				}

				start = new Date();
				for (var i = 0; i < counter; i++) {
					RouterClient.query("TimeTestB", { "count": i + 1 }, queryResponseCallBack);
				}
			});
		});

		it('OpenFinBus transport', function (done) {
			var RouterClient = new RouterClientConstructor({ clientName: "timeTest1Client", transportName: "OpenFinBus"});
			counter = 2000;

			this.timeout(5000); // increase Mocha timeout

			RouterClient.onReady(function () {
				function serverCallBack(error, queryMessage) {
					if (error) {
						console.log('Error in Server CallBack ' + JSON.stringify(error));
					} else {
						queryMessage.sendQueryResponse(null, queryMessage.data);
					}
				}

				RouterClient.addResponder("TimeTestB", serverCallBack);

				function queryResponseCallBack(error, response) {
					if (error) {
						console.log('Error in queryResponse ' + JSON.stringify(error));
					} else if (response.data.count === counter) {
						end = new Date();
						var elapsedTime = end.getTime() - start.getTime();
						var speedStatus = "OpenFin RoundTrip SpeedTest " + Math.round(counter / elapsedTime * 1000) + " round trips/sec";
						console.info(speedStatus);
						RouterClient.removeResponder("TimeTestB");
						done();
					}
				}

				start = new Date();
				for (var i = 0; i < counter; i++) {
					RouterClient.query("TimeTestB", { "count": i + 1 }, queryResponseCallBack);
				}
			});
		});

	});

});



