var React = require("react");
var TestRunnerActions = require("../stores/TestRunnerStore").Actions;
import TestItem from './TestItem';

class TestList extends React.Component {
	constructor() {
		super();
	}
	render() {
		let tests = this.props.tests;
		let self = this;

		let testList = [];
		tests.forEach((test, i) => {
			let item = <TestItem qs={self.props.qs} filter={self.props.filter} key={i} test={test} />;

			if (TestRunnerActions.testFilter(test, true, self.props.filter)) {
				testList.push(item);
			}
		});
		if (testList.length) {
			return <ul className="use-icons">
				{testList}
			</ul>
		} else {
			return null;
		}

	}
};

export default TestList;


