var Helpers = require('./Components/helpers');
const FIRST_WORKSPACE_NAME = 'Testing-McTesterson',
	SECOND_WORKSPACE_NAME = 'secondTestWS',
	SAVE_AS_NAME = 'SavedAs',
	BASE_DELAY = 2000;

function confirmAction(params) {
	let { choice, cb, timeout } = params;

	timeout = typeof timeout === 'undefined' ? BASE_DELAY : timeout;

	FSBL.Clients.RouterClient.query('TestRunner.DialogYesNo', {
		choice: choice
	}, function (err, message) {
		expect(err).to.be.null;
		setTimeout(cb, timeout);
	});
}
/**
 * Workspaces
 */

describe('System Components', function () {


	var numberOfWorkspacesAtStart = 0,
		numberOfWorkspacesAfterCancel = 0,
		numberOfWorkspacesAfterCreate = 0,
		numberOfWorkspacesAtEnd = 0,
		numberOfPinnedComponents = 0,
		numberOfWindowsInWorkspaceOnSave = 0,
		INITIAL_WORKSPACE_NAME = null;



	before(function (done) {
		FSBL.addEventListener('onReady', function () {
			console.log('FSBL IS READY');
			INITIAL_WORKSPACE_NAME = FSBL.Clients.WorkspaceClient.activeWorkspace.name;
			FSBL.Clients.WorkspaceClient.switchTo({
				name: 'Workspace 1'
			}, function () {
				FSBL.Clients.WorkspaceClient.remove({
					name: FIRST_WORKSPACE_NAME
				}, function () {
					FSBL.Clients.WorkspaceClient.remove({
						name: SECOND_WORKSPACE_NAME
					}, function () {
						FSBL.Clients.WorkspaceClient.remove({
							name: SAVE_AS_NAME
						}, function () {
							done();
						})
					})
				})
			});
		});
	});

	describe('Toolbar', function () {
		require('./Components/ToolbarTest');
	});
	describe('Workspace creation', function () {
		before(function (done) {
			FSBL.Clients.RouterClient.query('TestRunner.WorkspaceManagementMenu', {
				test: 'getWorkspaceList',
			}, function (err, message) {
				numberOfWorkspacesAtStart = message.data.workspaces.length;
				//set WS to dirty to make sure those menus work.
				FSBL.Clients.RouterClient.transmit('WorkspaceService.setActiveWorkspaceDirty', null, null);
				setTimeout(done, 750);
			});
		});

		it('should click the new workspace button', function (done) {
			this.timeout(10000);
			FSBL.Clients.RouterClient.query('TestRunner.WorkspaceManagementMenu', {
				test: 'createWorkspace',
			}, function (err, message) {
				setTimeout(done, BASE_DELAY);
			});
		});

		it('should cancel workspace creation at the save dialog', function (done) {
			this.timeout(10000);
			confirmAction({
				choice: 'cancel',
				cb: done
			});
		});

		it('should verify that no new workspace was created.', function (done) {
			FSBL.Clients.RouterClient.query('TestRunner.WorkspaceManagementMenu', {
				test: 'getWorkspaceList',
			}, function (err, message) {
				numberOfWorkspacesAfterCancel = message.data.workspaces.length;
				expect(numberOfWorkspacesAtStart).to.equal(numberOfWorkspacesAfterCancel);
				done();
			});
		});

		it('should click the new workspace button', function (done) {
			this.timeout(10000);
			FSBL.Clients.RouterClient.query('TestRunner.WorkspaceManagementMenu', {
				test: 'createWorkspace',
			}, function (err, message) {
				setTimeout(done, BASE_DELAY);
			});
		});

		it('should save active workspace when prompted', function (done) {
			this.timeout(10000);
			confirmAction({ choice: 'affirmative', cb: done });
		});

		it('should dirty the workspace so the prompt will appear on save.', function (done) {
			FSBL.Clients.RouterClient.transmit('WorkspaceService.setActiveWorkspaceDirty', null, null);
			setTimeout(done, BASE_DELAY);
		});

		it('should click the new workspace button', function (done) {
			this.timeout(10000);
			FSBL.Clients.RouterClient.query('TestRunner.WorkspaceManagementMenu', {
				test: 'createWorkspace',
			}, function (err, message) {
				setTimeout(done, BASE_DELAY * 2);
			});
		});

		it('should not save the current workspace', function (done) {
			this.timeout(10000);
			FSBL.Clients.RouterClient.query('TestRunner.DialogYesNo', {
				choice: 'negative'
			}, function (err, message) {
				done();
			});
		});

		it('should make sure our workspace is not in the menu yet', function (done) {
			FSBL.Clients.RouterClient.query('TestRunner.WorkspaceManagementMenu', {
				test: 'getWorkspaceList',
			}, function (err, message) {
				expect(message.data.workspaces.includes(FIRST_WORKSPACE_NAME)).to.be.false;
				done();
			});
		});

		it('should create our first test workspace', function (done) {
			this.timeout(10000);
			FSBL.Clients.RouterClient.query('TestRunner.DialogSingleInput', {
				input: FIRST_WORKSPACE_NAME,
				choice: 'affirmative'
			}, function (err, message) {
				//Need to give time for the input to flow back to the component.
				setTimeout(function () {
					confirmAction({ choice: 'affirmative', cb: done, timeout: BASE_DELAY / 2 });
				}, BASE_DELAY * 2);
			});
		});

		it('should update the number of WorkspacesAfterCreate', function (done) {
			FSBL.Clients.RouterClient.query('TestRunner.WorkspaceManagementMenu', {
				test: 'getWorkspaceList',
			}, function (err, message) {
				numberOfWorkspacesAfterCreate = message.data.workspaces.length;
				expect(numberOfWorkspacesAtStart + 1).to.equal(numberOfWorkspacesAfterCreate);
				done();
			});
		});

		it('should create a second workspace for later', function (done) {
			//We've already tested the UI for creating workspaces. Now we create a second workspace. Later in the test, we'll overwrite this workspace.
			this.timeout(60000);
			FSBL.Clients.WorkspaceClient.switchTo({
				name: SECOND_WORKSPACE_NAME,
			}, function (err, message) {
				setTimeout(done, BASE_DELAY / 2);
			});
		});

		it('should switch back to the first test workspace', function (done) {
			this.timeout(10000);
			FSBL.Clients.RouterClient.query('TestRunner.WorkspaceManagementMenu', {
				test: 'switchWorkspace',
				name: FIRST_WORKSPACE_NAME
			}, function (err, message) {
				expect(FSBL.Clients.WorkspaceClient.activeWorkspace.name).to.equal(FIRST_WORKSPACE_NAME);
				done();
			});
		});
	});
	describe('Workspace saving and reloading', function () {
		it('should spawn two simple charts that will persist in the workspace', function (done) {
			this.timeout(10000);
			function spawnChart() {
				return new Promise(function (resolve, reject) {
					FSBL.Clients.RouterClient.query('TestRunner.AppLauncherMenu', {
						test: 'spawnComponent',
						name: 'Simple Chart'
					}, function (err, message) {
						resolve();
					});
				});
			}

			spawnChart()
				.then(spawnChart)
				.then(done);
		});
		it('should save the workspace', function (done) {
			this.timeout(10000);
			FSBL.Clients.RouterClient.query('TestRunner.WorkspaceManagementMenu', {
				test: 'saveWorkspace'
			}, function (err, message) {
				setTimeout(function () {
					numberOfWindowsInWorkspaceOnSave = FSBL.Clients.WorkspaceClient.activeWorkspace.windows.length;
					done();
				}, BASE_DELAY);
			});
		});
		context('reloading the active workspace', function () {
			it('should click the button to reload our test workspace.', function (done) {
				this.timeout(10000);
				FSBL.Clients.RouterClient.query('TestRunner.WorkspaceManagementMenu', {
					test: 'switchWorkspace',
					name: FIRST_WORKSPACE_NAME
				}, function (err, message) {
					setTimeout(done, BASE_DELAY);
				});
			});

			it('should confirm that we want to reload our workspace', function (done) {
				this.timeout(10000);
				confirmAction({ choice: 'affirmative', cb: done });
			});

			//@todo, reenable when openfin fixes the cross domain preload issues.
			it('should verify that all windows are loaded in the workspace', function (done) {
				this.timeout(60000);
				let wsWindows = FSBL.Clients.WorkspaceClient.activeWorkspace.windows;
				let windowsFound = 0;
				let timeoutDuration = wsWindows.length * (1000);
				setTimeout(function () {
					expect(numberOfWindowsInWorkspaceOnSave).to.equal(wsWindows.length);
					wsWindows.forEach((name) => {
						Helpers.getWindow(name)
							.then(function (finWin) {
								expect(finWin).to.exist;
								checkComplete();
							});
					});
				}, timeoutDuration);

				function checkComplete() {
					windowsFound++
					if (windowsFound === wsWindows.length) {
						done();
					}
				}
			});

		});
		context('save as', function () {

			context('not overwriting an existing workspace', function () {
				it('should click "save as"', function (done) {
					FSBL.Clients.RouterClient.query('TestRunner.WorkspaceManagementMenu', {
						test: 'saveWorkspaceAs',
					}, function (err, message) {
						expect(err).to.be.null;
						setTimeout(done, BASE_DELAY);
					});
				});

				it('should cancel the input', function (done) {
					this.timeout(10000);
					FSBL.Clients.RouterClient.query('TestRunner.DialogSingleInput', {
						choice: 'cancel',
						input: SAVE_AS_NAME
					}, function (err, response) {
						setTimeout(function () {
							expect(FSBL.Clients.WorkspaceClient.activeWorkspace.name).to.equal(FIRST_WORKSPACE_NAME);
							done();
						}, BASE_DELAY);
					});
				});

				it('should click "save as"', function (done) {
					FSBL.Clients.RouterClient.query('TestRunner.WorkspaceManagementMenu', {
						test: 'saveWorkspaceAs',
					}, function (err, message) {
						expect(err).to.be.null;
						setTimeout(done, BASE_DELAY);
					});
				});

				it('should input the new name into the dialog', function (done) {
					this.timeout(10000);
					FSBL.Clients.RouterClient.query('TestRunner.DialogSingleInput', {
						choice: 'affirmative',
						input: SAVE_AS_NAME
					},
						function (err, response) {
							setTimeout(function () {
								expect(err).to.be.null;
								expect(FSBL.Clients.WorkspaceClient.activeWorkspace.name).to.equal(SAVE_AS_NAME);
								confirmAction({ choice: 'affirmative', cb: done });
							}, BASE_DELAY * 2);
						});
				});
				it('should verify that the new workspace is in the list', function (done) {
					FSBL.Clients.RouterClient.query('TestRunner.WorkspaceManagementMenu', {
						test: 'getWorkspaceList',
					}, function (err, message) {
						expect(message.data.workspaces.includes(SAVE_AS_NAME)).to.be.true;
						done();
					});
				});
			});



		});

	});


	describe('Workspace pinning', function () {
		it('should pin a workspace to the toolbar', function (done) {
			this.timeout(5000);
			FSBL.Clients.RouterClient.query('TestRunner.WorkspaceManagementMenu', {
				test: 'toggleWorkspacePin',
				name: INITIAL_WORKSPACE_NAME
			}, function (err, message) {
				setTimeout(done, BASE_DELAY);
			});
		});
		it('should verify that the workspace is in the toolbar', function (done) {
			FSBL.Clients.RouterClient.query('TestRunner.Toolbar', {
				test: 'getPins',
			}, function (err, message) {
				expect(message.data.includes(INITIAL_WORKSPACE_NAME)).to.be.true;
				done();
			});
		});
		it('should launch a workspace from the toolbar', function (done) {
			this.timeout(60000);
			FSBL.Clients.RouterClient.query('TestRunner.Toolbar', {
				test: 'spawnPinnedWorkspace',
				name: INITIAL_WORKSPACE_NAME
			}, function (err, message) {
				setTimeout(done, FSBL.Clients.WorkspaceClient.activeWorkspace.windows.length * 1000);
			});
		});
		it('should verify that the proper workspace was loaded', function (done) {
			expect(FSBL.Clients.WorkspaceClient.activeWorkspace.name).to.equal(INITIAL_WORKSPACE_NAME);
			done();
		});
		it('should unpin the workspace', function (done) {
			this.timeout(5000);
			FSBL.Clients.RouterClient.query('TestRunner.WorkspaceManagementMenu', {
				test: 'toggleWorkspacePin',
				name: INITIAL_WORKSPACE_NAME
			}, function (err, message) {
				setTimeout(done, BASE_DELAY);
			});
		});
		it('should verify that the workspace is no longer in the toolbar', function (done) {
			FSBL.Clients.RouterClient.query('TestRunner.Toolbar', {
				test: 'getPins',
			}, function (err, message) {
				expect(message.data.includes(INITIAL_WORKSPACE_NAME)).to.be.false;
				done();
			});
		});
	});
	require('./Components/AppMenuTest');
	require('./Components/FileMenuTest');
	require('./Components/LinkerUI');

	context('overwriting an existing workspace', function () {
		it('should click "save as"', function (done) {
			this.timeout(10000);
			FSBL.Clients.RouterClient.query('TestRunner.WorkspaceManagementMenu', {
				test: 'saveWorkspaceAs',
			}, function (err, message) {
				expect(err).to.be.null;
				setTimeout(done, BASE_DELAY * 2);
			});
		});

		it('should input the new name into the dialog', function (done) {
			this.timeout(20000);
			FSBL.Clients.RouterClient.query('TestRunner.DialogSingleInput', {
				choice: 'affirmative',
				input: SECOND_WORKSPACE_NAME
			},
				function (err, response) {
					expect(err).to.be.null;
					//extra long timeout so that data can get back to the menu, the input can close, and the confirm box can pop up.
					setTimeout(done, BASE_DELAY);
				});
		});
		it('should confirm that we want to overwrite the workspace', function (done) {
			this.timeout(10000);
			setTimeout(function () {
				confirmAction({
					choice: 'affirmative',
					cb: function () {
						expect(FSBL.Clients.WorkspaceClient.activeWorkspace.name).to.equal(SECOND_WORKSPACE_NAME);
						done();
					}
				});
			}, BASE_DELAY);
		});
		it('should hide that annoying goddamned confirmation', function (done) {
			this.timeout(10000);
			setTimeout(function () {
				confirmAction({
					choice: 'affirmative',
					cb: function () {
						expect(FSBL.Clients.WorkspaceClient.activeWorkspace.name).to.equal(SECOND_WORKSPACE_NAME);
						done();
					}
				});
			}, BASE_DELAY * 2);
		});

	});
	describe('Workspace removal', function () {
		it('should switch to the workspace that was loaded when the test began.', function (done) {
			this.timeout(10000);
			FSBL.Clients.RouterClient.query('TestRunner.WorkspaceManagementMenu', {
				test: 'switchWorkspace',
				name: INITIAL_WORKSPACE_NAME
			}, function (err, message) {
				setTimeout(done, BASE_DELAY * 1.5);
			});
		});

		it('should click the delete button for the first workspace we saved earlier', function (done) {
			this.timeout(10000);
			FSBL.Clients.RouterClient.query('TestRunner.WorkspaceManagementMenu', {
				test: 'deleteWorkspace',
				name: FIRST_WORKSPACE_NAME
			}, function (err, message) {
				setTimeout(done, BASE_DELAY);
			});
		});


		it('should confirm delete', function (done) {
			this.timeout(10000);
			setTimeout(function () {
				confirmAction({ choice: 'affirmative', cb: done });
			}, BASE_DELAY);
		});

		it('should click the delete button for the second workspace we saved earlier', function (done) {
			this.timeout(10000);
			FSBL.Clients.RouterClient.query('TestRunner.WorkspaceManagementMenu', {
				test: 'deleteWorkspace',
				name: SECOND_WORKSPACE_NAME
			}, function (err, message) {
				setTimeout(done, BASE_DELAY);
			});
		});


		it('should confirm delete', function (done) {
			this.timeout(10000);
			setTimeout(function () {
				confirmAction({ choice: 'affirmative', cb: done });
			}, BASE_DELAY);
		});

		it('should click the delete button for the first workspace we saved earlier', function (done) {
			this.timeout(10000);
			FSBL.Clients.RouterClient.query('TestRunner.WorkspaceManagementMenu', {
				test: 'deleteWorkspace',
				name: SAVE_AS_NAME
			}, function (err, message) {
				setTimeout(done, BASE_DELAY);
			});
		});

		it('should confirm delete', function (done) {
			this.timeout(10000);
			setTimeout(function () {
				confirmAction({ choice: 'affirmative', cb: done });
			}, BASE_DELAY);
		});

		it('should make sure we have the same number of workspaces we started with.', function (done) {
			FSBL.Clients.RouterClient.query('TestRunner.WorkspaceManagementMenu', {
				test: 'getWorkspaceList',
			}, function (err, message) {
				numberOfWorkspacesAtEnd = message.data.workspaces.length;
				expect(numberOfWorkspacesAtStart).to.equal(numberOfWorkspacesAtEnd);
				done();
			});
		});

		it('should make sure our workspace was the one that was deleted', function (done) {
			FSBL.Clients.RouterClient.query('TestRunner.WorkspaceManagementMenu', {
				test: 'getWorkspaceList',
			}, function (err, message) {

				expect(message.data.workspaces.includes(SAVE_AS_NAME)).to.be.false;
				done();
			});
		});
	});
});




