
var chai = require('chai');
var assert = chai.assert;
var should = chai.should;
var expect = chai.expect;
var RouterClient = FSBL.Clients.RouterClient;
var Helpers = require('./helpers');
window.Helpers = Helpers;
describe('App Launcher', function () {
	beforeEach(function (done) {
		/**
		 * App launcher doesn't get the component list until it's clicked. So before each test we open it to force it to update.
		 */
		FSBL.Clients.RouterClient.query('TestRunner.Toolbar', {
			test: 'openAppsMenu'
		}, function (err, message) {
			//hide it.
			fin.desktop.Window.getCurrent().focus();
			done();
		});
	});

	it('should open the adHoc Component', function (done) {

		this.timeout(10000);
		RouterClient.query('TestRunner.AppLauncherMenu', {
			test: 'createAdHoc'
		}, function (err, message) {
			if (err) {
				console.error(err);
			}
			expect(err).to.be.null;
			done();
		});
	});

	it('should create a component named "adhoc-test"', function (done) {
		this.timeout(10000);
		Helpers.getWindow('adhoc')
			.then(function () {
				RouterClient.query('TestRunner.AdhocComponentForm', {
					test: 'create',
					name: 'adhoc-test',
					url: 'http://www.github.com'
				}, function (err, message) {
					if (err) {
						console.error(err);
					}
					expect(err).to.be.null;
					done();
				});
			});
	});



	it('should validate that the component exists in the App Launcher', function (done) {
		this.timeout(10000);
		RouterClient.query('TestRunner.AppLauncherMenu', {
			test: 'checkIfComponentExists',
			name: 'adhoc-test'
		}, function (err, message) {
			if (err) {
				console.error(err);
			}
			expect(message.data.exists).to.be.true;
			done();
		});
	});

	it('should click the pin icon for every component in the app launcher.', function (done) {
		this.timeout(50000);
		RouterClient.query('TestRunner.AppLauncherMenu', {
			test: 'pinAllComponents'
		}, function (err, message) {
			if (err) {
				console.error(err);
			}
			numberOfPinnedComponents = message.data.numPinned;
			done();

		});
	});
	it('should verify that all components are pinned to the toolbar', function (done) {
		RouterClient.query('TestRunner.Toolbar', {
			test: 'countPinnedComponents'
		}, function (err, message) {
			if (err) {
				console.error(err);
			}
			expect(message.data.count).to.equal(numberOfPinnedComponents);
			done();
		});
	});
	it('should launch all components that are pinned to the toolbar', function (done) {
		this.timeout(60000);
		RouterClient.query('TestRunner.Toolbar', {
			test: 'launchPinnedComponents'
		}, function (err, response) {
			expect(response.data.componentsSpawned.length).to.equal(numberOfPinnedComponents);
			if (err) {
				console.error(err);
			}
			done();
		});
	});
	it('should close all of those windows', function (done) {
		Helpers.closeAllWindows()
			.then(done);
	});
	it.skip('should open all of the components in the app launcher', function (done) {
		this.timeout(60000);
		let checked = 0;
		let componentList = [];
		function checkDone() {
			console.log("CHECKING", checked, componentList.length);
			if (checked === componentList.length) {
				done();
			}
		}
		RouterClient.query('TestRunner.AppLauncherMenu', {
			test: 'spawnComponents'
		}, function (err, message) {
			if (err) {
				console.error(err);
			}
			componentList = message.data.componentList;

			for (let i = 0; i < componentList.length; i++) {
				let componentName = componentList[i];
				Helpers.getWindow(componentName)
					.then(function (finWin) {
						console.log('checking vis for:', finWin.name);
						checked++;
						Helpers.getVisibility(finWin)
							.then(Helpers.shouldbeVisible)
							.then(checkDone)
							.catch((err) => {
								console.error(err);
							});
					})
					.catch(function (err) {
						console.error(err);
					});
			}
		});
	});
	it('should close all of those windows', function (done) {
		Helpers.closeAllWindows()
			.then(done);
	});
	it('should unpin every component in the App Launcher Menu', function (done) {
		this.timeout(5000);
		RouterClient.query('TestRunner.AppLauncherMenu', {
			test: 'unpinAllComponents'
		}, function (err, message) {
			if (err) {
				console.error(err);
			}
			done();
		});
	});
	it('should verify that no components remain pinned on the Toolbar', function (done) {
		RouterClient.query('TestRunner.Toolbar', {
			test: 'countPinnedComponents'
		}, function (err, message) {
			if (err) {
				console.error(err);
			}
			expect(message.data.count).to.equal(0);
			done();
		});
	});
	it('should click delete on our adHoc component named "adhoc-test"', function (done) {
		this.timeout(10000);
		RouterClient.query('TestRunner.AppLauncherMenu', {
			test: 'removeAdHoc',
			name: 'adhoc-test'
		}, function (err, message) {
			if (err) {
				console.error(err);
			}
			expect(err).to.be.null;
			done();
		});
	});
	it('should confirm that we want to delete the adHoc component', function (done) {
		this.timeout(10000);

		RouterClient.query('TestRunner.DialogYesNo', {
			choice: 'affirmative'
		},
			function (err, response) {
				if (err) {
					console.error(err);
				}
				expect(err).to.be.null;
				setTimeout(function () {
					done();
				}, 2000);
			});
	});

	it('should validate that the component no longer exists in the App Launcher', function (done) {
		this.timeout(10000);
		RouterClient.query('TestRunner.AppLauncherMenu', {
			test: 'checkIfComponentExists',
			name: 'adhoc-test'
		}, function (err, message) {
			if (err) {
				console.error(err);
			}
			expect(message.data.exists).to.be.false;
			done();
		});
	});
});


