var React = require("react");
import TestFilter from './TestFilter';
import TestStats from './TestStats';
import SuiteSelector from './SuiteSelector'
var TestRunnerActions = require("../stores/TestRunnerStore").Actions;
var TestRunnerStore = require("../stores/TestRunnerStore").Store;

class Header extends React.Component {
    constructor(props) {
        super(props);
        TestRunnerStore.addListener('change', () => {
            this.setState({
                qs: TestRunnerStore.qs
            });
        });
        TestRunnerStore.addListener('change:activeTest', () => {
            this.setState({
                currentTest: TestRunnerStore.getActiveTest().title
            });
        });
        TestRunnerStore.addListener('change:suiteStatus', () => {
            let suiteStatus = TestRunnerStore.getSuiteStatus();
            this.setState({
                runningTests: !['done', 'Not Started'].includes(suiteStatus)
            });
        });
        this.state = {
            currentTest: '...waiting',
            runningTests: false,
            qs: false
        }
    }
    redirect(suiteName) {
        var newURL = window.location.protocol + '//' + window.location.host + window.location.pathname;
        newURL += '?grep=' + suiteName;
        window.location = newURL;
    }
    filterTests(e) {
        TestRunnerActions.filterTests(this.refs.filter.value);
    }
    render() {
        return <div className="header">
            <div className="header-left">
                <SuiteSelector qs={this.state.qs}
                    selectOnChange={this.redirect.bind    (this)}
                />
                {/* <TestFilter /> */}
            </div>
            {this.state.runningTests &&
                <div className="header-center">
                    <div>Current Test</div>
                    <div>{this.state.currentTest}</div>
                </div>}
            <div className="header-right">
                <TestStats />
            </div>
        </div>
    }
};

export default Header;


