global.should = chai.should;
global.expect = chai.expect;
global.mockWindow = require('./../../mocks/mockWindow');
global.bufferSize = 15;
global.DockingMath = require('./../../mocks/mockDockingService');
global.DockableWindow = require('./../../../../../../../src/services/docking/dockableWindow');
global.blue = chalk.cyan;
global.yellow = chalk.yellow;
global.red = chalk.red;
global.cyan = chalk.cyan;
global.defaults = require('./dockingDefaults');
global.DockableGroup = require('./../../../../../../../src/services/docking/dockableGroup');
global.constants = require('./../../../../../../../src/services/docking/constants');
global.stationaryWindow = new mockWindow('stationary', 'ciq-test');
global.movingWindow = new mockWindow('moving', 'ciq-test');
global.toolbar = new mockWindow('toolbar', 'ciq-test');
global.sw2 = new mockWindow('stationary2', 'ciq-test');
global.groupMask = new mockWindow('groupMask', 'ciq-test');
global.BoxMath = require('./../../../../../../../src/services/docking/boxMath');
