movingWindow.setBounds(
	initialMovingWindowBounds.left,
	initialMovingWindowBounds.top,
	initialMovingWindowBounds.width,
	initialMovingWindowBounds.height
);
DockingMath.setGroupMask(groupMask);
toolbar.setBounds(-2000, -2000, 3000, 32);

sw2.setBounds(
	stationaryWindow.left - stationaryWindow.width,
	stationaryWindow.top,
	stationaryWindow.width,
	stationaryWindow.height);

DockingMath.setGroupMode(groupMode);


