function mockWindow(name, uuid) {
	this.top = null;
	this.left = null;
	this.right = null;
	this.bottom = null;
	this.width = null;
	this.height = null;
	this.name = name;
	this.uuid = uuid;


	this.moveTo = function (left, top, success, errCB) {
		this.left = left;
		this.top = top;
		this.setBounds(left, top, this.width, this.height);
	};

	this.setBounds = function (left, top, width, height, success, errCB) {
		this.left = left;
		this.top = top;
		this.width = width;
		this.height = height;
		this.right = left + width;
		this.bottom = top + height;
		if (success) {
			success();
		}
	};

	this.setOpacity = function (opacity) {
		this.opacity = opacity;
	};
	this.disableFrame = function () {
		this.frame = false;
	};
	this.events = {};

	this.addEventListener = function (event, cb) {
		if (!this.events[event]) {
			this.events[event] = [];
		}
		this.events[event].push(cb);
	};

	this.removeEventListener = function (event, cb) {
		if (this.events[event]) {
			this.events[event].splice(this.events[event].indexOf(cb), 1);
		}
	};

	this.getMousePosition = function (cb) {
		cb({});
	};
	this.setBufferSize = function (buffer) {
		this.bufferSize = buffer;
	};
	this.getBounds = function (success, errCB) {
		success({
			top: this.top,
			left: this.left,
			right: this.right,
			bottom: this.bottom,
			width: this.width,
			height: this.height
		});
	};
	this.hide = function () {
		this.status = 'hidden';
	};
	this.show = function () {
		this.status = 'visible';
	};
	this.bringToFront = function () {
		//dummy thing.
		return;
	};
	return this;
}

module.exports = mockWindow;


