var React = require("react");
var TestRunnerActions = require("../stores/TestRunnerStore").Actions;
class TestFilter extends React.Component {
	constructor(props) {
		super(props);
	}
    filterTests(e) {
        TestRunnerActions.filterTests({
            property: 'title',
            value: this.refs.filter.value
        });
    }
    render() {
        return <input ref="filter" onKeyUp={this.filterTests.bind(this)}/>
    }
};

export default TestFilter;


