//@todo, add buffer size.
context('Monitor snapping', function () {
	it('should add two monitors', function () {
		DockingMath.addMonitor({
			name: 'monitor1',
			left: 6000,
			top: -4000,
			right: 8000,
			bottom: -2000
		});
		var monitors = DockingMath.getMonitors();
		var monitorNames = Object.keys(monitors);
		expect(Object.keys(monitors)).to.have.lengthOf(1);
		expect(monitors[monitorNames[0]].name).to.equal('monitor1');

		DockingMath.addMonitor({
			name: 'monitor2',
			left: 8000,
			top: -4000,
			right: 1000,
			bottom: -2000
		});

		var monitors = DockingMath.getMonitors();
		var monitorNames = Object.keys(monitors);
		expect(Object.keys(monitors)).to.have.lengthOf(2);
		expect(monitors[monitorNames[1]].name).to.equal('monitor2');
	});

	context('Monitor snapping', function () {
		var monitors, monitorNames, monitor;
		before(function () {
			monitors = DockingMath.getMonitors();
			monitorNames = Object.keys(monitors);
			monitor = monitors[monitorNames[0]];
			expect(monitor.bounds.min.x).to.not.be.NaN;
			expect(monitor.bounds.min.y).to.not.be.NaN;
			expect(monitor.bounds.max.x).to.not.be.NaN;
			expect(monitor.bounds.max.y).to.not.be.NaN;
			for (var region in monitor.snappingRegions) {
				var bounds = monitor.snappingRegions[region];
				expect(bounds.min.x).to.not.be.NaN;
				expect(bounds.min.y).to.not.be.NaN;
				expect(bounds.max.x).to.not.be.NaN;
				expect(bounds.max.y).to.not.be.NaN;
			}
		});
		context('Helper functions', function () {
			it('should not be able to snap to the monitor', function () {
				//Left
				var newLeft = monitor.bounds.min.x + 51;
				var newTop = monitor.bounds.min.y + 50;
				var moveRequest = createMoveRequest({
					newLeft: newLeft,
					newTop: newTop,
					newBottom: newTop + 60,
					newRight: newLeft + movingWindow.width,
					changeType: 0,
					win: movingWindow
				});
				movingWindow.requestBoundsChange(moveRequest);
				expect(monitor.canSnapToWindow(movingWindow)).to.be.false;

				//Top
				var newLeft = monitor.bounds.min.x + 52;
				var newTop = monitor.bounds.min.y + 51;
				moveRequest = createMoveRequest({
					newLeft: newLeft,
					newTop: newTop,
					newBottom: newTop + 60,
					newRight: newLeft + movingWindow.width,
					changeType: 0,
					win: movingWindow
				});
				movingWindow.requestBoundsChange(moveRequest);
				expect(monitor.canSnapToWindow(movingWindow)).to.be.false;

				//Right
				var newLeft = monitor.bounds.max.x - movingWindow.width - 51;
				var newTop = monitor.bounds.min.y + 310;
				moveRequest = createMoveRequest({
					newLeft: newLeft,
					newTop: newTop,
					newBottom: newTop + 60,
					newRight: newLeft + movingWindow.width,
					changeType: 0,
					win: movingWindow
				});
				movingWindow.requestBoundsChange(moveRequest);
				expect(monitor.canSnapToWindow(movingWindow)).to.be.false;

				//Bottom
				var newLeft = monitor.bounds.min.x + 52;
				var newTop = monitor.bounds.max.y - movingWindow.height - 51;
				moveRequest = createMoveRequest({
					newLeft: newLeft,
					newTop: newTop,
					newBottom: newTop + 60,
					newRight: newLeft + movingWindow.width,
					changeType: 0,
					win: movingWindow
				});
				movingWindow.requestBoundsChange(moveRequest);
				expect(monitor.canSnapToWindow(movingWindow)).to.be.false;
			});
			it('should be able to snap to the monitor', function () {
				//Left
				var newLeft = monitor.bounds.min.x + 2;
				var newTop = monitor.bounds.min.y + 50;
				moveRequest = createMoveRequest({
					newLeft: newLeft,
					newTop: newTop,
					newBottom: newTop + 60,
					newRight: newLeft + movingWindow.width,
					changeType: 0,
					win: movingWindow
				});
				movingWindow.requestBoundsChange(moveRequest);
				expect(monitor.canSnapToWindow(movingWindow)).to.be.true;

				expect(movingWindow.left).to.equal(monitor.bounds.min.x);


				//Top
				var newLeft = monitor.bounds.min.x + 52;
				var newTop = monitor.bounds.min.y + 10;
				moveRequest = createMoveRequest({
					newLeft: newLeft,
					newTop: newTop,
					newBottom: newTop + 60,
					newRight: newLeft + movingWindow.width,
					changeType: 0,
					win: movingWindow
				});
				movingWindow.requestBoundsChange(moveRequest);
				expect(monitor.canSnapToWindow(movingWindow)).to.be.true;
				expect(movingWindow.top).to.equal(monitor.bounds.min.y);

				//Right
				newLeft = monitor.bounds.max.x - movingWindow.width - 8;
				newTop = monitor.bounds.min.y + 310;
				moveRequest = createMoveRequest({
					newLeft: newLeft,
					newTop: newTop,
					newBottom: newTop + 60,
					newRight: newLeft + movingWindow.width,
					changeType: 0,
					win: movingWindow
				});
				movingWindow.requestBoundsChange(moveRequest);
				expect(monitor.canSnapToWindow(movingWindow)).to.be.true;
				expect(movingWindow.right).to.equal(monitor.bounds.max.x);
				//Bottom
				newLeft = monitor.bounds.min.x + 52;
				newTop = monitor.bounds.max.y - movingWindow.height - 10;
				moveRequest = createMoveRequest({
					newLeft: newLeft,
					newTop: newTop,
					newBottom: newTop + 60,
					newRight: newLeft + movingWindow.width,
					changeType: 0,
					win: movingWindow
				});
				movingWindow.requestBoundsChange(moveRequest);
				expect(monitor.canSnapToWindow(movingWindow)).to.be.true;
				expect(movingWindow.bottom).to.equal(monitor.bounds.max.y);
			});
		});
	});
});


