it('should test my move Helper', function () {
	resetGrid();
	groupMode.enabled = false;
	DockingMath.setGroupMode(groupMode);
	var ABounds = A.getBounds();
	var newLeft = A.left - 31;
	moveWindow({
		handle: 'left',
		win: A,
		newPosition: {
			left: newLeft
		},
		type: 'resize'
	});
	expect(A.left).to.equal(newLeft);
});
// D G straddle E H. verticle snapping to D bottom doesnʼt happen if dragging E bottom.
it('should make sure snapping works in a grid', function () {
	groupMode.enabled = true;
	DockingMath.setGroupMode(groupMode);
	resetGrid();

	var edgeTests = [bufferSize - 1, bufferSize - 4, bufferSize - 10];
	var type = 'resize';
	//Test will spin through the left and right edges of A, adding the value of edgeTests (above). It will then compare the resulting edge to the comparions inside of the left/right arrays.
	var comparisons = {
		"A": {
			handle: {
				left: [D.left],
				right: [D.right, B.left, E.left]
			}
		},
		"B": {
			handle: {
				left: [E.left, A.right, D.right],
				right: [E.right, C.left, F.left]
			}
		},
		"C": {
			handle: {
				left: [E.right, B.right, E.right, F.left],
				right: [F.right]
			}
		},
		"D": {
			handle: {
				left: [A.left, G.left],
				right: [A.right, B.left, E.left, G.right, H.left]
			}
		},
		"E": {
			handle: {
				left: [H.left, A.right, D.right, G.right],
				right: [H.right, C.left, F.left]
			}
		},
		"F": {
			handle: {
				left: [E.right, B.right, C.left, H.right, I.left],
				right: [C.right, I.right]
			}
		},
		"G": {
			handle: {
				left: [A.left, D.left],
				right: [A.right, D.right, B.left, E.left, H.left]
			}
		},
		"H": {
			handle: {
				left: [G.right, A.right, D.right],
				right: [I.left, E.right, C.left, F.left]
			}
		},
		"I": {
			handle: {
				left: [E.right, B.right, C.left, F.left, H.right],
				right: [C.right, F.right]
			}
		}
	};
	groupMode.enabled = false;
	runThroughComparisons(groupMode);
	//try with groupMode on.
	groupMode.enabled = true;
	runThroughComparisons(groupMode);

	function runThroughComparisons(groupMode) {
		resetGrid();
		DockingMath.setGroupMode(groupMode);

		for (var windowName in comparisons) {
			var win = DockingMath.getWindow(windowName);
			if (groupMode) {
				var group = DockingMath.getMovingGroup(win);

			}
			for (var handle in comparisons[windowName]['handle']) {
				//skip exterior edges.
				if (groupMode.enabled && win[handle] === group[handle]) {
					continue;
				}
				var comparisonEdges = comparisons[windowName]['handle'][handle];
				for (var i = 0; i < edgeTests.length; i++) {
					var destination = win[handle] + edgeTests[i];
					var newPosition = {};
					newPosition[handle] = destination;

					moveWindow({
						handle: handle,
						win: win,
						newPosition: newPosition,
						type: type
					});
					comparisonEdges.forEach((comparison, c) => {
						try {
							expect(win[handle]).to.equal(comparison);
						} catch (e) {
							console.log('HANDLE', handle, destination, c, win.name);
							console.log('GROUP MODE', groupMode);
							throw new Error(e);
						}
					});
				}
			}
		}
	}

});


