context('Top Edge', function () {
	it('should snap to the top of the stationary window when coming from above', function () {
		var newTop = stationaryWindow.top - movingWindow.height - 5;
		var moveRequest = createMoveRequest({
			newLeft: stationaryWindow.left + 40,
			newTop: newTop,
			newBottom: newTop + movingWindow.height,
			newRight: movingWindow.right,
			changeType: 0,
			win: movingWindow
		});

		movingWindow.requestBoundsChange(moveRequest);
		expect(movingWindow.bottom).to.equal(stationaryWindow.top);

	});

	context('TopLeft', function () {
		it('should snap to the top-left corner of the stationary window', function () {
			var newTop = stationaryWindow.top - movingWindow.height - 5;
			var newLeft = stationaryWindow.left - 10;
			var moveRequest = createMoveRequest({
				newLeft: newLeft,
				newTop: newTop,
				newBottom: newTop + movingWindow.height,
				newRight: newLeft + movingWindow.width,
				changeType: 0,
				win: movingWindow
			});
			movingWindow.requestBoundsChange(moveRequest);
			//DockingMath.onWindowMove(movingWindow);
			expect(movingWindow.bottom).to.equal(stationaryWindow.top);
			expect(movingWindow.left).to.equal(stationaryWindow.left);
		});
	});
	context('TopRight', function () {
		it('should snap to the top-right corner of the stationary window', function () {
			var newTop = stationaryWindow.top - movingWindow.height - 10;
			var newLeft = stationaryWindow.right - movingWindow.width + 5;
			var moveRequest = createMoveRequest({
				newLeft: newLeft,
				newTop: newTop,
				newRight: newLeft + movingWindow.width,
				newBottom: newTop + movingWindow.height,
				changeType: 0,
				win: movingWindow
			});
			movingWindow.requestBoundsChange(moveRequest);

			expect(movingWindow.bottom).to.equal(stationaryWindow.top);
			expect(movingWindow.right).to.equal(stationaryWindow.right);
		});
	});

	it('should traverse the entirety of the top edge from right to left, staying snapped', function () {
		var newTop = stationaryWindow.top - movingWindow.height - 10;
		cb = noop;

		for (var newLeft = stationaryWindow.right - 7; newLeft > stationaryWindow.left; newLeft--) {

			var moveRequest = createMoveRequest({
				newLeft: newLeft,
				newTop: newTop,
				newBottom: newTop + movingWindow.height,
				newRight: newLeft + movingWindow.width,
				changeType: 0,
				win: movingWindow
			});
			if (newLeft === stationaryWindow.left + 1) {
				cb = undefined;
			}
			movingWindow.requestBoundsChange(moveRequest, cb);
			//DockingMath.onWindowMove(movingWindow);
			expect(movingWindow.bottom).to.equal(stationaryWindow.top);
		}
	});
});


