
context('Left Edge', function () {
	it('should snap to the left edge of the stationary window, coming rom the left.', function () {
		var newLeft = stationaryWindow.left - movingWindow.width - 10;
		var newTop = stationaryWindow.top;
		var moveRequest = createMoveRequest({
			newLeft: newLeft,
			newTop: newTop,
			newBottom: newTop + movingWindow.height,
			newRight: newLeft + movingWindow.width,
			changeType: 0,
			win: movingWindow
		});
		movingWindow.requestBoundsChange(moveRequest);
		expect(movingWindow.right).to.equal(stationaryWindow.left);
	});
	it('should traverse the entirety of the left edge from top to bottom, staying snapped', function () {
		var newLeft = stationaryWindow.left - movingWindow.width;
		for (var newTop = stationaryWindow.bottom - 10; newTop < stationaryWindow.bottom; newTop++) {

			var moveRequest = createMoveRequest({
				newLeft: newLeft,
				newTop: newTop,
				newBottom: newTop + movingWindow.height,
				newRight: newLeft + movingWindow.width,
				changeType: 0,
				win: movingWindow
			});
			movingWindow.requestBoundsChange(moveRequest);
			expect(movingWindow.right).to.equal(stationaryWindow.left);
		}
	});
	context('leftTop', function () {
		it('should align its top edge with the stationary window\'s top edge', function () {
			//right edge aligned with left edge, moving window's topRight and stationary window's topLeft.
			var newLeft = stationaryWindow.left - movingWindow.width - 10;
			var newTop = stationaryWindow.top + 10;
			var moveRequest = createMoveRequest({
				newLeft: newLeft,
				newTop: newTop,
				newBottom: newTop + movingWindow.height,
				newRight: newLeft + movingWindow.width,
				changeType: 0,
				win: movingWindow
			});
			movingWindow.requestBoundsChange(moveRequest);
			expect(movingWindow.top).to.equal(stationaryWindow.top);
			expect(movingWindow.right).to.equal(stationaryWindow.left);
		});
	});
	context('leftBottom', function () {
		it('should align its bottom right edge with the stationary window\'s bottom edge', function () {
			var newLeft = stationaryWindow.left - movingWindow.width - 10;
			var newTop = stationaryWindow.bottom - movingWindow.height - 10;
			var moveRequest = createMoveRequest({
				newLeft: newLeft,
				newTop: newTop,
				newBottom: newTop + movingWindow.height,
				newRight: newLeft + movingWindow.width,
				changeType: 0,
				win: movingWindow
			});
			movingWindow.requestBoundsChange(moveRequest);

			expect(movingWindow.right).to.equal(stationaryWindow.left);
			expect(movingWindow.bottom).to.equal(stationaryWindow.bottom);
		});
	});
});


