var chai = require('chai');
var assert = chai.assert;
var should = chai.should;
var expect = chai.expect;

var LinkerClient = FSBL.Clients.LinkerClient;
var WindowClient = FSBL.Clients.WindowClient;


describe('LinkerClient', function () {
	var methods = getAllMethods(LinkerClient);
	var baseMethods = getAllMethods(new FSBL.Clients.BaseClient());

	var testMethods = methods.filter(function (item) {
		return !baseMethods.includes(item)
	})

	var untestedMethods = {};
	for (var i = 0; i < testMethods.length; i++) {
		untestedMethods[testMethods[i]] = true;
	}

	function testThat(method, test, fn) {
		it(test, fn)
		if (untestedMethods[method]) delete untestedMethods[method];
	}
	context('Managing Groups', function () {
		testThat('createGroup', 'should create a group', function (done) {
			LinkerClient.createGroup({ name: 'newGroup' }, function (err, groups) {
				var numG = 0;
				for (var i = 0; i < groups.length; i++) {
					var g = groups[i]
					if (g.name == 'newGroup') numG++;
				}
				expect(numG).to.equal(1)
				done()
			})
		})

		testThat('updateGroup', 'should update a group', function (done) {
			LinkerClient.updateGroup('newGroup', { name: 'newGroup2' }, function (err, groups) {
				var numG = 0;
				for (var i = 0; i < groups.length; i++) {
					var g = groups[i]
					if (g.name == 'newGroup2') numG++;
				}
				expect(numG).to.equal(1)
				done()
			})
		})

		testThat('deleteGroup', 'should delete a group', function (done) {
			LinkerClient.deleteGroup('newGroup2', function (err, groups) {
				var numG = 0;
				for (var i = 0; i < groups.length; i++) {
					var g = groups[i]
					if (g.name == 'newGroup2') numG++;
				}
				expect(numG).to.equal(0)
				done()
			})
		})

	})

	context('Managing Window Grouping', function () {
		testThat('addToGroup', 'should add this window to a group', function (done) {
			LinkerClient.addToGroup('group1', null, function (err, groups) {
				expect(groups.length).to.equal(1);
				expect(groups.filter(function(g) { return g.name == 'group1'}).length).to.equal(1);
				LinkerClient.addToGroup('group2', null, function (err, groups) {
					expect(groups.length).to.equal(2);
					expect(groups.filter(function (g) { return g.name == 'group1' }).length).to.equal(1);
					expect(groups.filter(function (g) { return g.name == 'group2' }).length).to.equal(1);
					done();
				})
			})
		})

		testThat('removeFromGroup', 'should remove this window from the group', function (done) {
			LinkerClient.removeFromGroup('group1', null, function (err, groups) {
				expect(groups.length).to.equal(1);
				expect(groups.filter(function (g) { return g.name == 'group1' }).length).to.equal(0);
				expect(groups.filter(function (g) { return g.name == 'group2' }).length).to.equal(1);
				LinkerClient.removeFromGroup('group2', null, function () { //restoring state back so tests dont fail on second run
					done();	
				})
				

			})
		})
	})

	context.skip('Completion', function () {
		//This needs to be considered in the context of an overarching code-coverage tool. This causes failures that aren't _really_ failures. We need to look at this eventually, but this isn't the solution. -- Brad
		it('should check all functions', function (done) {
			expect(untestedMethods).to.be.empty;
		});
	});

});



