context('Stationary Left Edge', function () {
	it('Should wiggle around inside of the top snapping region of the stationary window while maintaining its width', function () {
		var newLeft = stationaryWindow.right;
		var newTop = stationaryWindow.top;
		var moveRequest = createMoveRequest({
			newLeft: newLeft,
			newTop: newTop,
			newBottom: newTop + movingWindow.height,
			newRight: newLeft + movingWindow.width,
			changeType: 0,
			win: movingWindow
		});
		movingWindow.requestBoundsChange(moveRequest);

		var oldRight = movingWindow.right;
		var oldLeft = movingWindow.left;
		var oldWidth = movingWindow.width;
		var initialHeight = stationaryWindow.height + 60;

		function traverseRegion() {
			var initialTop = movingWindow.top;
			var initialBottom = movingWindow.bottom;
			function wobble(n) {
				if (n > 0) {
					n = 0 - n;
				} else if (n < 0) {
					n = Math.abs(n) + 1;
				}

				return n;
			}
			for (var i = 1; initialTop + i < stationaryWindow.top + 15; i = wobble(i)) {
				var moveRequest = createMoveRequest({
					newLeft: stationaryWindow.right,
					newRight: movingWindow.right,
					newTop: movingWindow.bottom - initialTop + i,
					newBottom: movingWindow.bottom,
					win: movingWindow,
					changeType: 1
				});
				if (initialTop + i === stationaryWindow.top - 14) {
					cb = undefined;
				} else {
					cb = noop;
				}
				movingWindow.requestBoundsChange(moveRequest, cb);
				
				expect(oldRight).to.equal(movingWindow.right);
				expect(movingWindow.left).to.equal(stationaryWindow.right);
				expect(movingWindow.bottom).to.equal(initialBottom);
				if (movingWindow.top === stationaryWindow.top) {
					expect(movingWindow.height).to.equal(initialHeight);
				}
			}
		}

		traverseRegion();

	});
	it('Should snap to the left edge of the stationary window', function () {
		var newLeft = stationaryWindow.left - movingWindow.width - 10;
		var newTop = stationaryWindow.top + 40;
		var moveRequest = createMoveRequest({
			newLeft: newLeft,
			newTop: newTop,
			newBottom: newTop + movingWindow.height,
			newRight: newLeft + movingWindow.width,
			changeType: 0,
			win: movingWindow
		});

		//Setup. Next move will resize.
		movingWindow.requestBoundsChange(moveRequest);
		var oldRight = movingWindow.right;
		var oldLeft = movingWindow.left;
		var moveRequest = createMoveRequest({
			newLeft: movingWindow.left,
			newRight: stationaryWindow.left - 14,
			newTop: movingWindow.top,
			newBottom: movingWindow.bottom,
			win: movingWindow,
			changeType: 1
		});
		movingWindow.requestBoundsChange(moveRequest);

		expect(oldLeft).to.equal(movingWindow.left);
		expect(movingWindow.right).to.equal(stationaryWindow.left);
	});
	it('While snapped to the left edge of the window, it should snap to the top on resize', function () {
		var newLeft = stationaryWindow.left - movingWindow.width - 10;
		var newTop = stationaryWindow.top + 40;
		var moveRequest = createMoveRequest({
			newLeft: newLeft,
			newTop: newTop,
			newBottom: newTop + movingWindow.height,
			newRight: newLeft + movingWindow.width,
			changeType: 0,
			win: movingWindow
		});
		movingWindow.requestBoundsChange(moveRequest);

		var oldBottom = movingWindow.bottom;
		var oldRight = movingWindow.right;
		var oldLeft = movingWindow.left;
		var moveRequest = createMoveRequest({
			newLeft: movingWindow.left,
			newRight: movingWindow.right,
			newTop: stationaryWindow.top + 14,
			newBottom: movingWindow.bottom,
			win: movingWindow,
			changeType: 1
		});
		movingWindow.requestBoundsChange(moveRequest);

		expect(oldLeft).to.equal(movingWindow.left);
		expect(oldBottom).to.equal(movingWindow.bottom);
		expect(movingWindow.right).to.equal(stationaryWindow.left);
		expect(movingWindow.top).to.equal(stationaryWindow.top);

	});
	it('should resize past the top edge of the stationary window', function () {
		var newLeft = stationaryWindow.left - movingWindow.width - 10;
		var newTop = stationaryWindow.bottom - movingWindow.height - 40;
		var moveRequest = createMoveRequest({
			newLeft: newLeft,
			newTop: newTop,
			newBottom: newTop + movingWindow.height,
			newRight: newLeft + movingWindow.width,
			changeType: 0,
			win: movingWindow
		});
		movingWindow.requestBoundsChange(moveRequest);

		var oldTop = movingWindow.top;
		var oldRight = movingWindow.right;
		var oldLeft = movingWindow.left;

		newTop = stationaryWindow.top - 16;
		moveRequest = createMoveRequest({
			newLeft: movingWindow.left,
			//14 px left of the stationary window
			newRight: movingWindow.left + movingWindow.width,
			newTop: newTop,
			//3 px below the top edge of the window.
			newBottom: newTop + movingWindow.height,
			win: movingWindow,
			changeType: 1
		});

		movingWindow.requestBoundsChange(moveRequest);

		expect(oldLeft).to.equal(movingWindow.left);
		expect(movingWindow.right).to.equal(stationaryWindow.left);
		expect(movingWindow.top).to.equal(stationaryWindow.top - 16);
	});
	it('While snapped to the left edge of the window, it should snap to the bottom on resize', function () {
		var newLeft = stationaryWindow.left - movingWindow.width - 10;
		var newTop = stationaryWindow.bottom - movingWindow.height - 40;
		var moveRequest = createMoveRequest({
			newLeft: newLeft,
			newTop: newTop,
			newBottom: newTop + movingWindow.height,
			newRight: newLeft + movingWindow.width,
			changeType: 0,
			win: movingWindow
		});
		movingWindow.requestBoundsChange(moveRequest);

		var oldBottom = movingWindow.bottom;
		var oldRight = movingWindow.right;
		var oldLeft = movingWindow.left;

		moveRequest = createMoveRequest({
			newLeft: movingWindow.left,
			newRight: movingWindow.right,
			newTop: movingWindow.top,
			newBottom: stationaryWindow.bottom - 13,
			win: movingWindow,
			changeType: 1
		});
		movingWindow.requestBoundsChange(moveRequest);

		expect(oldLeft).to.equal(movingWindow.left);
		expect(movingWindow.right).to.equal(stationaryWindow.left);
	});

	it('should resize past the bottom edge of the stationary window', function () {
		var newLeft = stationaryWindow.left - movingWindow.width - 10;
		var newTop = stationaryWindow.bottom - movingWindow.height - 40;
		var moveRequest = createMoveRequest({
			newLeft: newLeft,
			newTop: newTop,
			newBottom: newTop + movingWindow.height,
			newRight: newLeft + movingWindow.width,
			changeType: 0,
			win: movingWindow
		});
		movingWindow.requestBoundsChange(moveRequest);

		var oldTop = movingWindow.top;
		var oldRight = movingWindow.right;
		var oldLeft = movingWindow.left;

		moveRequest = createMoveRequest({
			newLeft: movingWindow.left,
			//14 px left of the stationary window
			newRight: movingWindow.right,
			newTop: movingWindow.top,
			//3 px below the top edge of the window.
			newBottom: stationaryWindow.bottom + 40,
			win: movingWindow,
			changeType: 1
		});
		movingWindow.requestBoundsChange(moveRequest);

		expect(oldLeft).to.equal(movingWindow.left);
		expect(oldTop).to.equal(movingWindow.top);
		expect(movingWindow.bottom).to.equal(stationaryWindow.bottom + 40);
		expect(movingWindow.right).to.equal(stationaryWindow.left);
	});

	it('should snap its bottom and right edges to the top left of the stationary window', function () {
		var newLeft = stationaryWindow.left - movingWindow.width - 14;
		var newTop = stationaryWindow.top - movingWindow.height - 10;
		var moveRequest = createMoveRequest({
			newLeft: newLeft,
			newTop: newTop,
			newBottom: newTop + movingWindow.height,
			newRight: newLeft + movingWindow.width,
			changeType: 0,
			win: movingWindow
		});
		movingWindow.requestBoundsChange(moveRequest);

		var oldTop = movingWindow.top;
		var oldRight = movingWindow.right;
		var oldLeft = movingWindow.left;

		moveRequest = createMoveRequest({
			newLeft: movingWindow.left,
			//14 px left of the stationary window
			newRight: movingWindow.right,
			newTop: movingWindow.top,
			//3 px below the top edge of the window.
			newBottom: stationaryWindow.top - 3,
			win: movingWindow,
			changeType: 1
		});
		movingWindow.requestBoundsChange(moveRequest);
		expect(oldLeft).to.equal(movingWindow.left);
		expect(oldTop).to.equal(movingWindow.top);

		expect(movingWindow.bottom).to.equal(stationaryWindow.top);
		expect(movingWindow.right).to.equal(stationaryWindow.left);
	});
});


