
context('Bottom Edge', function () {
	/**
 *  +---------------+
	|               |
	|               |
	|  Stationary   |
	|               |
	|               |
	|               |
	+---------+-----+
			  ^
			  |
	   +------+---------+
	   |                |
	   |                |
	   |                |
	   |     Moving     |
	   |                |
	   |                |
	   +----------------+
 */
	it('should snap to the bottom edge of the stationary window, coming from the bottom.', function () {
		var newLeft = stationaryWindow.right - 30;
		var newTop = stationaryWindow.bottom + 10;
		var moveRequest = createMoveRequest({
			newLeft: newLeft,
			newTop: newTop,
			newBottom: newTop + movingWindow.height,
			newRight: newLeft + movingWindow.width,
			changeType: 0,
			win: movingWindow
		});
		movingWindow.requestBoundsChange(moveRequest);
		expect(movingWindow.top).to.equal(stationaryWindow.bottom);
	});
	it('should traverse the entirety of the bottom edge from left to right, staying snapped', function () {
		var newTop = stationaryWindow.bottom + 10;
		var cb = noop;
		for (var newLeft = stationaryWindow.right; newLeft > stationaryWindow.left; newLeft--) {
			var moveRequest = createMoveRequest({
				newLeft: newLeft,
				newTop: newTop,
				newBottom: newTop + movingWindow.height,
				newRight: newLeft + movingWindow.width,
				changeType: 0,
				win: movingWindow
			});
			if (newLeft === stationaryWindow.left + 1) {
				cb = undefined;
			}
			movingWindow.requestBoundsChange(moveRequest,cb);
			expect(movingWindow.top).to.equal(stationaryWindow.bottom);
			if (Math.abs(stationaryWindow.left - movingWindow.left) <= 14) {
				expect(movingWindow.left).to.equal(stationaryWindow.left);
			}
		}
	});
	context('bottomLeft', function () {
		it('should snap its left edge to the bottom left of the stationary window.', function () {
			var newLeft = stationaryWindow.left + 7;
			var newTop = stationaryWindow.bottom + 10;
			var moveRequest = createMoveRequest({
				newLeft: newLeft,
				newTop: newTop,
				newBottom: newTop + movingWindow.height,
				newRight: newLeft + movingWindow.width,
				changeType: 0,
				win: movingWindow
			});
			movingWindow.requestBoundsChange(moveRequest);
			expect(movingWindow.top).to.equal(stationaryWindow.bottom);
			expect(movingWindow.left).to.equal(stationaryWindow.left);
		});

		it('should snap its right edge to the bottom left of the stationary window.', function () {
			var newLeft = stationaryWindow.left - movingWindow.width + 7;
			var newTop = stationaryWindow.bottom + 10;
			var moveRequest = createMoveRequest({
				newLeft: newLeft,
				newTop: newTop,
				newBottom: newTop + movingWindow.height,
				newRight: newLeft + movingWindow.width,
				changeType: 0,
				win: movingWindow
			});
			movingWindow.requestBoundsChange(moveRequest);
			expect(movingWindow.top).to.equal(stationaryWindow.bottom);
			expect(movingWindow.right).to.equal(stationaryWindow.left);
		});
	});

	context('bottomRight', function () {
		it('should snap its left edge to the bottom right of the stationary window.', function () {
			var newLeft = stationaryWindow.right - 7;
			var newTop = stationaryWindow.bottom + 10;
			var moveRequest = createMoveRequest({
				newLeft: newLeft,
				newTop: newTop,
				newBottom: newTop + movingWindow.height,
				newRight: newLeft + movingWindow.width,
				changeType: 0,
				win: movingWindow
			});
			movingWindow.requestBoundsChange(moveRequest);
			expect(movingWindow.top).to.equal(stationaryWindow.bottom);
			expect(movingWindow.left).to.equal(stationaryWindow.right);
		});

		it('should snap its right edge to the bottom right of the stationary window.', function () {
			var newLeft = stationaryWindow.right - 7;
			var newTop = stationaryWindow.bottom + 10;
			var moveRequest = createMoveRequest({
				newLeft: newLeft,
				newTop: newTop,
				newBottom: newTop + movingWindow.height,
				newRight: newLeft + movingWindow.width,
				changeType: 0,
				win: movingWindow
			});
			movingWindow.requestBoundsChange(moveRequest);

			//DockingMath.onWindowMove(movingWindow);
			newLeft = stationaryWindow.right - movingWindow.width - 12;
			newTop = movingWindow.top;
			moveRequest = createMoveRequest({
				newLeft: newLeft,
				newTop: newTop,
				newBottom: newTop + movingWindow.height,
				newRight: newLeft + movingWindow.width,
				changeType: 0,
				win: movingWindow
			});
			movingWindow.requestBoundsChange(moveRequest);
			expect(movingWindow.top).to.equal(stationaryWindow.bottom);
			expect(movingWindow.right).to.equal(stationaryWindow.right);
		});
	});

});


