require('./DockingSuite/dockingCommon');
describe('Docking', function () {
	var windowIterator = function* () {
		var windowList = DockingMath.getWindows();
		for (var windowName in windowList) {
			yield windowList[windowName];
		}
	};
	before(function () {
		require('./DockingSuite/initialSetup');
	});
	context('Basic Moving and Resizing without snapping.', function () {
		require('./DockingSuite/basicMoveAndResize');
	});

	context('BoxMath Helper functions', function () {
		require('./DockingSuite/boxMath/helpers');
	});

	context('Snapping with 1 stationary window', function () {

		require('./DockingSuite/snapping/moving/topEdge');
		require('./DockingSuite/snapping/moving/rightEdge');
		require('./DockingSuite/snapping/moving/bottomEdge');
		require('./DockingSuite/snapping/moving/leftEdge');

	});

	context('Resize snapping', function () {
		require('./DockingSuite/snapping/resizing/leftEdge');
		require('./DockingSuite/snapping/resizing/rightEdge');
	});

	context('Snapping Multiple windows', function () {
		require('./DockingSuite/snapping/snappingMultipleWindows');
	});
	context('Snapping to monitors', function () {
		require('./DockingSuite/snapping/monitors/monitorSnapping');
	});

	context('Docking/Groups', function () {

		context('Group Mode', function () {
			require('./DockingSuite/docking/groupMode');
		});
		context('Helpers', function () {
			require('./DockingSuite/docking/helpers');
		});
		context('Exterior edge resizing', function () {
			require('./DockingSuite/docking/exteriorEdges');
		});

		context('Exterior corner resizing', function () {
			require('./DockingSuite/docking/exteriorCorners');
		});

		context('Interior edge resizing', function () {
			require('./DockingSuite/docking/interiorEdges');
		});

		context('Interior corner resizing', function () {
			require('./DockingSuite/docking/interiorCorners');
		});
		context('Grid snapping', function () {
			require('./DockingSuite/docking/gridSnapping');
			require('./DockingSuite/docking/shortCircuitResizing');
		});
		afterEach(function () {
			expect(movingWindow.height).to.be.greaterThan(0);
			expect(movingWindow.width).to.be.greaterThan(0);
		});
	});
	afterEach(function () {
		var iter = windowIterator();
		for (var win of iter) {
			expect(win.opacity).to.equal(1);
		}
	});
});





