var React = require("react");
var ReactDOM = require("react-dom");
var TestRunnerStore;
var TestRunnerActions;
import SuiteList from './components/SuiteList';
import TestItem from './components/TestItem';
import Header from './components/Header';
import 'react-select/dist/react-select.css';
import '../TestRunner.css';

//every X ms during the test execution, we look for the last test on the page, and scroll there. This keeps the tests continually flowing to the bottom.
const SCROLL_BOTTOM_INTERVAL_DURATION = 300;
function MyReporter(runner) {
	Mocha.reporters.Base.call(this, runner);
	var passes = 0;
	var failures = 0;
	var level = 0;
	var stats = this.stats;
	runner.on('test', function (test) {
		TestRunnerActions.setActiveTest(test);
	});

	runner.on('test end', function (test) {
		TestRunnerActions.updateTest(test);
	});

	runner.on('suite', function (suite) {
		// TestRunnerActions.updateSuite(suite);
	});

	runner.on('start', function (suite) {
		TestRunnerActions.startSuite();
		this.suite.suites.forEach((suite) => {

			TestRunnerActions.addSuite(suite);
		});
		updateStats();
	});
	runner.on('pending', function (test) {
		updateStats();
	});
	runner.on('pass', function (test) {
		passes++;
		// console.log(`PASS: ${test.fullTitle()}`);
		updateStats();
	});

	runner.on('fail', function (test, err) {
		failures++;
		// console.log('fail: %s -- error: %s', test.fullTitle(), err.message);
		updateStats();
	});

	runner.on('end', function () {
		console.log('end: %d/%d', passes, passes + failures);
		TestRunnerActions.endSuite();
	});
	function updateStats() {
		stats.total = runner.total;

		// TODO: add to stats
		var percent = stats.tests / runner.total * 100 | 0;
		// if (progress) {
		// 	progress.update(percent).draw(ctx);
		// }

		// update stats
		var ms = new Date() - stats.start;
		stats.duration = (ms / 1000).toFixed(2);
		TestRunnerActions.updateStats(stats);
	}
}

mocha.setup({
	timeout: 2500,
	ui: 'bdd',
	bail: false,
	reporter: MyReporter
});
var Tests = require('./Tests');

class TestRunner extends React.Component {
	constructor() {
		super();
		this.state = {
			testList: TestRunnerActions.getTestList(),
			qs: false,
			filter: TestRunnerStore.filter
		};
	}
	componentDidMount() {
		mocha.globals(['FSBL']);
		TestRunnerActions.handleQueryString();
		TestRunnerStore.addListener('change:filter', () => {
			console.log('SETTING FILTER', TestRunnerStore.filter);
			this.setState({
				filter: TestRunnerStore.filter
			})
		});
	}
	componentWillMount() {
		var self = this;
		TestRunnerStore.addListener('change:tests', () => {
			this.setState({
				testList: TestRunnerActions.getTestList()
			});
		});
	}
	render() {
		var self = this;
		console.log('RENDINGER ROOT', self.state.filter);
		var testList = self.state.testList.map((item, i) => {
			if (item instanceof Mocha.Suite) {
				return <SuiteList qs={self.state.qs} key={i} filter={self.state.filter} suites={[item]} />
			} else {
				return <TestItem qs={self.state.qs} filter={self.state.filter} test={item} />
			}
		})
		return (<div ref="node">
			<Header/>
			<div className="TestSuite">
				{testList}
			</div>
		</div>)
	}
};
//for debugging.
window.TestRunnerStore = TestRunnerStore;
require('../../../samples/src/assets/css/finfont.css');
require('../../../samples/src/assets/css/finsemble.scss');

FSBL.addEventListener('onReady', function () {
	TestRunnerStore = require("./stores/TestRunnerStore").Store;
	TestRunnerActions = require("./stores/TestRunnerStore").Actions;

	//render component when FSBL is ready.
	ReactDOM.render(
		<TestRunner />
		, document.getElementById('TestRunner-component-wrapper'));
});



