it('Should move a window', function (done) {
	var newBounds = {
		left: 200,
		top: 200,
		right: 200 + movingWindow.width,
		bottom: 200 + movingWindow.height,
		width: movingWindow.width,
		height: movingWindow.height
	};
	movingWindow.setBounds(newBounds);
	var bounds = movingWindow.getBounds();
	expect(bounds.left).to.equal(200);
	expect(bounds.top).to.equal(200);
	expect(bounds.width).to.equal(movingWindow.width);
	done();
});

it('should resize the window', function () {
	var newHeight = movingWindow.height + 10;
	var newWidth = movingWindow.width + 20;
	var newBounds = {
		left: 200,
		top: 200,
		right: 200 + movingWindow.width,
		bottom: 200 + movingWindow.height,
		width: newWidth,
		height: newHeight
	};
	movingWindow.setBounds(newBounds);
	expect(movingWindow.height).to.equal(newHeight);
	expect(movingWindow.width).to.equal(newWidth);
});

it('should move its right edge just outside of the top edge of the stationary window', function () {
	var newLeft = stationaryWindow.left + bufferSize + 1;
	var newTop = stationaryWindow.top + bufferSize + 1;
	var moveRequest = createMoveRequest({
		newLeft: newLeft,
		newTop: newTop,
		newBottom: newTop + movingWindow.height,
		newRight: newLeft + movingWindow.width,
		changeType: 0,
		win: movingWindow
	});
	movingWindow.requestBoundsChange(moveRequest);
	expect(movingWindow.left).to.equal(stationaryWindow.left + bufferSize + 1);
	//DockingMath.onWindowMove(movingWindow);

});


it('should move its left edge just outside of the right edge of the stationary window and not snap', function () {

	var newLeft = stationaryWindow.right + bufferSize + 1;
	var newTop = stationaryWindow.top + bufferSize + 1;
	var moveRequest = createMoveRequest({
		newLeft: newLeft,
		newTop: newTop,
		newBottom: newTop + movingWindow.height,
		newRight: newLeft + movingWindow.width,
		changeType: 0,
		win: movingWindow
	});
	movingWindow.requestBoundsChange(moveRequest);
	expect(movingWindow.left).to.equal(stationaryWindow.right + bufferSize + 1);
});

it('should move its top edge just outside of the bottom edge of the stationary window', function () {

	var newLeft = stationaryWindow.left + bufferSize + 1;
	var newTop = stationaryWindow.top + bufferSize + 1;
	var moveRequest = createMoveRequest({
		newLeft: newLeft,
		newTop: newTop,
		newBottom: newTop + movingWindow.height,
		newRight: newLeft + movingWindow.width,
		changeType: 0,
		win: movingWindow
	});
	movingWindow.requestBoundsChange(moveRequest);
	expect(movingWindow.left).to.equal(stationaryWindow.left + bufferSize + 1);
});

it('should move its right edge just outside of the left edge of the stationary window', function () {
	var newLeft = stationaryWindow.left - movingWindow.width - bufferSize - 1;
	var moveRequest = createMoveRequest({
		newLeft: newLeft,
		newTop: stationaryWindow.top + bufferSize + 1,
		newBottom: movingWindow.bottom,
		newRight: movingWindow.right,
		changeType: 0,
		win: movingWindow
	});
	movingWindow.requestBoundsChange(moveRequest);
	expect(movingWindow.left).to.equal(newLeft);
});


