
context('Right Edge', function () {
	it('should snap to the right edge of the stationary window, coming from the right.', function () {
		var newLeft = stationaryWindow.right + 10;
		var newTop = stationaryWindow.top;
		var moveRequest = createMoveRequest({
			newLeft: newLeft,
			newTop: newTop,
			newBottom: newTop + movingWindow.height,
			newRight: newLeft + movingWindow.width,
			changeType: 0,
			win: movingWindow
		});
		movingWindow.requestBoundsChange(moveRequest);
		//DockingMath.onWindowMove(movingWindow);
		expect(movingWindow.left).to.equal(stationaryWindow.right);
	});

	context('rightTop', function () {
		it('should snap to the top right corner of the stationary window', function () {
			var newLeft = stationaryWindow.right + 10;
			var newTop = stationaryWindow.top + 7;
			var moveRequest = createMoveRequest({
				newLeft: newLeft,
				newTop: newTop,
				newBottom: newTop + movingWindow.height,
				newRight: newLeft + movingWindow.width,
				changeType: 0,
				win: movingWindow
			});
			movingWindow.requestBoundsChange(moveRequest);
			expect(movingWindow.left).to.equal(stationaryWindow.right);
			expect(movingWindow.top).to.equal(stationaryWindow.top);
		});

	});
	context('rightBottom', function () {
		//stationary rightBottom, moving leftBottom
		it('should snap to the bottom right corner of the stationary window', function () {
			var newLeft = stationaryWindow.right + 10;
			var newTop = stationaryWindow.bottom - movingWindow.height - 1;
			var moveRequest = createMoveRequest({
				newLeft: newLeft,
				newTop: newTop,
				newBottom: newTop + movingWindow.height,
				newRight: newLeft + movingWindow.width,
				changeType: 0,
				win: movingWindow
			});
			movingWindow.requestBoundsChange(moveRequest);
			expect(movingWindow.left).to.equal(stationaryWindow.right);
			expect(movingWindow.bottom).to.equal(stationaryWindow.bottom);
		});
		it('should snap to the right edge of the stationary window', function () {
			var newLeft = stationaryWindow.right + 12;
			var newTop = stationaryWindow.bottom - movingWindow.height - 1;
			var moveRequest = createMoveRequest({
				newLeft: newLeft,
				newTop: newTop,
				newBottom: newTop + movingWindow.height,
				newRight: newLeft + movingWindow.width,
				changeType: 0,
				win: movingWindow
			});
			movingWindow.requestBoundsChange(moveRequest);

			expect(movingWindow.left).to.equal(stationaryWindow.right);
		});
	});

	it('should traverse the entirety of the right edge from top to bottom, staying snapped', function () {
		var newLeft = stationaryWindow.right + 10;
		var newTop = stationaryWindow.bottom + 10;
		var moveRequest = createMoveRequest({
			newLeft: newLeft,
			newTop: newTop,
			newBottom: newTop + movingWindow.height,
			newRight: newLeft + movingWindow.width,
			changeType: 0,
			win: movingWindow
		});
		movingWindow.requestBoundsChange(moveRequest);

		for (var adjustedTop = movingWindow.top; adjustedTop < stationaryWindow.top; adjustedTop++) {
			moveRequest = createMoveRequest({
				newLeft: newLeft,
				newTop: adjustedTop,
				newBottom: adjustedTop + movingWindow.height,
				newRight: newLeft + movingWindow.width,
				changeType: 0,
				win: movingWindow
			});
			movingWindow.requestBoundsChange(moveRequest);
			//DockingMath.onWindowMove(movingWindow);
			expect(movingWindow.left).to.equal(stationaryWindow.right);

			//In the bottom snapping region
			if (Math.abs(stationaryWindow.bottom - movingWindow.bottom) <= 14) {
				expect(movingWindow.bottom).to.equal(stationaryWindow.bottom);
			}

			//In the top snapping region
			if (Math.abs(stationaryWindow.top - movingWindow.top) <= 14) {
				expect(movingWindow.top).to.equal(stationaryWindow.top);
			}
		}
	});
});


