var chai = require('chai');
var assert = chai.assert;
var should = chai.should;
var expect = chai.expect;
var RouterClient = FSBL.Clients.RouterClient;
var Helpers = require('./helpers');
describe('File Menu', function () {
    it('should open a documentation page', function (done) {
        this.timeout(5000);
        console.log('opening')
        RouterClient.query('TestRunner.FileMenu', {
            test: 'openDocumentation'
        }, function () {
            let finWindow = null;
            Helpers.getWindow('documentation')
                .then(function (finWin) {
                    return new Promise(function (resolve, reject) {
                        finWindow = finWin;
                        resolve(finWin);
                    });
                })
                .then(Helpers.getVisibility)
                .then(Helpers.shouldbeVisible)
                .then(function () {
                    return new Promise(function (resolve, reject) {
                        finWindow.close(true);
                        resolve();
                    });
                })
                .then(done.bind(this, null, null));
        });
    });
});


