var chai = require('chai');
var assert = chai.assert;
var should = chai.should;
var expect = chai.expect;


var LauncherClient = FSBL.Clients.LauncherClient;

function getComponentList(done) {
	FSBL.Clients.LauncherClient.getComponentList(function (err, response) {
		expect(err).to.be.null;
		expect(response).to.exist;
		ComponentList = response;
		done();
	});
}
var spawnedDescriptor,
	spawnedWindowIdentifier,
	spawnedWindowWrap,
	ComponentList;

describe('LauncherClient', function () {

	var methods = getAllMethods(LauncherClient);
	var baseMethods = getAllMethods(new FSBL.Clients.BaseClient());

	var testMethods = methods.filter(function (item) {
		return !baseMethods.includes(item);
	});

	var untestedMethods = {};
	for (var i = 0; i < testMethods.length; i++) {
		untestedMethods[testMethods[i]] = true;
	}

	function testThat(method, test, fn) {
		it(test, fn);
		if (untestedMethods[method]){ delete untestedMethods[method]; }
	}



	context('ComponentList', function () {
		before(function (done) {
			getComponentList(done);
		});

		testThat('getComponentList', 'should retrieve the componentList from the manifest.', function () {
			Object.keys(ComponentList).forEach((componentName, i) => {
				var component = ComponentList[componentName];
				expect(component).to.have.all.keys(['foreign', 'component','window']);
			});
		});


		testThat('getComponentDefaultConfig', 'should get the Toolbar defaults', function (done) {
			FSBL.Clients.LauncherClient.getComponentDefaultConfig('Toolbar', function (err, response) {
				expect(err).to.be.null;
				expect(response).to.deep.equal(ComponentList.Toolbar);
				done();
			});
		});

	});

	context('User Defined Components', function () {
		testThat('addUserDefinedComponent', 'should add a user defined component called "Launcher Test"', function (done) {
			FSBL.Clients.LauncherClient.addUserDefinedComponent({
				name: 'Launcher Test',
				url: 'google.com'
			}, function (err, response) {
				expect(err).to.be.null;
				expect(response).to.be.null;
				done();
			});
		});

		it('should update the ComponentList global', function (done) {
			getComponentList(done);
		});

		it('should see Launcher Test in the componentList', function () {
			expect(ComponentList).to.have.property('Launcher Test');
		});

		testThat('removeUserDefinedComponent', 'should remove the user defined component called "Launcher Test"', function (done) {
			FSBL.Clients.LauncherClient.removeUserDefinedComponent({
				name: 'Launcher Test',
				url: 'google.com'
			}, function (err, response) {
				expect(err).to.be.null;
				expect(response).to.be.null;
				done();
			});
		});

		it('should update the ComponentList global', function (done) {
			getComponentList(done);
		});

		it('should NOT see Launcher Test in the componentList', function () {
			expect(ComponentList).to.not.have.property('Launcher Test');
		});

	});

	context('Spawning Windows', function () {
		this.timeout(15000);
		testThat('spawn','should spawn an advanced chart', function (done) {
			FSBL.Clients.LauncherClient.spawn('Advanced Chart', {}, function (err, response) {
				expect(err).to.be.null;
				expect(response).to.exist;
				spawnedWindowIdentifier = response.windowIdentifier;
				spawnedWindowDescriptor = response.windowDescriptor;
				spawnedWindowWrap = response.finWindow;
				done();
			});
		});

		testThat('showWindow','should move a window', function (done) {
			FSBL.Clients.LauncherClient.showWindow(spawnedWindowIdentifier, {}, function(err, response) {
				expect(err).to.be.null;
				expect(response).to.exist;
				done();
			});
		});

		testThat('getMonitorInfo','has a unclaimedRect member', function (done) {
			var params={
				windowIdentifier: spawnedWindowIdentifier
			};
			FSBL.Clients.LauncherClient.getMonitorInfo(params, function(err, response) {
				expect(err).to.be.null;
				expect(response.unclaimedRect).to.exist;
				done();
			});
		});

		testThat('getMonitorInfoAll','returns an array', function (done) {
			FSBL.Clients.LauncherClient.getMonitorInfoAll(function(err, response) {
				expect(err).to.be.null;
				expect(response).to.be.a("array");
				done();
			});
		});

		testThat('getRawWindow', 'should return an object.', function (done) {
			setTimeout(function(){
				let w=FSBL.Clients.LauncherClient.getRawWindow(spawnedWindowIdentifier);
				expect(w).to.be.a("object");
				done();
			},0);
		});

		testThat('getActiveDescriptors', 'should return a list.', function (done) {
			FSBL.Clients.LauncherClient.getActiveDescriptors(function (err, response) {
				expect(response).to.not.be.empty;
				done();
			});
		});

		it('should close the advanced window.', function (done) {
			fin.desktop.Window.wrap(spawnedWindowIdentifier.uuid, spawnedWindowIdentifier.windowName).close(true, function(){
				done();
			});
		});
	});

	context.skip('Completion', function () {
		//This needs to be considered in the context of an overarching code-coverage tool. This causes failures that aren't _really_ failures. We need to look at this eventually, but this isn't the solution. --Brad
		it('should check all functions', function (done) {
			expect(untestedMethods).to.be.empty;
			done();
			console.log("UntestedMethods",untestedMethods);
		});
	});

	after(function (done) {
		done();
	});

});




