var counter = 0;
DockingMath.addWindow(stationaryWindow).then(function (dockableMock) {
	stationaryWindow = DockingMath.getWindow(stationaryWindow.name);
	counter++;
	check();
});

DockingMath.addWindow(movingWindow).then(function (dockableMock) {
	console.log('aaaay')
	movingWindow = DockingMath.getWindow(movingWindow.name);
	counter++;
	check();
});
DockingMath.addWindow(toolbar).then(function (dockableMock) {
	toolbar = DockingMath.getWindow(toolbar.name);
	counter++;
	check();
});

DockingMath.addWindow(sw2).then(function (dockableMock) {
	sw2 = DockingMath.getWindow(sw2.name);;
	counter++;
	check();
});
DockingMath.setAllowGroupsToSnap(true);
function check() {
	if (counter === 4) {
		finishDockingSetup();
	}
}





