var Helpers = require('./helpers');
const BASE_DELAY = 2000;

describe('LinkerUI', function () {
	let chart1,
		chart2,
		chart3,
		windows = [];
	it('should spawn the charts we need to test the linker', function (done) {
		this.timeout(20000);
		function spawnChart() {
			return new Promise(function (resolve, reject) {
				FSBL.Clients.LauncherClient.spawn('Advanced Chart', {}, function (err, response) {
					let finWin = response.finWindow;
					windows.push(finWin);
					checkCompletion();
				});
				resolve();
			});
		}
		//Using API to speed up this test; we also test the menu elewhere.
		spawnChart()
			.then(spawnChart)
			.then(spawnChart);
		function checkCompletion() {
			if (windows.length === 3) {
				[chart1, chart2, chart3] = windows;
				done();
			}
		}

	});

	it('should open the linker on Chart 1', function (done) {

		FSBL.Clients.RouterClient.query(`TestRunner.${chart1.name}.windowTitleBar`, {
			test: 'clickLinker',
		}, function (err, response) {
			done();
		});
	});
	it('should choose yellow for chart 1', function (done) {
		this.timeout(5000);
		FSBL.Clients.RouterClient.query(`TestRunner.${chart1.name}.Linker`, {
			test: 'chooseColor',
			color: 'yellow'
		}, function (err, response) {
			setTimeout(done, BASE_DELAY);
		});
	});
	it('should verify that yellow is chosen in the DOM of the window', function (done) {
		this.timeout(5000);
		FSBL.Clients.RouterClient.query(`TestRunner.${chart1.name}.windowTitleBar`, {
			test: 'getLinkerGroupClasses'
		}, function (err, response) {
			console.log('COLORS', response.data);
			expect(response.data.join('').includes('yellow')).to.be.true;
			done();
		});
	});
	it('should verify that yellow is chosen in the DOM of the linker', function (done) {
		FSBL.Clients.RouterClient.query(`TestRunner.${chart1.name}.Linker`, {
			test: 'getLinkerGroupClasses'
		}, function (err, response) {
			console.log('COLORS-L', response.data);

			let colors = response.data;
			for (var i = 0; i < colors.length; i++) {
				var color = colors[i];
				if (color.includes('yellow')) {
					expect(color.includes('active')).to.be.true;
					break;
				}
			}
			done();
		});
	});
	it('should open the linker on Chart 2', function (done) {
		this.timeout(5000);
		FSBL.Clients.RouterClient.query(`TestRunner.${chart2.name}.windowTitleBar`, {
			test: 'clickLinker',
		}, function (err, response) {
			setTimeout(done, BASE_DELAY);
		});
	});
	it('should verify that there are no chosen colors for chart 2', function (done) {
		FSBL.Clients.RouterClient.query(`TestRunner.${chart2.name}.windowTitleBar`, {
			test: 'getLinkerGroupClasses'
		}, function (err, response) {
			expect(response.data.length).to.equal(0);
			done();
		});
	});
	it('should choose yellow for chart 2', function (done) {
		this.timeout(5000);
		FSBL.Clients.RouterClient.query(`TestRunner.${chart2.name}.Linker`, {
			test: 'chooseColor',
			color: 'yellow'
		}, function (err, response) {
			setTimeout(done, BASE_DELAY);
		});
	});
	it('should change symbol on chart 1', function (done) {
		this.timeout(5000);

		FSBL.Clients.RouterClient.query(`TestRunner.${chart1.name}.AdvancedChart`, {
			test: 'changeSymbol',
			symbol: 'NFLX'
		}, function (err, response) {
			setTimeout(done, BASE_DELAY);
		});
	});
	it('should verify that chart 1 and 2 share the same symbol', function (done) {
		this.timeout(10000);
		let chart1Symbol, chart2Symbol;
		FSBL.Clients.RouterClient.query(`TestRunner.${chart1.name}.AdvancedChart`, {
			test: 'getSymbol'
		}, function (err, response) {
			chart1Symbol = response.data;
			checkComparison();
		});
		FSBL.Clients.RouterClient.query(`TestRunner.${chart2.name}.AdvancedChart`, {
			test: 'getSymbol'
		}, function (err, response) {
			chart2Symbol = response.data;
			checkComparison();
		});
		function checkComparison() {
			if (chart1Symbol && chart2Symbol) {
				expect(chart1Symbol).to.equal(chart2Symbol);
				done();
			}
		}
	});
	it('should choose blue on chart 1', function (done) {
		FSBL.Clients.RouterClient.query(`TestRunner.${chart1.name}.Linker`, {
			test: 'chooseColor',
			color: 'blue'
		}, function (err, response) {
			done();
		});
	});
	it('should open the linker on Chart 3', function (done) {
		this.timeout(5000);

		FSBL.Clients.RouterClient.query(`TestRunner.${chart3.name}.windowTitleBar`, {
			test: 'clickLinker',
		}, function (err, response) {
			setTimeout(done, BASE_DELAY);
		});
	});
	it('should choose blue on chart 3', function (done) {
		this.timeout(5000);

		FSBL.Clients.RouterClient.query(`TestRunner.${chart3.name}.Linker`, {
			test: 'chooseColor',
			color: 'blue'
		}, function (err, response) {
			setTimeout(done, BASE_DELAY);
		});
	});
	it('should change symbol on chart 3', function (done) {
		FSBL.Clients.RouterClient.query(`TestRunner.${chart3.name}.AdvancedChart`, {
			test: 'changeSymbol',
			symbol: 'GOOG'
		}, function (err, response) {
			done();
		});
	});
	it('should verify that chart 1 and 3 share the same symbol; chart 2 should not share the same symbol', function (done) {
		this.timeout(10000);

		let chart1Symbol, chart2Symbol, chart3Symbol;
		FSBL.Clients.RouterClient.query(`TestRunner.${chart1.name}.AdvancedChart`, {
			test: 'getSymbol'
		}, function (err, response) {
			console.log("RECEIVED SYMBOL", response);
			chart1Symbol = response.data;
			checkComparison();
		});

		FSBL.Clients.RouterClient.query(`TestRunner.${chart2.name}.AdvancedChart`, {
			test: 'getSymbol'
		}, function (err, response) {
			console.log("RECEIVED SYMBOL", response);

			chart2Symbol = response.data;
			checkComparison();
		});
		FSBL.Clients.RouterClient.query(`TestRunner.${chart3.name}.AdvancedChart`, {
			test: 'getSymbol'
		}, function (err, response) {
			console.log("RECEIVED SYMBOL", response);

			chart3Symbol = response.data;
			checkComparison();
		});
		function checkComparison() {
			if (chart1Symbol && chart2Symbol && chart3Symbol) {
				expect(chart1Symbol).to.equal(chart3Symbol);
				expect(chart1Symbol).to.not.equal(chart2Symbol);
				done();
			}
		}
	});
	it('should change the symbol on chart 2', function (done) {
		FSBL.Clients.RouterClient.query(`TestRunner.${chart2.name}.AdvancedChart`, {
			test: 'changeSymbol',
			symbol: 'AAPL'
		}, function (err, response) {
			done();
		});
	});
	it('should verify that chart 1 and 2 share the same symbol, chart 3 should not share the same symbol', function (done) {
		this.timeout(10000);

		let chart1Symbol, chart2Symbol, chart3Symbol;
		FSBL.Clients.RouterClient.query(`TestRunner.${chart1.name}.AdvancedChart`, {
			test: 'getSymbol'
		}, function (err, response) {
			chart1Symbol = response.data;
			checkComparison();
		});

		FSBL.Clients.RouterClient.query(`TestRunner.${chart2.name}.AdvancedChart`, {
			test: 'getSymbol'
		}, function (err, response) {
			chart2Symbol = response.data;
			checkComparison();
		});
		FSBL.Clients.RouterClient.query(`TestRunner.${chart3.name}.AdvancedChart`, {
			test: 'getSymbol'
		}, function (err, response) {
			chart3Symbol = response.data;
			checkComparison();
		});
		function checkComparison() {
			if (chart1Symbol && chart2Symbol && chart3Symbol) {
				expect(chart1Symbol).to.equal(chart2Symbol);
				expect(chart2Symbol).to.not.equal(chart3Symbol);
				done();
			}
		}
	});
	// after((done) => {
	// 	Helpers.closeAllWindows()
	// 		.then(done)
	// });
});


