it('should resize from bottomLeft', function () {
	resetGrid();
	var cachedBounds = getCachedBoundsForGrid();

	var moveRequest = createMoveRequest({
		newLeft: E.left - 20,
		newTop: E.top,
		newBottom: E.bottom - 20,
		newRight: E.right,
		changeType: 1,
		win: E
	});
	E.requestBoundsChange(moveRequest);

	expect(D.bottom).to.equal(E.bottom);
	[D.bottom, G.top, H.top].forEach((number) => {
		expect(number).to.equal(E.bottom);
	});
	[D.right, G.right, H.left].forEach((number, i) => {
		expect(number).to.equal(E.left);
	});

	moveRequest = createMoveRequest({
		newLeft: E.left + 20,
		newTop: E.top,
		newBottom: E.bottom - 20,
		newRight: E.right,
		changeType: 1,
		win: E
	});
	E.requestBoundsChange(moveRequest);

	expect(D.bottom).to.equal(E.bottom);
	[D.bottom, G.top, H.top].forEach((number) => {
		expect(number).to.equal(E.bottom);
	});
	[D.right, G.right, H.left].forEach((number, i) => {
		expect(number).to.equal(E.left);
	});
	var shouldBeTheSame = ['A', 'B', 'C', 'F', 'I'];
	cachedBounds.forEach((obj) => {
		if (shouldBeTheSame.includes(obj.name)) {
			ensureBoundsAreTheSame(DockingMath.getWindow(obj.name), obj.bounds, 'Resize E\'s bottom left corner');
		}
	});
});
