it('should resize from topRight', function () {
	resetGrid();
	var cachedBounds = getCachedBoundsForGrid();

	var moveRequest = createMoveRequest({
		newLeft: E.left,
		newTop: E.top - 20,
		newBottom: E.bottom,
		newRight: E.right + 20,
		changeType: 1,
		win: E
	});
	E.requestBoundsChange(moveRequest);

	[C.bottom, B.bottom, F.top].forEach((number, i) => {
		expect(number).to.equal(E.top);
	});

	//left edge shouldn't move
	[D.right, A.right, B.left].forEach((number, i) => {
		expect(number).to.equal(E.left);
	});

	[F.left, B.right, C.left].forEach((number, i) => {
		expect(number).to.equal(E.right);
	});

	moveRequest = createMoveRequest({
		newLeft: E.left,
		newTop: E.top + 60,
		newBottom: E.bottom,
		newRight: E.right - 50,
		changeType: 1,
		win: E
	});
	E.requestBoundsChange(moveRequest);

	[C.bottom, B.bottom, F.top].forEach((number, i) => {
		expect(number).to.equal(E.top);
	});

	//left edge shouldn't move
	[D.right, A.right, B.left].forEach((number, i) => {
		expect(number).to.equal(E.left);
	});


	[F.left, B.right, C.left].forEach((number, i) => {
		expect(number).to.equal(E.right);
	});
	var shouldBeTheSame = ['A', 'D', 'G', 'H', 'I'];
	cachedBounds.forEach((obj) => {
		if (shouldBeTheSame.includes(obj.name)) {
			ensureBoundsAreTheSame(DockingMath.getWindow(obj.name), obj.bounds, 'Resize E\'s top right corner');
		}
	});
});

