it('should resize along a path', function () {
	resetGrid();
	var cachedBounds = getCachedBoundsForGrid();

	var start = {
		x: E.left - 1,
		y: E.bottom - 1,
	};
	var end = {
		x: E.left - 150,
		y: E.bottom - 150
	};
	var cb = noop;
	for (var x = start.x, y = start.y; x > end.x; x-- , y--) {
		var moveRequest = createMoveRequest({
			newLeft: x,
			newTop: E.top,
			newBottom: y,
			newRight: E.right,
			changeType: 1,
			win: E
		});
		moveRequest.mousePosition = getMousePosFromResizeHandle(E, 'bottomLeft');
		if (x === end.x + 1) {
			cb = undefined;
		}
		E.requestBoundsChange(moveRequest, cb);

		expect(D.bottom).to.equal(E.bottom);
		[D.bottom, G.top, H.top].forEach((number) => {
			expect(number).to.equal(E.bottom);
		});
		[D.right, G.right, H.left].forEach((number, i) => {
			expect(number).to.equal(E.left);
		});

	}
	var shouldBeTheSame = ['A', 'B', 'C', 'F', 'I'];
	cachedBounds.forEach((obj) => {
		if (shouldBeTheSame.includes(obj.name)) {
			ensureBoundsAreTheSame(DockingMath.getWindow(obj.name), obj.bounds, 'Resize E\'s bottom left edge along a path');
		}
	});
});
it('should move a group and then try resizing', function () {
	resetGrid();
	createMovableGroupFromGrid();
	var moveRequest = createMoveRequest({
		newLeft: E.left + 40,
		newTop: E.top + 10,
		newBottom: E.bottom,
		newRight: E.right,
		changeType: 0,
		win: E
	});

	E.requestBoundsChange(moveRequest);
	var cachedBounds = getCachedBoundsForGrid();

	var start = {
		x: E.left - 1,
		y: E.bottom - 1,
	};
	var end = {
		x: E.left - 150,
		y: E.bottom - 150
	};
	var cb = noop;

	for (var x = start.x, y = start.y; x > end.x; x-- , y--) {
		moveRequest = createMoveRequest({
			newLeft: x,
			newTop: E.top,
			newBottom: y,
			newRight: E.right,
			changeType: 1,
			win: E
		});
		if (x === end.x + 1) {
			cb = undefined;
		}
		E.requestBoundsChange(moveRequest, cb);
	}
	expect(D.bottom).to.equal(E.bottom);
	[D.bottom, G.top, H.top].forEach((number) => {
		expect(number).to.equal(E.bottom);
	});
	[D.right, G.right, H.left].forEach((number, i) => {
		expect(number).to.equal(E.left);
	});
	var shouldBeTheSame = ['A', 'B', 'C', 'F', 'I'];
	cachedBounds.forEach((obj) => {
		if (shouldBeTheSame.includes(obj.name)) {
			ensureBoundsAreTheSame(DockingMath.getWindow(obj.name), obj.bounds, 'Resize E\'s bottom left edge along a path');
		}

	});
});

it('should resize a shared corner creating a straddle. Then move another corner, pulling all the appropriate edges with it.', function () {
	resetGrid();
	var cachedBounds = getCachedBoundsForGrid();

	var moveRequest = createMoveRequest({
		newLeft: B.left,
		newTop: B.top,
		newBottom: B.bottom,
		newRight: B.right - 40,
		changeType: 1,
		win: B
	});
	B.requestBoundsChange(moveRequest);

	moveRequest = createMoveRequest({
		newLeft: E.left,
		newTop: E.top + 40,
		newBottom: E.bottom,
		newRight: E.right + 60,
		changeType: 1,
		win: E
	});
	//general algo would assume just the bottom edge is being moved. overwrite with this function.
	moveRequest.mousePosition = getMousePosFromResizeHandle(E, 'topRight');
	E.requestBoundsChange(moveRequest);


	[B.bottom, F.top, C.bottom].forEach((number, i) => {
		expect(number).to.equal(E.top);
	});

	[F.left].forEach((number, i) => {
		expect(number).to.equal(E.right);
	});

	var shouldBeTheSame = ['A', 'D', 'G', 'H', 'I'];
	cachedBounds.forEach((obj) => {
		if (shouldBeTheSame.includes(obj.name)) {
			ensureBoundsAreTheSame(DockingMath.getWindow(obj.name), obj.bounds, 'Resize E\'s bottom right corner along a path');
		}

	});
});

it('should test cascading resizing.', function () {
	resetGrid();
	var cachedBounds = getCachedBoundsForGrid();

	var moveRequest = createMoveRequest({
		newLeft: B.left,
		newTop: B.top,
		newBottom: B.bottom,
		newRight: B.right - 40,
		changeType: 1,
		win: B
	});
	B.requestBoundsChange(moveRequest);

	moveRequest = createMoveRequest({
		newLeft: D.left,
		newTop: D.top + 40,
		newBottom: D.bottom,
		newRight: D.right - 40,
		changeType: 1,
		win: D
	});

	moveRequest.mousePosition = getMousePosFromResizeHandle(D, 'topRight');
	D.requestBoundsChange(moveRequest);


	[A.bottom, B.bottom, C.bottom].forEach((number, i) => {
		expect(number).to.equal(D.top);
	});


	[A.right, B.left].forEach((number, i) => {
		expect(number).to.equal(D.right);
	});

	var shouldBeTheSame = ['G', 'H', 'I'];
	cachedBounds.forEach((obj) => {
		if (shouldBeTheSame.includes(obj.name)) {
			ensureBoundsAreTheSame(DockingMath.getWindow(obj.name), obj.bounds, 'Resize E\'s bottom right corner along a path');
		}
	});

	resetGrid();
	var cachedFBounds = F.getBounds();
	var cachedEBounds = E.getBounds();
	moveRequest = createMoveRequest({
		newLeft: F.left,
		newTop: F.top,
		newBottom: F.bottom - 70,
		newRight: F.right,
		changeType: 1,
		win: F
	});
	F.requestBoundsChange(moveRequest);

	moveRequest = createMoveRequest({
		newLeft: F.left + 20,
		newTop: F.top,
		newBottom: F.bottom - 20,
		newRight: F.right,
		changeType: 1,
		win: F
	});

	moveRequest.mousePosition = getMousePosFromResizeHandle(F, 'bottomLeft');
	F.requestBoundsChange(moveRequest);

	[F.left, E.right, I.left, H.right].forEach((number, i) => {
		expect(number).to.equal(moveRequest.left);
	});

});

it('should reset grid and consoletitute groups', function () {
	groupMode.enabled = true;
	DockingMath.setGroupMode(groupMode);

	resetGrid();
	DockingMath.getGroupNames().forEach((groupName) => {
		DockingMath.removeGroup(groupName);
	});
	expect(Object.keys(DockingMath.getGroups())).to.have.lengthOf(0);
	DockingMath.constituteGroups();
});


