
context('Stationary Right Edge', function () {
	it('Should snap to the right edge of the stationary window', function () {
		var newLeft = stationaryWindow.right + 40;
		var newTop = stationaryWindow.top + 40;
		var moveRequest = createMoveRequest({
			newLeft: newLeft,
			newTop: newTop,
			newBottom: newTop + movingWindow.height,
			newRight: newLeft + movingWindow.width,
			changeType: 0,
			win: movingWindow
		});
		//Setup. Next move will resize.
		movingWindow.requestBoundsChange(moveRequest);

		var oldRight = movingWindow.right;
		var oldLeft = movingWindow.left;
		moveRequest = createMoveRequest({
			newLeft: stationaryWindow.right,
			newRight: movingWindow.right,
			newTop: movingWindow.top,
			newBottom: movingWindow.bottom,
			win: movingWindow,
			changeType: 1
		});
		movingWindow.requestBoundsChange(moveRequest);

		expect(oldRight).to.equal(movingWindow.right);
		expect(movingWindow.left).to.equal(stationaryWindow.right);
		expect(movingWindow.width).to.equal(oldRight - oldLeft + Number(oldLeft) - movingWindow.left);
	});
	it('Should wiggle around inside of the right snapping region of the stationary window while maintaining its width', function () {
		//Setup. Next move will resize.
		var newLeft = stationaryWindow.right - 1;
		var newTop = stationaryWindow.top + 40;
		var moveRequest = createMoveRequest({
			newLeft: newLeft,
			newTop: newTop,
			newBottom: newTop + movingWindow.height,
			newRight: newLeft + movingWindow.width,
			changeType: 0,
			win: movingWindow
		});
		movingWindow.requestBoundsChange(moveRequest);

		var oldRight = movingWindow.right;
		var oldLeft = movingWindow.left;
		var oldWidth = movingWindow.width;
		moveRequest = createMoveRequest({
			newLeft: stationaryWindow.right + 5,
			newRight: movingWindow.right,
			newTop: movingWindow.top,
			newBottom: movingWindow.bottom,
			win: movingWindow,
			changeType: 1
		});
		movingWindow.requestBoundsChange(moveRequest);


		expect(oldRight).to.equal(movingWindow.right);
		expect(movingWindow.left).to.equal(stationaryWindow.right);
		expect(movingWindow.width).to.equal(oldWidth);
	});

	it('While snapped to the right edge of the window, it should snap to the top on resize', function () {
		var newLeft = stationaryWindow.right - 12;
		var newTop = stationaryWindow.top + 40;
		var moveRequest = createMoveRequest({
			newLeft: newLeft,
			newTop: newTop,
			newBottom: newTop + movingWindow.height,
			newRight: newLeft + movingWindow.width,
			changeType: 0,
			win: movingWindow
		});
		movingWindow.requestBoundsChange(moveRequest);

		var oldBottom = movingWindow.bottom;
		moveRequest = createMoveRequest({
			newLeft: movingWindow.left,
			newRight: movingWindow.right,
			newTop: stationaryWindow.top + 14,
			newBottom: movingWindow.bottom,
			win: movingWindow,
			changeType: 1
		});
		movingWindow.requestBoundsChange(moveRequest);
		//DockingMath.onWindowMove(moveRequest);

		expect(oldBottom).to.equal(movingWindow.bottom);
		expect(movingWindow.left).to.equal(stationaryWindow.right);
		expect(movingWindow.top).to.equal(stationaryWindow.top);
	});
	it('should resize past the top edge of the stationary window', function () {
		var newLeft = stationaryWindow.right - 10;
		var newTop = stationaryWindow.top;
		var moveRequest = createMoveRequest({
			newLeft: newLeft,
			newTop: newTop,
			newBottom: newTop + movingWindow.height,
			newRight: newLeft + movingWindow.width,
			changeType: 0,
			win: movingWindow
		});
		movingWindow.requestBoundsChange(moveRequest);

		var oldTop = movingWindow.top;
		var oldRight = movingWindow.right;
		var oldLeft = movingWindow.left;

		moveRequest = createMoveRequest({
			newLeft: movingWindow.left,
			//14 px left of the stationary window
			newRight: movingWindow.right,
			newTop: stationaryWindow.top - 16,
			//3 px below the top edge of the window.
			newBottom: stationaryWindow.bottom,
			win: movingWindow,
			changeType: 1
		});
		movingWindow.requestBoundsChange(moveRequest);

		expect(oldLeft).to.equal(movingWindow.left);
		expect(movingWindow.left).to.equal(stationaryWindow.right);
		expect(movingWindow.top).to.equal(stationaryWindow.top - 16);
	});

	it('While snapped to the right edge of the window, it should snap to the bottom on resize', function () {
		var newLeft = stationaryWindow.right + 10;
		var newTop = stationaryWindow.top + 40;
		var moveRequest = createMoveRequest({
			newLeft: newLeft,
			newTop: newTop,
			newBottom: newTop + 60,
			newRight: newLeft + movingWindow.width,
			changeType: 0,
			win: movingWindow
		});
		movingWindow.requestBoundsChange(moveRequest);

		var oldTop = movingWindow.top;
		var oldRight = movingWindow.right;
		var oldLeft = movingWindow.left;

		moveRequest = createMoveRequest({
			newLeft: movingWindow.left,
			newRight: movingWindow.right,
			newTop: movingWindow.top,
			newBottom: stationaryWindow.bottom - 13,
			win: movingWindow,
			changeType: 1
		});
		movingWindow.requestBoundsChange(moveRequest);

		expect(movingWindow.left).to.equal(stationaryWindow.right);
		expect(movingWindow.bottom).to.equal(stationaryWindow.bottom);
	});

	it('should resize past the bottom edge of the stationary window', function () {
		var newLeft = stationaryWindow.right;
		var newTop = stationaryWindow.top - movingWindow.height - 40;
		var moveRequest = createMoveRequest({
			newLeft: newLeft,
			newTop: newTop,
			newBottom: newTop + movingWindow.height,
			newRight: newLeft + movingWindow.width,
			changeType: 0,
			win: movingWindow
		});
		movingWindow.requestBoundsChange(moveRequest);

		var oldTop = movingWindow.top;
		var oldRight = movingWindow.right;
		var oldLeft = movingWindow.left;

		moveRequest = createMoveRequest({
			newLeft: movingWindow.left,
			//14 px left of the stationary window
			newRight: movingWindow.right,
			newTop: movingWindow.top,
			//3 px below the top edge of the window.
			newBottom: stationaryWindow.bottom + 40,
			win: movingWindow,
			changeType: 1
		});
		movingWindow.requestBoundsChange(moveRequest);

		expect(oldTop).to.equal(movingWindow.top);
		expect(movingWindow.bottom).to.equal(stationaryWindow.bottom + 40);
		expect(movingWindow.left).to.equal(stationaryWindow.right);
	});

	it('While snapped to a side, it should snap to the bottom while remaining snapped to the top.', function () {
		var newLeft = stationaryWindow.right;
		var newTop = stationaryWindow.top;
		var moveRequest = createMoveRequest({
			newLeft: newLeft,
			newTop: newTop,
			//CHANGE NOTE: This wasn't breaking before because of a fix i'd put in for openfin that forced moves to always pick up on the window's width/height at move.
			newBottom: newTop + movingWindow.height,
			newRight: newLeft + movingWindow.width,
			changeType: 0,
			win: movingWindow
		});
		movingWindow.requestBoundsChange(moveRequest);
		var oldWidth = movingWindow.width;
		moveRequest = createMoveRequest({
			newLeft: movingWindow.left,
			//14 px left of the stationary window
			newRight: movingWindow.right,
			newTop: movingWindow.top,
			//3 px below the top edge of the window.
			newBottom: stationaryWindow.bottom - 12,
			win: movingWindow,
			changeType: 1
		});
		movingWindow.requestBoundsChange(moveRequest);
		expect(movingWindow.top).to.equal(stationaryWindow.top);
		expect(movingWindow.bottom).to.equal(stationaryWindow.bottom);
		expect(oldWidth).to.equal(movingWindow.width);
	});
});


