var chai = require('chai');
var assert = chai.assert;
var should = chai.should;
var expect = chai.expect;

var StoreClient = FSBL.Clients.DataStoreClient;
var testData = {
	field: "testField", value: "value1"
};

var testData2 = {
	store: "testStore3", field: "testField2", value: "value2"
};
var storeListener = null;
var fieldListener = null;

describe('StoreClient', function () {
	var methods = getAllMethods(StoreClient);
	var baseMethods = getAllMethods(new FSBL.Clients.BaseClient());
	var mainLocalStore;
	before(function (done) {
		done();
	});

	describe('Local Store Client', function () {
		var methods = getAllMethods(StoreClient);
		var localListener;
		var localFieldListener;
		var baseMethods = getAllMethods(new FSBL.Clients.BaseClient());
		var localTestData = {
			store: "localTestStore", field: "localTestField", value: "localTestvalue"
		};
		var localTestData2 = {
			store: "localTestStore2", field: "localTestField2", value: "localTestvalue2"
		};
		before(function (done) {//make sure we have data before we test....For async
			StoreClient.createStore({
				store: "mainLocalStore", values: {
					localTestField: localTestData.value,
					field2: { nestedField: "nestedFieldValue" }
				}
			}, function (err, store) {
				mainLocalStore = store;
				done();
			});
		});
		it('should set a value in the local store and return it in a listener', function (done) {
			localFieldListener = function (err, data) {
				expect(data.value).to.equal(localTestData2.value);
				mainLocalStore.removeListener(localTestData2, localFieldListener);
				done();
			};
			mainLocalStore.addListener(localTestData2, localFieldListener);
			mainLocalStore.setValue(localTestData2);

		});

		it('should set a nested value in the local store', function (done) {
			var nestData = { field: "test.abc", value: "testnested" };
			localFieldListener = function (err, data) {
				expect(data.value).to.equal(nestData.value);
				mainLocalStore.removeListener(nestData, localFieldListener);
				done();
			};
			mainLocalStore.addListener(nestData, localFieldListener);
			mainLocalStore.setValue(nestData);

		});

		it('should get a value in the local store', function (done) {
			mainLocalStore.getValue(localTestData, function (err, data) {
				expect(!err);

				expect(data).to.equal(localTestData.value);
				done();
			});

		});
		it('should get all values in the local store', function (done) {
			mainLocalStore.getValues(function (err, data) {
				expect(!err);
				expect(data.localTestField).to.equal(localTestData.value);
				done();
			});

		});
		it('should get multiple values in the local store', function (done) {
			var f1 = { field: "field1", value: "value1" };
			var f2 = { field: "field42", value: "value2" };

			mainLocalStore.setValues([f1, f2]);
			mainLocalStore.getValues([f1, f2], function (err, data) {
				expect(!err);
				expect(data[f1.field]).to.exist;
				expect(data[f2.field]).to.equal(f2.value);
				done();
			});

		});
		it('should get multiple values from string in the local store', function (done) {
			var f1 = { field: "field1", value: "value1" };
			var f2 = { field: "field42", value: "value2" };

			mainLocalStore.setValues([f1, f2]);

			mainLocalStore.getValues(["field1", "field42"], function (err, data) {
				expect(!err);
				expect(data[f1.field]).to.exist;
				expect(data[f2.field]).to.exist;
				expect(data[f2.field]).to.equal(f2.value);
				done();
			});

		});
		it('should get a nested value in the store', function (done) {
			mainLocalStore.getValue({
				field: "field2.nestedField"
			}, function (err, data) {
				expect(data).to.equal("nestedFieldValue");
				done();
			});

		});
		it('should trigger listener when store changes', function (done) {
			storeListener = function (err, data) {
				expect(data.value.name).to.equal(mainLocalStore.name);
				mainLocalStore.removeListener(storeListener);
				done();

			};
			mainLocalStore.addListener(storeListener);

			mainLocalStore.setValue(localTestData);

		});
		it('should create a new store/get store local', function (done) {
			StoreClient.createStore({ store: "store2" }, function (err, data) {
				StoreClient.getStore({ store: "store2" }, function (err, store) {
					expect(store.values).to.exist;
					done();
				});

			});
		});
		it('should create a new store and then remove it', function (done) {
			StoreClient.createStore({ store: "storeToRemove" }, function (err, store) {
				store.destroy(function (err, response) {
					expect(!err);
					expect(response).to.be.true;
					done();
				});

			});
		});
		it('should fail to get a store that doesnt exist local', function (done) {
			StoreClient.getStore({ store: "storeToRemove1234" }, function (err, response) {
				expect(err).to.not.be.null;
				done();
			});
		});
		it('should set a value and then remove it - local', function (done) {
			var tdata = { field: "tempField", value: "tempvalue" };
			var tempList = function (err, data) {
				mainLocalStore.removeListener(tdata, tempList);
				mainLocalStore.removeValue(tdata, function (err, response) {
					expect(!err);
					expect(!response);
					done();
				});


			};
			mainLocalStore.addListener(tdata, tempList);
			mainLocalStore.setValue(tdata);

		});
		it('should set a nested value and then remove it - local', function (done) {

			var tdata = { field: "tempField2", value: { abc: 123 } };
			var tempList = function (err, data) {
				mainLocalStore.removeListener(tdata, tempList);
				mainLocalStore.removeValue("tempField2.abc", function (err, response) {
					expect(mainLocalStore.getValue("tempField2.abc")).to.be.null;
					expect(!err);
					expect(!response);
					done();
				});


			};
			mainLocalStore.addListener(tdata, tempList);
			mainLocalStore.setValue(tdata);

		});
		it('should listen to nested value update', function (done) {
			var tdata = { field: "newNested", value: { child: "child value1" } };
			var listen = { field: "newNested.child" };
			var tempList = function (err, data) {
				mainLocalStore.removeListener(tdata, tempList);
				expect(!err);

				done();
			};

			mainLocalStore.setValue(tdata, function () {
				mainLocalStore.addListener(listen, tempList);
				tdata = { field: "newNested", value: { child: "child value2" } };
				mainLocalStore.setValue(tdata);
			});

		});
		it('should get parent object update of child', function (done) {
			var newObjectNested = {
				field: "parentObject.childObj", value: { childObj: "123" }
			};
			var list = { field: "parentObject" };
			var tempList = function (err, data) {
				mainLocalStore.removeListener(list, tempList);
				expect(!err);
				done();
			};

			mainLocalStore.setValue(newObjectNested, function () {
				newObjectNested = {
					field: "parentObject.childObj", value: { childObj: "2222" }
				};
				mainLocalStore.addListener(list, tempList);
				mainLocalStore.setValue(newObjectNested);
			});
		});
		it('should remove store listener', function (done) {
			var sl = function (err, data) {
			};
			mainLocalStore.addListener(sl);
			mainLocalStore.removeListener(sl, function (err, data) {
				expect(data).to.be.true;
				done();
			});
		});
		it('should add/remove listeners with the listeners call store level', function (done) {
			var sl = function (err, data) {
			};
			mainLocalStore.addListeners(sl);
			mainLocalStore.removeListener(sl, function (err, data) {
				expect(data).to.be.true;
				done();
			});
		});
		it('should add/remove listeners single to single listener  with the listeners call field level', function (done) {
			var sl = function (err, data) {
			};
			mainLocalStore.addListeners(["abc"], sl, function () {
				mainLocalStore.removeListeners(["abc"], sl, function (err, data) {
					expect(data).to.be.true;
					done();
				});
			});

		});
		it('should add/remove multiple fields to single listener with the listeners call field level', function (done) {
			var sl = function (err, data) {
			};
			mainLocalStore.addListeners(["abc", "123"], sl, function () {
				mainLocalStore.removeListeners(["abc", "123"], sl, function (err, data) {
					expect(data).to.be.true;
					done();
				});
			});

		});
		it('should add/remove  listeners as an object', function (done) {
			var sl = function (err, data) {
			};
			mainLocalStore.addListeners([{ field: "abc", listener: sl }], null, function () {
				mainLocalStore.removeListeners([{ field: "abc", listener: sl }], null, function (err, data) {
					expect(data).to.be.true;
					done();
				});
			});

		});

	});

	describe("Global Store", function () {
		var mainGlobalStore;
		var testDataGlobal = {
			field: "testFieldg", value: "value1g"
		};
		before(function (done) {//make sure we have data before we test....For async
			var params = {
				store: "mainGlobalStore",
				global: true,
				values: {
					"testFieldg": testDataGlobal.value
				}
			};
			StoreClient.createStore(params, function (err, store) {
				mainGlobalStore = store;
				done();
			});
		});
		it('should create a new store/get store global', function (done) {
			StoreClient.createStore({ store: "newglobalStore", global: true }, function (err, store) {
				StoreClient.getStore({ store: "newglobalStore" }, function (err, responseStore) {
					expect(responseStore.values).to.exist;
					expect(responseStore.name).to.equal(store.name);
					done();
				});

			});
		});

		it('set/get global data', function (done) {
			fieldListenerGlobal = function (err, data) {
				expect(data.value).to.equal(testDataGlobal.value);
				mainGlobalStore.removeListener(testDataGlobal, fieldListenerGlobal);
				done();
			};
			mainGlobalStore.addListener(testDataGlobal, fieldListenerGlobal);
			mainGlobalStore.setValue(testDataGlobal);
		});
		it('should get all values in the global store', function (done) {
			mainGlobalStore.getValues(function (err, data) {
				expect(!err);
				expect(data.testFieldg).to.equal(testDataGlobal.value);
				done();
			});

		});
		it('should get multiple values  in the global store', function (done) {
			var f1 = { field: "field1", value: "value1" };
			var f2 = { field: "field42", value: "value2" };
			mainGlobalStore.setValues([f1, f2], function () {
				mainGlobalStore.getValues([f1, f2], function (err, data) {
					expect(!err);
					expect(data[f1.field]).to.exist;
					done();
				});
			});



		});
		it('should get multiple values from string in the global store', function (done) {
			var f1 = { field: "field123", value: "value1" };
			var f2 = { field: "field42", value: "value2" };

			mainGlobalStore.setValues([f1, f2]);


			mainGlobalStore.getValues(["field123", "field42"], function (err, data) {
				expect(!err);
				expect(data[f1.field]).to.exist;
				done();
			});

		});
		it('should set a value and then remove it - global', function (done) {
			var tdata = { field: "tempField123", value: "tempvalue", global: true };
			var tempList = function (err, data) {
				mainGlobalStore.removeListener(tdata, tempList);
				mainGlobalStore.removeValue(tdata, function (err, response) {
					expect(!err);
					expect(!response);
					done();
				});
			};
			mainGlobalStore.addListener(tdata, tempList);
			mainGlobalStore.setValue(tdata);

		});
		it('should set a value a nested value', function (done) {
			var tdata = { field: "newNested", value: { abc: "123" }, global: true };
			var tempList = function (err, data) {
				mainGlobalStore.removeListener(tdata, tempList);
				expect(!err);
				done();
			};
			mainGlobalStore.addListener(tdata, tempList);
			mainGlobalStore.setValue(tdata);

		});
		it('should listen to nested value update', function (done) {
			var tdata = { field: "newNested.abc3", value: "123#1", global: true };
			var tempList = function (err, data) {
				mainGlobalStore.removeListener(tdata, tempList);
				expect(!err);
				done();
			};
			mainGlobalStore.addListener(tdata, tempList);
			mainGlobalStore.setValue(tdata);

		});
		it('should get parent object update of child', function (done) {
			var newObjectNested = {
				field: "parentObject.childObj", value: { childObj: "123" }
			};
			var list = { field: "parentObject" };
			var tempList = function (err, data) {
				mainGlobalStore.removeListener(list, tempList);
				expect(!err);
				done();
			};

			mainGlobalStore.setValue(newObjectNested, function () {
				newObjectNested = {
					field: "parentObject.childObj", value: { childObj: "2222" }
				};
				mainGlobalStore.addListener(list, tempList);
				mainGlobalStore.setValue(newObjectNested);
			});
		});
		it('Should transmit field removal', function (done) {
			var newObjectNested = {
				field: "objforsubremoval", value: { childObj: { child2: "child2value" } }
			};
			var list = { field: "objforsubremoval.childObj.child2" };
			var tempList = function (err, data) {
				mainGlobalStore.removeListener(list, tempList);
				expect(!err);
				done();
			};

			mainGlobalStore.setValue(newObjectNested, function () {
				newObjectNested = {
					field: "objforsubremoval.childObj", value: { child2: "removals" }
				};
				mainGlobalStore.addListener(list, tempList);
				mainGlobalStore.setValue(newObjectNested);
			});
		});
		it('should create a new store and then remove it', function (done) {
			StoreClient.createStore({ store: "storeToRemoveGlobal", global: true }, function (err, store) {
				store.destroy(function (err, response) {
					expect(response).to.be.true;
					done();
				});
			});
		});

		it('should fail to get a store that doesnt exist', function (done) {
			StoreClient.getStore({ store: "storeToRemove1234", global: true }, function (err, response) {
				expect(err).to.not.be.null;
				done();
			});
		});

	});


});





